import {
    Component,
    OnInit,
    Input,
    ViewChild,
    Output,
    EventEmitter,
    OnChanges,
    SimpleChanges
} from "@angular/core";
import {
    FormControl,
    Validators,
    FormGroup,
    FormBuilder,
    ValidationErrors,
    AbstractControl
} from "@angular/forms";
import { ImageLibComponent } from "../image-lib/image-lib.component";
import { ModalComponent } from "../modal/modal.component";
import { SitesService, Site } from "../../../api";
import { ErrorsService } from "../../../global/services/errors.service";
import { StoreService } from "../../services/store.service";
import { PermissionService } from "../../services/permission.service";

@Component({
    selector: "app-form-edit-site",
    templateUrl: "./form-edit-site.component.html",
    styleUrls: ["./form-edit-site.component.scss"]
})
export class FormEditSiteComponent implements OnInit, OnChanges {
    @ViewChild("imgLib")
    imgLib: ImageLibComponent;
    @ViewChild("modalMedia")
    modalMedia: ModalComponent;
    @Input() site: Site;
    @Input() updateStore = false;
    @Input() onlyFavicon = false;
    @Output() onSave: EventEmitter<Site> = new EventEmitter();

    form: FormGroup;
    mediaLibFor;
    confMediaLib = {
        tabs: {
            freeImg: { active: false },
            biblio: { active: true },
            internet: { active: true },
            color: { active: false }
        }
    };
    wantAddLogo = false;
    pendingInfosSite = false;

    constructor(
        private fb: FormBuilder,
        private siteApi: SitesService,
        private errorsService: ErrorsService,
        public storeService: StoreService,
        public permService: PermissionService
    ) {}

    ngOnInit() {}

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.site) {
            this.initForm();
        }
    }

    initForm() {
        const { fb, site } = this;

        let validatorOptSiteName = [Validators.maxLength(100)];
        let validatorOptContactEmail = [Validators.maxLength(255)];

        // // Si on montre tout le formulaire, on ajoute ducoup les champs required
        if (!this.onlyFavicon) {
            validatorOptSiteName = [
                ...validatorOptSiteName,
                Validators.required
            ];
            validatorOptContactEmail = [
                ...validatorOptContactEmail,
                Validators.email,
                Validators.required
            ];
        }

        const ctrlCfg = {
            site_name: new FormControl(site.site_name, validatorOptSiteName),
            contact_email: new FormControl(
                site.contact_email,
                validatorOptContactEmail
            ),
            logo: new FormControl(site.logo, [Validators.maxLength(2500)]),
            favicon: new FormControl(site.favicon, [Validators.maxLength(255)]),
            google_site_verification: new FormControl(
                site.google_site_verification,
                [Validators.maxLength(100)]
            ),
            google_analytics: new FormControl(site.google_analytics, [
                Validators.maxLength(50)
            ])
        };

        this.form = fb.group(ctrlCfg);
    }

    onShown(imgLib) {
        if (imgLib) {
            imgLib.reset();
        }
    }

    closeModalMedia($event) {
        const img = $event.img;
        let key = this.mediaLibFor;
        let value = "";
        if (this.mediaLibFor === "logo") {
            value = img.srcs
                ? JSON.stringify(img.srcs)
                : JSON.stringify({ default: img.src });
        } else if (this.mediaLibFor === "favicon") {
            value = img.srcs ? img.srcs["160"] : img.src;
        }

        this.form.controls[key].setValue(value);
        this.modalMedia.close();
    }

    openMediaLib(mediaLibFor: string) {
        if (
            mediaLibFor === "favicon" &&
            this.site.plan.name === this.permService.PLAN.FREE
        ) {
            this.permService.openModalPlan(this.permService.LIMIT.SET_FAVICON);
            return;
        }
        this.mediaLibFor = mediaLibFor;
        this.wantAddLogo = true;
        this.storeService.curSite = this.site;
        this.modalMedia.open();
    }

    saveSite() {
        if (this.form.valid) {
            const site = this.form.value;
            const id = String(this.site.id);
            this.pendingInfosSite = true;
            // const site = ;
            this.siteApi.apiSitesSlugPut(site, id).subscribe(
                site => {
                    this.site = site;

                    this.pendingInfosSite = false;
                    this.onSave.emit(site);
                    if (this.updateStore && this.storeService.curSite) {
                        this.storeService.curSite = site;
                    }
                },
                err => {
                    this.errorsService.handleErrorHttp(err);
                }
            );
        } else {
            this.markAllAsTouched(this.form);
            this.getFormValidationErrors();
        }
    }

    markAllAsTouched(formGroup: FormGroup) {
        Object.keys(formGroup.controls).forEach(field => {
            const control = formGroup.get(field);
            if (control instanceof FormControl) {
                control.markAsTouched({ onlySelf: true });
            } else if (control instanceof FormGroup) {
                this.markAllAsTouched(control);
            }
        });
    }

    getFormValidationErrors() {
        Object.keys(this.form.controls).forEach(key => {
            const controlErrors: ValidationErrors = this.form.get(key).errors;
            // console.log("this.form.controls", this.form.controls);
            if (controlErrors != null) {
                // console.log("Object.keys(controlErrors)", Object.keys(controlErrors));
                // Object.keys(controlErrors).forEach(keyError => {
                //     console.log(keyError);
                //     console.log(
                //         "Key control: " +
                //             key +
                //             ", keyError: " +
                //             keyError +
                //             ", err value: ",
                //         controlErrors[keyError]
                //     );
                // });
            }
        });
    }

    clickOnIdsGoogle() {
        this.permService.openModalPlan(this.permService.LIMIT.SET_SEO);
    }
}



