import {
    NgModule,
    ModuleWithProviders,
    SkipSelf,
    Optional
} from "@angular/core";
import { Configuration } from "./configuration";
import { HttpClient } from "@angular/common/http";

import { CommandeService } from "./api/commande.service";
import { DefaultService } from "./api/default.service";
import { FactureService } from "./api/facture.service";
import { MagasinsService } from "./api/magasins.service";
import { MoyensService } from "./api/moyens.service";
import { PluginsService } from "./api/plugins.service";
import { ProcessService } from "./api/process.service";
import { AuthenticationService } from "../global/services/authentication.service";
import { StatsService } from "./api/stats.service";
import { SendcloudService} from "./api/sendcloud.service";

@NgModule({
    imports: [],
    declarations: [],
    exports: [],
    providers: [
        CommandeService,
        DefaultService,
        FactureService,
        MagasinsService,
        MoyensService,
        PluginsService,
        ProcessService,
        StatsService,
        SendcloudService,
        {
            provide: Configuration,
            useFactory: (authService: AuthenticationService) =>
                new Configuration({
                    accessToken: authService.GetAccessToken(),
                    apiKeys: authService.getApiKeys()
                }),
            deps: [AuthenticationService],
            multi: false
        }
    ]
})
export class ApiModule {
    public static forRoot(
        configurationFactory: () => Configuration
    ): ModuleWithProviders {
        return {
            ngModule: ApiModule,
            providers: [
                { provide: Configuration, useFactory: configurationFactory }
            ]
        };
    }

    constructor(
        @Optional() @SkipSelf() parentModule: ApiModule,
        @Optional() http: HttpClient
    ) {
        if (parentModule) {
            throw new Error(
                "ApiModule is already loaded. Import in your base AppModule only."
            );
        }
        if (!http) {
            throw new Error(
                "You need to import the HttpClientModule in your AppModule! \n" +
                    "See also https://github.com/angular/angular/issues/20575"
            );
        }
    }
}



