import { Component, OnInit, ViewChild, Input } from "@angular/core";
import { StoreService } from "../../services/store.service";
import { MenusService } from "../../../api";
import { ModuleService } from "../../../global/services/module.service";
import { AppService } from "../../../global/services/app.service";

declare var $;

@Component({
    selector: "app-draft-editor",
    templateUrl: "./draft-editor.component.html",
    styleUrls: ["./draft-editor.component.scss"]
})
export class DraftEditorComponent implements OnInit {
    @ViewChild("bodyEditor") bodyEditor;
    @Input() content: string = "";
    draftCpt;
    @Input() draftProps: any = {
        useMedia: false,
        wrapperCss: "wrapper-admin-draf-editor",
        toolbarCss: "rdw-toolbar-media",
        uid: null,
        bearer: null,
        content: "",
        freeImages: [],
        alwaysFocus: true,
        onAction: data => this.onActionDraftJs(data)
    };

    sizeArticle = 13000;
    nearLimiteSizeArticle = { firstTime: false, status: false };
    hasSaveChanges = true;
    has_loaded = false;

    wordCount = 0;
    @Input() wordCountMax = 6000;
    wordCountInError = false;
    @Input() hasMaxWords = true;

    // numero de la dernier page du menu atteinte
    lastPageReached = 0;
    inLoadingNextPage = false;

    actions = {
        loadMenuItems: () => this.loadMenuItems()
    };

    STEprops = {
        stopLoadItems: false,
        treeData: [],
        // onChange: data => {
        //     // console.log("onChange data from react sortable", data);
        //     this.items = this.STEprops.treeData = this.pageService.refreshMenuItems(data);
        //     this.pageService.refreshView = true;
        // },
        onAction: data => {
            // console.log("onAction from draft editor", data.action);
            // todo ajouter action pour la restrictions des pages (open modal...)
            return this.actions[data.action] && this.actions[data.action](data);
        },
        currentPage: null
    };

    constructor(
        private storeService: StoreService,
        private menuApi: MenusService,
        private appService: AppService
    ) {}

    ngOnInit() {
        this.appService.refreshUid();
        this.draftProps.uid = this.storeService.curSite["uid"];
        this.draftProps.bearer =
            "Bearer " + localStorage.getItem("currentUser");
        this.draftProps.toolbar = this.getDefaultToolBar();
        // console.log("--->", this.draftProps.content, this.content);
        if (this.content) {
            this.draftProps.content = this.content;
        }
        if (this.hasMaxWords) {
            this.wordCount = this.getSize(this.content);
        }

        // si draftProps.useMedia === true
        // this.draftProps.freeImages = this.imgLibService.getFreeImages();

        // A deplacer dans un service plus general ?
        // console.log("draft editor subscribe to ListenerReact$--->");
        window["ListenerReact$"].subscribe(e => {
            // console.log("ListenerReact$ in Draft editor", e);
            if (e.app !== "editor") {
                return;
            }
            if (e.action === "want_pages") {
                // window["ListenerEditor$"].next({
                //     action: "want_pages",
                //     data: this.STEprops
                // });
                this.loadMenuItems();
            }
            if (e.action === "want_pages_modules") {
                // console.log("send pages modules", e.data.nameModule, e);
                this.storeService.getPagesModule(e.data.nameModule);
            }
        });

        this.draftCpt = window["DraftEditorRcpt"].default;
        this.has_loaded = true;
    }

    onActionDraftJs(data) {
        // console.log("data", data);
        if (this.hasMaxWords) {
            this.wordCount = this.getSize();
        }

        // this.nearLimiteSizeArticle.status = this.bodyEditor.cptInstance.state.defaultContent.length >= this.sizeArticle - 1000;
        // if (this.nearLimiteSizeArticle.status && this.nearLimiteSizeArticle.firstTime) {
        //     this.nearLimiteSizeArticle.firstTime = false;
        // }

        // if (data.content !== this.draftProps.content) {
        //     this.hasSaveChanges = false;
        // }
    }

    getDefaultToolBar() {
        return {
            options: [
                "inline",
                "blockType",
                "textAlign",
                "list",
                "link",
                "colorPicker"
            ],
            inline: {
                inDropdown: true,
                options: ["bold", "italic", "underline", "strikethrough"],
                bold: { className: "" },
                italic: { className: "" }
            },
            blockType: {
                className: "bordered-option-classname",
                options: ["H1", "H2", "H3", "Normal"]
            },
            textAlign: {
                inDropdown: true,
                className: "",
                options: ["left", "center", "right", "justify"]
            },
            list: {
                inDropdown: true,
                options: ["unordered", "ordered"]
            },
            //   link: {
            //     component: ModalLink,
            //     showOpenOptionOnHover: false,
            //     trailingWhitespace: false,
            //     linkifyItSchemas: {
            //       wifeo: {
            //         validate: function(text, pos, self) {
            //           // should return matched length on success
            //           var tail = text.slice(pos);
            //           // console.log('tail', tail)
            //           return tail.length;
            //         },
            //         normalize: function(match) {
            //           // console.log('match', match)
            //           match.url = match.url.replace(/^wifeo:/, "");
            //         }
            //       }
            //     }
            //   },
            colorPicker: {
                // icon: , // need svg
                className:
                    "we-h-100 we-d-flex we-align-items-center we-justify-content-center",
                // component: ModalColorPic
                colors: [
                    "var(--primary)",
                    "var(--secondary)",
                    "var(--success)",
                    "var(--info)",
                    "rgb(97,189,109)",
                    "rgb(26,188,156)",
                    "rgb(84,172,210)",
                    "rgb(44,130,201)",
                    "rgb(147,101,184)",
                    "rgb(71,85,119)",
                    "rgb(204,204,204)",
                    "rgb(65,168,95)",
                    "rgb(0,168,133)",
                    "rgb(61,142,185)",
                    "rgb(41,105,176)",
                    "rgb(85,57,130)",
                    "rgb(40,50,78)",
                    "rgb(0,0,0)",
                    "rgb(247,218,100)",
                    "rgb(251,160,38)",
                    "rgb(235,107,86)",
                    "rgb(226,80,65)",
                    "rgb(163,143,132)",
                    "rgb(239,239,239)",
                    "rgb(255,255,255)",
                    "rgb(250,197,28)",
                    "rgb(243,121,52)",
                    "rgb(209,72,65)",
                    "rgb(184,49,47)",
                    "rgb(124,112,107)",
                    "rgb(209,213,216)",
                    "rgba(209, 213, 216, 0)"
                ]
            }
        };
    }

    isValidSizeArticle() {
        const res =
            this.bodyEditor.cptInstance.state.defaultContent.length <=
            this.sizeArticle;
        if (!res) {
            alert("L'article est trop charg, veuillez rduire sa taille");
        }

        return res;
    }

    getSize(content?: string) {
        // console.log("get Size content", content);
        let strLength = 0;
        if (typeof content === "string") {
            strLength = content.length;
        } else if (this.bodyEditor) {
            // console.log("jQuery(html).text();", $(this.bodyEditor.cptInstance.state.defaultContent).text());
            strLength = $(
                this.bodyEditor.cptInstance.state.defaultContent
            ).text().length;
        }

        this.wordCountInError = strLength > this.wordCountMax;

        return strLength;
    }

    getContent() {
        return this.bodyEditor.cptInstance.state.defaultContent;
    }

    // Charges les menu items de la page suivante
    loadMenuItems() {
        // console.log("loadMenuItems");
        if (this.inLoadingNextPage || this.STEprops.stopLoadItems) {
            window["ListenerEditor$"].next({
                action: "want_pages",
                data: this.STEprops
            });
            return;
        }

        this.inLoadingNextPage = true;

        this.menuApi
            .apiMenusSlugItemsGet(
                String(this.storeService.curMenu.id),
                ++this.lastPageReached
            )
            .subscribe(paginator => {
                this.inLoadingNextPage = false;
                // console.log("paginator menu items", paginator);
                this.STEprops.treeData = [
                    ...this.STEprops.treeData,
                    ...paginator["items"]
                ];
                // console.log("this.STEprops.treeData", this.STEprops.treeData, paginator["totalItems"]);
                if (this.STEprops.treeData.length === paginator["totalItems"]) {
                    this.STEprops.stopLoadItems = true;
                }
                // update
                window["ListenerEditor$"].next({
                    action: "want_pages",
                    data: this.STEprops
                });
            });
    }
}



