import { Pipe, PipeTransform } from "@angular/core";
import { PermissionService, LIMIT } from "../services/permission.service";

@Pipe({
    name: "hasReachedLimit"
})
export class HasReachedLimitPipe implements PipeTransform {
    constructor(private permService: PermissionService) {}

    transform(dataToCompare: number, key: LIMIT): any {
        return this.permService.hasReachedLimit(key, dataToCompare);
    }
}



