import { InjectionToken } from "@angular/core";

// if jamais on doit savoir quelle app utilise le module ou passer d'autres infos
export const CONFIG_MODULE: InjectionToken<{ CfgModule }> = new InjectionToken<
    CfgModule
>("config");

export enum APP_WIFEO {
    EDITOR = "EDITOR",
    APPS = "APPS"
}
export class CfgModule {
    nameApp?: APP_WIFEO;
}



