import {
    Component,
    OnInit,
    ViewChild,
    ElementRef,
    EventEmitter,
    Output,
    Input,
    OnChanges,
    SimpleChanges,
    AfterViewInit
} from "@angular/core";
import {
    FormBuilder,
    FormGroup,
    Validators,
    FormControl,
    ValidatorFn,
    AbstractControl
} from "@angular/forms";
// import { Image } from '../../classes/Image';
import { ImageLibService } from "../../services/image-lib.service";
import { ReactCptComponent } from "../react-cpt/react-cpt.component";
import { ScriptService } from "../../services/script.service";
import { StoreService } from "../../services/store.service";

declare var $: any;
declare var MediaLibraryRcpt: any;

let nextIdLib = 0;

@Component({
    selector: "app-image-lib",
    templateUrl: "./image-lib.component.html",
    styleUrls: ["./image-lib.component.scss"],
    providers: [ImageLibService]
})
export class ImageLibComponent implements OnInit, OnChanges, AfterViewInit {
    @Input()
    idLib: string | number = `app-image-lib-${nextIdLib++}`;

    @Input()
    preSelectFreeImg: string = "";

    @Input()
    preSelectColor: string = "";

    @Input()
    preSelectBiblioImg: string = "";

    @Input()
    urlImage: string = "";

    @Input()
    uid: string = "";
    @Input()
    startOn: string = null;
    @Input()
    privateFiles: string = null;    

    @Input()
    conf: any = {
        tabs: {
            freeImg: { active: true, startOn: true },
            biblio: { active: true, startOn: false },
            internet: { active: true, startOn: false },
            color: { active: true, startOn: false }
        }
    };

    @Output()
    onSelect: EventEmitter<any> = new EventEmitter();

    @Output()
    onSave: EventEmitter<any> = new EventEmitter();

    @ViewChild("imageFileName")
    imageFileName: ElementRef;

    @ViewChild("btnFreeImg")
    btnFreeImg: ElementRef;

    @ViewChild("mediaLibReact")
    mediaLibReact: ReactCptComponent;

    mediaLibrary: any;

    tabSelected: string = "freeImg";

    idxFreeImg = null;
    idxColor = null;
    idxBiblio = null;

    freeImages: any = [];

    colors: any = [];

    biblioImgs: any = [];

    MLprops = {
        multiSel: false,
        onSelect: this.onSelectBiblio.bind(this),
        bearer: localStorage.getItem("currentUser")
            ? "Bearer " + localStorage.getItem("currentUser")
            : "",
        distantId: null,
        showTagFilters: false
    };

    selectedImg = null;

    formExtUrl: FormGroup;
    formExtUrlSubmitError = false;

    onSelectBiblio(data: any) {
        this.selectedImg = data.img;
        this.onSelect.emit({
            tabSelected: this.tabSelected,
            isImg: true,
            img: this.selectedImg
        });
    }

    constructor(
        private imgLibService: ImageLibService,
        private scriptService: ScriptService,
        private storeService: StoreService,
        private fb: FormBuilder
    ) {
        this.scriptService
            .load("js", "MediaLibraryRcpt")
            .then(data => {
                this.mediaLibrary = MediaLibraryRcpt.default;
            })
            .catch(error => console.log("Err: " + error));
        this.createForm();
    }

    ngAfterViewInit(): void {
        if (this.startOn) {
            $(`#${this.idLib}-${this.startOn}-imageLib-tab`).tab("show");
            setTimeout(() => {
                this.setTabSelected(this.startOn);
            }, 350);
        }
    }

    createForm() {
        this.formExtUrl = this.fb.group({
            urlImgBg: new FormControl(this.urlImage, [
                Validators.required,
                this.forbiddenNameValidator(/bob/i) // <-- Here's how you pass in the custom validator.
            ])
        });
    }

    get urlImgBgForm() {
        return this.formExtUrl.get("urlImgBg");
    }

    forbiddenNameValidator(nameRe: RegExp): ValidatorFn {
        return (control: AbstractControl): { [key: string]: any } | null => {
            const forbidden = nameRe.test(control.value);
            return forbidden
                ? { forbiddenName: { value: control.value } }
                : null;
        };
    }

    ngOnInit() {
        this.getFreeImages();
        this.getColors();
        this.setPreSelect();
        this.MLprops["uid"] = this.uid || this.storeService.curSite["uid"];
        let libraryUid = this.storeService.curSite["uids"].filter(
            uid => uid.nom === "files"
        );


        if(this.privateFiles == "1"){
            this.MLprops['privateFiles'] = 1;
            this.MLprops['showTagFilters'] = true;
        }
        if (libraryUid.length > 0) {
            this.MLprops["distantId"] = libraryUid[0]["distant_id"];
            //console.log("Id MEdia", libraryUid[0]['distant_id']);
        }
    }

    getFreeImages() {
        this.freeImages = this.imgLibService.getFreeImages();
    }

    getColors() {
        this.colors = this.imgLibService.getColors();
    }

    selectFreeImg(index) {
        this.idxFreeImg = index;
        this.onSelect.emit({
            tabSelected: this.tabSelected,
            isImg: true,
            img: this.freeImages[index]
        });
    }

    setTabSelected(typeTab) {
        if (typeTab === "biblio") {
            this.mediaLibReact.binding();
        }

        this.tabSelected = typeTab;
        const selector = `#${this.idLib}-${typeTab}-imageLib-tab`;
        // console.log("selector", selector);
        $(selector).tab("show");
    }

    save() {
        switch (this.tabSelected) {
            case "freeImg":
                this.saveFromFreeImg();
                break;
            case "internet":
                this.saveFromInternet();
                break;
            case "color":
                this.saveFromColor();
                break;
            case "biblio":
                this.saveFromBiblio();
                break;
        }
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.idxFreeImg = null;
        this.idxColor = null;
        this.idxBiblio = null;
        this.setPreSelect();
    }

    setPreSelect() {
        const condFreeImg =
            this.preSelectFreeImg === "" || this.preSelectFreeImg === null;
        const condBiblioImg =
            this.preSelectBiblioImg === "" || this.preSelectBiblioImg === null;
        const condColor =
            this.preSelectColor === "" || this.preSelectColor === null;
        if (!condFreeImg) {
            for (let i = 0; i < this.freeImages.length; i++) {
                if (this.preSelectFreeImg === this.freeImages[i].src) {
                    this.idxFreeImg = i;
                    return;
                }
            }
        }
        if (!condColor) {
            for (let i = 0; i < this.colors.length; i++) {
                if (this.preSelectColor === this.colors[i].className) {
                    this.idxColor = i;
                    return;
                }
            }
        }
        if (!condBiblioImg) {
            for (let i = 0; i < this.biblioImgs.length; i++) {
                if (this.preSelectBiblioImg === this.biblioImgs[i].value) {
                    this.idxBiblio = i;
                    return;
                }
            }
        }
    }

    selectColor(index) {
        this.idxColor = index;
        this.onSelect.emit({ isImg: false, color: this.colors[index] });
    }

    saveFromBiblio() {
        if (this.selectedImg === null) {
            return;
        }
        //console.log('this.selectedImg', this.selectedImg)
        this.onSave.emit({
            tabSelected: this.tabSelected,
            img: this.selectedImg,
            isImg: true
        });
    }

    saveFromFreeImg() {
        if (this.idxFreeImg === null) {
            for (let i = 0; i < this.freeImages.length; i++) {
                if (this.preSelectFreeImg === this.freeImages[i].src) {
                    this.onSave.emit({
                        tabSelected: this.tabSelected,
                        img: this.freeImages[i].src
                    });
                    return;
                }
            }
            return;
        } else {
            this.onSave.emit({
                tabSelected: this.tabSelected,
                img: this.freeImages[this.idxFreeImg],
                isImg: true
            });
        }
    }
    saveFromInternet() {
        this.testImage(this.urlImage)
            .then(() => {
                this.onSave.emit({
                    tabSelected: this.tabSelected,
                    img: { src: this.urlImage },
                    isImg: true
                });
            })
            .catch(() => {
                this.formExtUrlSubmitError = true;
            });
    }

    saveFromColor() {
        if (this.idxColor === null) {
            for (let i = 0; i < this.colors.length; i++) {
                if (this.preSelectColor === this.colors[i].src) {
                    this.onSave.emit({
                        tabSelected: this.tabSelected,
                        color: this.colors[i],
                        isImg: false
                    });
                    return;
                }
            }
            return;
        }
        this.onSave.emit({
            tabSelected: this.tabSelected,
            color: this.colors[this.idxColor],
            isImg: false
        });
    }

    testImage(url, timeoutT?) {
        return new Promise(function(resolve, reject) {
            let timeout = timeoutT || 5000;
            let timer,
                img = new Image();
            img.onerror = img.onabort = function() {
                clearTimeout(timer);
                reject("error");
            };
            img.onload = function() {
                clearTimeout(timer);
                resolve("success");
            };
            timer = setTimeout(function() {
                // reset .src to invalid URL so it stops previous
                // loading, but doens't trigger new load
                img.src = "//!!!!/noexist.jpg";
                reject("timeout");
            }, timeout);
            img.src = url;
        });
    }

    reset() {
        this.preSelectFreeImg = "";
        this.preSelectColor = "";
        this.preSelectBiblioImg = "";
        this.urlImage = "";
        this.selectedImg = null;
        this.formExtUrl.reset();
        this.idxFreeImg = null;
        this.idxColor = null;
        this.idxBiblio = null;
        this.formExtUrlSubmitError = false;
    }

    buildBg() {
        let bg = {};

        bg = {
            class: "",
            gradient: "",
            img: "",
            overlay: "",
            type: "",
            shadow: false, // .shadow || ""
            border: false, // .border || ""
            rounded: false // .rounded || .rounded-0
        };

        return bg;
    }
}



