export class GridLayout {
    xs?: string;
    sm?: string;
    md?: string;
    lg?: string;
    xl?: string;
    extra?: string;

    constructor(fields?: GridLayout) {
        if (fields) {
            Object.assign(this, fields);
        }
    }

    toString() {
        let str = "";
        str += this.xs ? " " + this.xs : "";
        str += this.sm ? " " + this.sm : "";
        str += this.md ? " " + this.md : "";
        str += this.lg ? " " + this.lg : "";
        str += this.xl ? " " + this.xl : "";
        str += this.extra ? " " + this.extra : "";
        return str;
    }
}



