import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { GridLayout } from "../../classes/GridLayout";
import { StoreService } from "../../services/store.service";
import { MenuItem } from "../../classes/menu";
import { Router } from "@angular/router";

@Component({
    selector: "app-sidebar",
    templateUrl: "./sidebar.component.html",
    styleUrls: ["./sidebar.component.scss"]
})
export class SidebarComponent implements OnInit {
    @Input()
    grid: GridLayout = new GridLayout({
        xs: "we-col-7",
        sm: "we-col-sm-5",
        md: "we-col-md-4",
        lg: "we-col-lg-3",
        xl: ""
    });

    @Input()
    styleCss: string = "";

    @Input()
    bg: string = "we-sidebar-default";

    @Input()
    useGrid: boolean = false;

    @Input()
    isToggled: boolean = false;

    @Input()
    styleSidebarClosed: object = {};

    @Input()
    classNameSidebarClosed: string = "";

    @Input()
    items: MenuItem[] = [];

    @Input()
    itemsFooter: MenuItem[] = [
        {
            iconL: { active: true, className: "fal fa-arrow-left we-mr-2" },
            className: "we-sidebar-nav-item we-sidebar-nav-link we-w-100",
            name: "Mes sites",
            command: () => this.goToDashboard(),
            order: 0,
            type: "default",
            linkFill: true
        }
    ];

    @Input()
    iconBack: { show: boolean; title?: string } = { show: true };
    // iconBack: { show: boolean; title?: string } = { show: false, title: "" };

    @Output()
    toggle: EventEmitter<any> = new EventEmitter();

    backTo: string = "/";

    routeLvl = "/";

    constructor(public store: StoreService, private router: Router) {}

    ngOnInit() {
        this.setStyleCss();
        this.setIsToggled();
    }

    onToggle() {
        this.store.toggleSidebar();
        this.setStyleCss();
        this.setIsToggled();
        this.toggle.emit();
    }

    setIsToggled() {
        // this.isToggled = this.store.sidebarIsHidden;
    }
    setStyleCss() {
        // this.styleCss = this.store.styleSidebar;
    }

    goToDashboard() {
        const strMsg = JSON.stringify({
            goTo: "dashboard"
        });
        parent.postMessage(strMsg, "*");
    }

    back() {
        this.router.navigateByUrl("/");
    }
}



