import { Pipe, PipeTransform } from "@angular/core";
import { getCountryName } from "../utils/utils";

@Pipe({
    name: "isocodeCountryToName"
})
export class IsocodeCountryToNamePipe implements PipeTransform {
    transform(isoCode: string): any {
        const name = getCountryName(isoCode);
        if (name) {
            return name;
        } else {
            return isoCode;
        }
    }
}



