import { Injectable } from "@angular/core";
import { ScriptStore, Scripts } from "../store/script.store";
import { formatDomain } from "../utils/utils";
import { environment } from "../../../environments/environment";

declare var document: any;

@Injectable()
export class ScriptService {
    scripts: any = {};
    private scriptsStore = [];

    constructor() {
        ScriptStore.forEach((script: any) => {
            this.scripts[script.name] = {
                loaded: false,
                src: script.src,
                id: script.id,
                dependencies: script.dependencies || [],
                type: script.type || "js",
                name: script.name,
                versioning: script.versioning !== false
            };
        });
    }

    getVersionFiles(script) {
        return script.versioning ? `?v=${environment.versionFiles}` : "";
    }

    load(type: "css" | "js", ...scripts: string[]) {
        const promises: any[] = [];
        scripts.forEach(script => promises.push(this.loadScript(type, script)));
        return Promise.all(promises);
    }

    loadScript(type: "css" | "js", name: string) {
        return new Promise((resolve, reject) => {
            if (!this.scripts[name]) {
                console.error("the script " + name + " isn't in the registry");
                return;
            }
            // resolve if already loaded
            if (this.scripts[name].loaded) {
                const id = this.scripts[name].id;
                resolve({
                    script: name,
                    loaded: true,
                    status: "Already Loaded",
                    id
                });
            } else {
                // load script
                const { id, text } = this.scripts[name];
                const script =
                    type === "css"
                        ? this.scriptCss(name, id, text)
                        : this.scriptJs(name, id, text);
                if (script.readyState) {
                    // IE
                    script.onreadystatechange = () => {
                        if (
                            script.readyState === "loaded" ||
                            script.readyState === "complete"
                        ) {
                            script.onreadystatechange = null;
                            this.scripts[name].loaded = true;
                            resolve({
                                script: name,
                                loaded: true,
                                status: "Loaded",
                                id
                            });
                        }
                    };
                } else {
                    // Others
                    script.onload = () => {
                        this.scripts[name].loaded = true;
                        resolve({
                            script: name,
                            loaded: true,
                            status: "Loaded",
                            id
                        });
                    };
                }
                script.onerror = (error: any) =>
                    resolve({ script: name, loaded: false, status: "Loaded" });
                document.getElementsByTagName("head")[0].appendChild(script);
            }
        });
    }

    loader(...scripts: string[]) {
        const promises: any[] = [];
        scripts.forEach(script => {
            if (this.scripts[script]) {
                const type = this.scripts[script].type;
                promises.push(this.loadScript(type, script));
            }
        });
        return Promise.all(promises);
    }

    scriptJs(name, id?, text?) {
        const script = document.createElement("script");
        if (id) {
            script.id = id;
        }
        if (text) {
            script.text = text;
        }
        script.type = "text/javascript";
        script.src =
            formatDomain(this.scripts[name].src) +
            this.getVersionFiles(this.scripts[name]);
        return script;
    }

    scriptCss(name, id?, text?) {
        const script = document.createElement("link");
        if (id) {
            script.id = id;
        }
        if (text) {
            script.text = text;
        }
        script.type = "text/css";
        script.rel = "stylesheet";
        script.href =
            formatDomain(this.scripts[name].src) +
            this.getVersionFiles(this.scripts[name]);
        return script;
    }

    getScripts() {
        return this.scripts;
    }

    getScriptByName(name) {
        return this.scripts[name];
    }

    addScript(script: Scripts, replace = false) {
        if (!this.scripts[script.name] || replace) {
            this.scripts[script.name] = script;
            return true;
        }
        return false;
    }

    removeScript(name, keepNode = true, keepInStore?: boolean) {
        if (this.scripts[name]) {
            if (!keepNode) {
                const curThemeNode = document.getElementById(
                    this.scripts[name].id
                );
                if (curThemeNode) {
                    curThemeNode.remove();
                }
            }

            if (keepInStore) {
                this.scripts[name].loaded = false;
            } else {
                delete this.scripts[name];
            }

            return true;
        }

        return false;
    }

    isset(name) {
        return typeof this.scripts[name] !== "undefined";
    }

    scriptJsWithContent(content: string, id?: string) {
        const script = document.createElement("script");
        if (id) {
            // test que la node n'existe pas deja, si elle existe on la supprime
            const node = document.getElementById(id);
            if (node) {
                node.remove();
            }

            script.id = id;
        }
        script.type = "text/javascript";
        script.text = content;
        document.getElementsByTagName("head")[0].appendChild(script);
        return script;
    }
}



