import {
    Component,
    OnInit,
    ContentChild,
    TemplateRef,
    Input,
    OnDestroy,
    AfterViewInit,
    ChangeDetectorRef
} from "@angular/core";
import { StoreService } from "../../services/store.service";
import { Router } from "@angular/router";
import { environment } from "../../../../environments/environment";
import { Subscription } from "rxjs";
import { PermissionService } from "../../services/permission.service";

export enum ACTIONS_NAV_HEADER {
    TOGGLE_SIDEBAR = "TOGGLE_SIDEBAR",
    OPEN_SIDEBAR = "OPEN_SIDEBAR",
    CLOSE_SIDEBAR = "CLOSE_SIDEBAR"
}

@Component({
    selector: "app-nav-header",
    templateUrl: "./nav-header.component.html",
    styleUrls: ["./nav-header.component.scss"]
})
export class NavHeaderComponent implements OnInit, OnDestroy, AfterViewInit {
    activeItemMenu: "pages" | "design" | "bureau" | "" = "bureau";

    idOverlayParams = "overlayNavHeaderParams";

    paramsOpen: boolean = false;

    @Input() warnBeforeLeft: () => {
        warning: boolean;
        acceptWarning?: boolean;
    } = () => {
        return { warning: false };
    };

    @ContentChild(TemplateRef) tplRef: TemplateRef<any>;

    hasParams: boolean = false;

    cssItemsNav = "we-btn we-nav-link navheader__btn-nav";
    itemsNav = [
        {
            name: "Pages",
            id: "pages",
            iconCss: "fal fa-rectangle-landscape we-mr-3",
            css: this.cssItemsNav,
            command: () => this.goTo("pages")
        },
        {
            name: "Style",
            id: "design",
            iconCss: "fal fa-brush we-mr-3",
            css: this.cssItemsNav,
            command: () => this.goTo("design")
        },
        {
            name: "Bureau",
            id: "bureau",
            iconCss: "fal fa-cube we-mr-3",
            css: this.cssItemsNav,
            command: () => undefined
        }
    ];

    $subActions: Subscription;
    urlCentreAide = "https://wifeo.zendesk.com/hc/fr";
    urlCentreAideNewRequest = "https://wifeo.zendesk.com/hc/fr/requests/new";

    constructor(
        public store: StoreService,
        private router: Router,
        private cd: ChangeDetectorRef,
        public permService: PermissionService
    ) {}

    ngOnInit() {
        if (this.tplRef) {
            this.hasParams = true;
        }
    }

    ngAfterViewInit(): void {
        this.$subActions = this.store.actions$.subscribe(action => {
            // Pour eviter l'erreur ExpressionChangedAfterItHasBeenCheckedError
            setTimeout(() => {
                switch (action.name) {
                    case ACTIONS_NAV_HEADER.TOGGLE_SIDEBAR:
                        this.toggleParams();
                        break;
                    case ACTIONS_NAV_HEADER.OPEN_SIDEBAR:
                        this.openParams();
                        break;
                    case ACTIONS_NAV_HEADER.CLOSE_SIDEBAR:
                        this.closeParams();
                        break;
                }
            }, 200);
        });
    }

    goTo(type: "pages" | "design" | "dashboard") {
        const strMsg = JSON.stringify({
            goTo: type
        });
        if (type === "pages" || type === "design") {
            if (!this.inIframe()) {
                const { scheme, pathParent } = environment;
                return (location.href = `${scheme}://${pathParent}/editeur`);
            }

            // subterfuge pour avertir les components ayant implementer canDeactivate() que l'iframe va se fermer
            const result = this.warnBeforeLeft();
            // Si pas de warning ou que l'user a accepter le warning -> redirection & fermeture de la modal
            if (!result.warning || result.acceptWarning) {
                parent.postMessage(strMsg, "*");
            }
        } else {
            parent.postMessage(strMsg, "*");
        }
    }

    toggleParams() {
        if (this.paramsOpen) {
            this.closeParams();
        } else {
            this.openParams();
        }
    }

    openParams() {
        this.paramsOpen = true;
    }
    closeParams() {
        this.paramsOpen = false;
    }

    clickOnOverlay(event: Event) {
        if (event.srcElement && event.srcElement.id === this.idOverlayParams) {
            this.closeParams();
        }
    }

    inIframe() {
        try {
            return window.self !== window.top;
        } catch (e) {
            return true;
        }
    }

    ngOnDestroy(): void {
        if (this.$subActions) {
            this.$subActions.unsubscribe();
        }
    }
}



