import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../../services/authentication.service';
import { slug } from '../../../shared/utils/utils';
import { ErrorsService } from '../../services/errors.service';
import 'rxjs/add/operator/finally';


declare var axios: any;
declare var $: any;

@Component({
    moduleId: module.id,
    templateUrl: 'login.component.html'
})

export class LoginComponent implements OnInit {
    model: any = {
        username: '',
        password: ''
    };

    loading = false;
    reset = false;
    loginAction = true;
    email_sended = false;
    error = '';
    returnUrl: string;

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private authenticationService: AuthenticationService,
        private errService: ErrorsService,
    ) { }



    onResetPassword(){

        this.reset = true;

    }

    ngOnInit() {
        // reset login status
        this.authenticationService.logout();

        // get return url from route parameters or default to '/'
        this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
    }

    setAction(action) {
        this.loginAction = action === 'login';
        this.error = '';
    }

    register() {
        this.loading = true;
        this.authenticationService.register(this.model.username1, this.model.password1)
            .finally(() => this.loading = false)
            .subscribe(
                result => {
                    if (result['token'] && result['status']) {
                        this.router.navigate(['/']);
                    } else {
                        this.error = 'Username or password is incorrect';
                    }
                },
                error => {
                    this.errService.handleErrorHttp(error);
                    this.error = error.error && error.error.error || 'Erreur inconnue';
                }
            );
    }


    onEmailReceived(){
        this.email_sended = false;
        this.reset = false;
        this.loginAction = true;
    }


    resetting() {
        this.loading = true;
        this.authenticationService.resetting(this.model.username)
            .finally(() => this.loading = false)
            .subscribe(
                result => {
                    this.email_sended = true;
                },
                error => {
                    this.errService.handleErrorHttp(error);
                    this.error = error.error && error.error.error || 'Erreur inconnue';
                }
            );
    }



    login() {
        this.loading = true;
        this.authenticationService.login(this.model.username, this.model.password)
            .finally(() => this.loading = false)
            .subscribe(
                result => {
                    if (result['token']) {
                        localStorage.setItem('currentUser', result['token']);
                        localStorage.setItem('emailUser', this.model.username);
                        // login successful so redirect to return url
                        // replaceUrl : supprime le path /login de l'historique de navigation
                        this.router.navigateByUrl(this.returnUrl, { replaceUrl: true }).catch(e => {
                            this.router.navigate(['/dashboard'], { replaceUrl: true });
                        });
                    } else {
                        this.error = 'Username or password is incorrect';
                    }
                },
                error => {
                    this.errService.handleErrorHttp(error);
                    this.error = 'Username or password is incorrect';
                }
            );
    }
}



