import {
    Directive,
    Output,
    EventEmitter,
    ElementRef,
    AfterViewInit
} from "@angular/core";

declare var $: any;
export enum EventCollapse {
    SHOW = "show.bs.collapse",
    SHOWN = "shown.bs.collapse",
    HIDE = "hide.bs.collapse",
    HIDDEN = "hidden.bs.collapse"
}

@Directive({
    selector: "[appCollapse]"
})
export class CollapseDirective implements AfterViewInit {
    private element;

    @Output() onShowCollapse = new EventEmitter();
    @Output() onShownCollapse = new EventEmitter();
    @Output() onHideCollapse = new EventEmitter();
    @Output() onHiddenCollapse = new EventEmitter();

    constructor(elementRef: ElementRef) {
        this.element = elementRef.nativeElement;
    }

    ngAfterViewInit() {
        const id = this.element.getAttribute("data-target");
        const $this = this;
        // This event fires immediately when the show instance method is called.
        $(document).on(EventCollapse.SHOW, id, () => {
            $this.onShowCollapse.emit(EventCollapse.SHOW);
        });

        // This event is fired when a collapse element has been made visible to the user (will wait for CSS transitions to complete).
        $(document).on(EventCollapse.SHOWN, id, () => {
            $this.onShownCollapse.emit(EventCollapse.SHOWN);
        });

        // This event is fired immediately when the hide method has been called.
        $(document).on(EventCollapse.HIDE, id, () => {
            $this.onHideCollapse.emit(EventCollapse.HIDE);
        });

        // This event is fired when a collapse element has been hidden from the user (will wait for CSS transitions to complete).
        $(document).on(EventCollapse.HIDDEN, id, () => {
            $this.onHiddenCollapse.emit(EventCollapse.HIDDEN);
        });
    }
}



