import { Injectable } from "@angular/core";
import { Http, Headers, Response } from "@angular/http";
import { HttpClient } from "@angular/common/http";
import "rxjs/add/operator/map";
import { Observable } from "rxjs/Observable";
import { Router } from "@angular/router";
import { environment } from "../../../environments/environment";

@Injectable()
export class AuthenticationService {
    public token: string;

    constructor(private http: HttpClient, private router: Router) {
        // set token if saved in local storage
        if (localStorage.getItem("currentUser")) {
            const currentUser = localStorage.getItem("currentUser");
            this.token = currentUser;
        }
    }

    refresh() {
        this.setToken();
    }

    login(username: string, password: string): Observable<Object> {
        const url = "https://user.wifeosite.com/login_check";
        const body = new FormData();
        body.append("_username", username);
        body.append("_password", password);

        return this.http.post(url, body);
    }

    resetting(username: string): Observable<Object> {
        const url = "https://user.wifeosite.com/resetting/send-email";
        const body = new FormData();
        body.append("username", username);
        //body.append('_password', password);

        return this.http.post(url, body);
    }

    passwordUpdate(password: string, password2: string, id: string): Observable<Object> {
        const url = "https://user.wifeosite.com/resetting/reset/" + id;
        const body = new FormData();
        body.append("fos_user_resetting_form[plainPassword][first]", password);
        body.append("fos_user_resetting_form[plainPassword][second]", password2);

        return this.http.post(url, body);
    }

    register(username: string, password: string): Observable<Object> {
        const url = "https://user.wifeosite.com/register";

        const body = new FormData();
        body.append("_username", username);
        body.append("_password", password);

        return this.http.post(url, body);
    }

    logout(): void {
        // clear token remove user from local storage to log user out
        this.token = null;
        localStorage.removeItem("currentUser");
    }

    setToken() {
        if (localStorage.getItem("currentUser")) {
            const currentUser = localStorage.getItem("currentUser");
            this.token = currentUser;
        }
    }

    GetAccessToken() {
        this.setToken();
        return "Bearer " + this.token;
    }

    getApiKeys() {
        this.setToken();
        return {
            Authorization: "Bearer " + this.token
        };
    }

    getBasePath() {
        let basePath = "";
        if (!environment.production) {
            basePath = "";
        }

        return basePath;
    }
}



