import {
    Directive,
    ElementRef,
    Renderer,
    Input,
    AfterViewInit,
    OnInit,
    Output,
    EventEmitter
} from "@angular/core";
import { ScriptService } from "../services/script.service";
import { Scripts } from "../store/script.store";

/**
 * USE CASE
 *<div fontFamily [font] > //will focus
 *<div appAutofocus="true" > //will focus
 *<div appAutofocus="false"> //will not focus
 */

@Directive({
    selector: "[fontFamily]"
})
export class FontFamilyDirective implements OnInit {
    @Input() font: {
        id: string;
        name: string;
        path: string;
    } = null;
    @Input() idFont = null;
    @Input() nameFont = null;
    @Input() pathFont = null;
    @Output() onFontLoaded: EventEmitter<any> = new EventEmitter();
    constructor(
        private el: ElementRef,
        private renderer: Renderer, // peut pas utiliser !important avec renderer...
        private scriptService: ScriptService
    ) {}

    ngOnInit() {
        this.setProps();
        if (this.pathFont) {
            this.el.nativeElement.setAttribute(
                "style",
                `font-family: ${this.nameFont};display: none;`
            );
            const script: Scripts = {
                id: this.idFont,
                name: this.idFont,
                // On as deja open sans de charger, si on le charge a nouveau ca va deconner avec le changement de font
                // De meme pour les autres fonts souvent repeter comme Roboto (not fixed yet)
                src: this.pathFont
                    .replace("Open+Sans|", "")
                    .replace("Open+Sans", "")
            };

            this.scriptService.addScript(script);
            this.scriptService.loadScript("css", script.name).then((v: any) => {
                this.el.nativeElement.setAttribute(
                    "style",
                    `font-family: ${this.nameFont};display: block;`
                );
                this.onFontLoaded.emit();
            });
        } else {
            this.el.nativeElement.setAttribute(
                "style",
                `font-family: ${this.nameFont};display: block;`
            );
        }
    }

    setProps() {
        if (this.font) {
            if (this.font.id) {
                this.idFont = this.font.id;
            }
            if (this.font.name) {
                this.nameFont = this.font.name;
            }
            if (this.font.path) {
                this.pathFont = this.font.path;
            }
        }
    }
}



