import { Component, OnInit, Output, EventEmitter, Input } from "@angular/core";
import { ScriptService } from "../../services/script.service";
import { formatDomain } from "../../utils/utils";
import { StoreService } from "../../services/store.service";
declare var $: any;

@Component({
    selector: "app-theme-selector",
    templateUrl: "./theme-selector.component.html",
    styleUrls: ["./theme-selector.component.scss"]
})
export class ThemeSelectorComponent implements OnInit {
    @Input()
    setTheme = null;
    @Output()
    onSelected: EventEmitter<any> = new EventEmitter();
    @Output()
    changeStyle: EventEmitter<any> = new EventEmitter();

    prefix: string = "theme-picker-";

    themes = [
        {
            name: "Elegant",
            file: "lux",
            style: {
                height: "80px",
                backgroundColor: "#1a1a1a",
                color: "#fff",
                boxShadow: "0 0 5px #656363"
            }
        },
        {
            name: "Minimal",
            file: "simplex",
            style: {
                height: "80px",
                backgroundColor: "#D9230F",
                color: "#fff",
                boxShadow: "0 0 5px #656363"
            }
        },
        {
            name: "Crisp",
            file: "journal",
            style: {
                height: "80px",
                backgroundColor: "#EB6864",
                color: "#fff",
                boxShadow: "0 0 5px #656363"
            }
        },
        {
            name: "Mint",
            file: "minty",
            style: {
                height: "80px",
                backgroundColor: "#78C2AD",
                color: "#fff",
                boxShadow: "0 0 5px #656363"
            }
        },
        {
            name: "Basic",
            file: "basic",
            style: {
                height: "80px",
                backgroundColor: "#447fff",
                color: "#fff",
                boxShadow: "0 0 5px #656363"
            }
        },
        {
            name: "Metro",
            file: "cosmo",
            style: {
                height: "80px",
                backgroundColor: "#2780E3",
                color: "#fff",
                boxShadow: "0 0 5px #656363"
            }
        },
        {
            name: "Material",
            file: "materia",
            style: {
                height: "80px",
                backgroundColor: "#2196F3",
                color: "#fff",
                boxShadow: "0 0 5px #656363"
            }
        }
        // { name: 'Sketchy', file: 'sketchy', style: { height: '80px', backgroundColor: '#333', color: '#fff' } }
    ];

    currentThemeID = "";

    curTheme = this.themes[4];

    constructor(
        private scriptService: ScriptService,
        public store: StoreService
    ) {}

    ngOnInit() {
        this.themes.map(t => {
            const nameTheme = this.prefix + t.file;
            const url = `https://editor.wifeosite.com/assets/css/${t.file}.min.css`;
            const src = formatDomain(url);

            this.scriptService.addScript({
                id: nameTheme,
                name: nameTheme,
                src
            });

            // if (this.setTheme && this.currentThemeID === "") {
            //     console.log("src", src);
            //     this.currentThemeID =
            //         (this.setTheme.url === src && nameTheme) || "";
            // }

            return (t["src"] = src);
        });
        if (this.setTheme && this.setTheme.file === "custom-user") {
            this.themes.unshift(this.setTheme);
        }

        if (this.store.design_config.config.config) {
            const themeUser = this.themes.find(
                t => t.name === this.store.design_config.config.config.theme
            );
            if (themeUser) {
                this.curTheme = themeUser;
            }
            // sinon c'est le theme par default, cad theme basic
        }
    }

    onSelect(event: Event, theme) {
        if (theme.canSelect === false) {
            return;
        }
        const name = this.prefix + theme.file;

        this.updateStyle(event, theme);

        this.curTheme = theme;

        // if (this.currentThemeID === name) {
        //     this.updateStyle(event, theme);
        //     return;
        // } else {
        //     this.changeTheme(name, theme, event);
        //     this.curTheme = theme;
        // }
    }

    changeTheme(name, theme, event) {
        const scriptCss = document.getElementById("userBS"); // original dans le head, surement temporaire
        this.scriptService.loadScript("css", name).then((v: any) => {
            const { id, script, status } = v;
            if (status === "Already Loaded") {
                const nodeScript = this.scriptService.scriptCss(script, id);
                document
                    .getElementsByTagName("head")[0]
                    .appendChild(nodeScript);
            } else {
                if (scriptCss) {
                    scriptCss.remove();
                }
            }
            this.onSelected.next(theme);
            setTimeout(() => {
                const curThemeNode = document.getElementById(
                    this.currentThemeID
                );
                if (curThemeNode) {
                    curThemeNode.remove();
                }
                this.currentThemeID = id;
                this.updateStyle(event, theme);
            }, 500);
        });
    }

    updateStyle(event: Event, theme) {
        event.preventDefault();
        event.stopPropagation();
        theme.id = this.prefix + theme.file;
        // console.log("update theme", theme);
        this.changeStyle.emit({ theme });
    }
}



