import {
    Component,
    OnInit,
    OnDestroy,
    ElementRef,
    ViewChild,
    AfterViewInit,
    ContentChildren,
    ViewContainerRef,
    ContentChild,
    TemplateRef,
    Input,
    ChangeDetectorRef
} from "@angular/core";

// USE CASE IMPORTANT:
/**
 * <app-viewport-detector>
 *  <ng-template> <!-- OBLIGATOIRE SINON DOUBLE CHILDREN  -->
 *      ...
 *
 * NgTemplate POUR PAS QUE ANGULAR INSERE LES CHILDREN
 * C'EST CE COMPONENT QUI SE CHARGE D'AFFICHER OU NON LES CHILDREN
 */

@Component({
    selector: "app-viewport-detector",
    templateUrl: "./viewport-detector.component.html",
    styleUrls: ["./viewport-detector.component.scss"]
})
export class ViewportDetectorComponent
    implements OnInit, OnDestroy, AfterViewInit {
    element: Element;
    isLoaded: boolean = false;
    observer: IntersectionObserver = null;
    canLoaded: boolean = false;
    @ViewChild("hostCpt") hostCpt: ElementRef;
    @ContentChild(TemplateRef) tplRef;
    @Input() index: number;
    @Input() for: string;
    @Input() options: IntersectionObserverInit;

    constructor(
        private viewContainerRef: ViewContainerRef,
        private cd: ChangeDetectorRef
    ) {}

    log(...args) {
        if (this.for) {
            console.log(...args);
        }
    }

    ngOnInit() {
        // console.log("init TemplateRef", this.tplRef);
    }

    ngOnDestroy(): void {
        // If the IntersectionObserver isn't supported, we never started tracking the
        // given target in the first place.
        if (this.observer) {
            this.observer.unobserve(this.element);
        }
    }

    /**
     * Probleme: vu que tous les components sont vide apres le view init, tous les components seront charger en meme temps
     * Solution: mettre une classe CSS avec un min-height abus
     */
    ngAfterViewInit(): void {
        this.startObservation();
    }

    startObservation() {
        // console.log("on setup SectionComponent", this.hostCpt);
        this.element = this.hostCpt.nativeElement;
        this.observer = new IntersectionObserver(entries => {
            // If intersectionRatio is 0, the cpt is out of view
            // and we do not need to do anything.
            if (entries[0].intersectionRatio <= 0) {
                return;
            }
            if (!this.isLoaded) {
                this.viewContainerRef.createEmbeddedView(this.tplRef);
                this.canLoaded = this.isLoaded = true;
            } else {
                this.unobserve();
            }
        }, this.options);
        this.observer.observe(this.element);
    }

    unobserve() {
        this.observer.unobserve(this.element);
        this.observer = null;
    }

    reObserve() {
        if (!this.isLoaded) {
            this.unobserve();
            this.startObservation();
        }
    }
}



