import { Component, OnInit, Input } from "@angular/core";

@Component({
    selector: "app-loading",
    templateUrl: "./loading.component.html",
    styleUrls: ["./loading.component.scss"]
})
export class LoadingComponent implements OnInit {
    @Input()
    title: string = "";

    @Input()
    type: string = "pulse";

    // opts utiliser pour le type skeleton
    @Input()
    opts: { nbCards: number } = {
        nbCards: 1
    };

    @Input()
    size:
        | ""
        | "fa-xs"
        | "fa-sm"
        | "fa-lg"
        | "fa-2x"
        | "fa-3x"
        | "fa-4x"
        | "fa-5x"
        | "fa-7x"
        | "fa-10x" = "";

    @Input()
    baseClass: string = "we-text-center";

    @Input()
    styleDiv: string = "";

    @Input()
    active: boolean = true;

    @Input()
    fullscreen: boolean = false;

    @Input()
    useDiv: boolean = true;

    loaders: any = [
        { type: "spinner", css: "fas fa-spinner fa-spin" },
        { type: "circle", css: "fas fa-circle-notch fa-spin" },
        { type: "sync", css: "fas fa-sync fa-spin" },
        { type: "cog", css: "far fa-cog fa-spin" },
        { type: "pulse", css: "fas fa-spinner fa-pulse" }
    ];
    loader: { type: string; css: string } = null;

    constructor() {}

    ngOnInit() {
        this.loader =
            this.loaders.find(loader => loader.type === this.type) ||
            this.loader[0];

        if (this.fullscreen) {
            this.styleDiv = `position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 2000;
            background-color:rgba(31, 31, 33, 0.39);
            color: white;
            `;
        }
    }
}



