import { Component, OnInit, Input, Inject } from "@angular/core";
import {
    PermissionService,
    LIMIT,
    ContentModalLimitation
} from "../../services/permission.service";
import { StoreService } from "../../services/store.service";
import { CONFIG_MODULE, CfgModule, APP_WIFEO } from "../../classes/CfgModule";

@Component({
    selector: "app-link-to-plans",
    templateUrl: "./link-to-plans.component.html",
    styleUrls: ["./link-to-plans.component.scss"]
})
export class LinkToPlansComponent implements OnInit {
    // <a />
    @Input() useLink = false;
    // <button />
    @Input() useBtn = true;
    // outline btn
    @Input() outline = false;
    // Afficher l'icon sur small screen
    @Input() responsive = false;
    // pour le plus haut plan
    @Input() renew = false;
    // remplace le texte par ng-content
    @Input() customTitleBtn = false;
    // size btn -> .btn-sm
    @Input() small = false;
    // Ouvre la modal  ou fais une action selon l'app
    @Input() openModal = true;
    // Btn associer ou non a une limite (pour la modal)
    @Input() keyLimit: LIMIT;
    @Input() customModalLim: ContentModalLimitation;
    constructor(
        public permService: PermissionService,
        @Inject(CONFIG_MODULE) private config: CfgModule
    ) {}

    ngOnInit() {}

    onCLickBtn() {
        if (this.openModal) {
            this.permService.openModalPlan(this.keyLimit, this.customModalLim);
        } else {
            switch (this.config.nameApp) {
                case APP_WIFEO.APPS:
                    this.postMsgToPlans();
                    break;
                case APP_WIFEO.EDITOR:
                    this.permService.goToPlans();
                    break;
            }
        }
    }

    onClickLink() {
        if (this.openModal) {
            this.permService.openModalPlan(this.keyLimit, this.customModalLim);
        } else {
            switch (this.config.nameApp) {
                case APP_WIFEO.APPS:
                    this.postMsgToPlans();
                    break;
            }
        }
        return false;
    }

    postMsgToPlans() {
        const strMsg = JSON.stringify({
            goTo: "plans"
        });
        parent.postMessage(strMsg, "*");
    }
}



