import { Component, OnInit, Input, EventEmitter, Output } from "@angular/core";

@Component({
    selector: "editor-element-by-line-form",
    templateUrl: "./element-by-line-form.component.html",
    styleUrls: ["./element-by-line-form.component.scss"]
})
export class ElementByLineFormComponent implements OnInit {
    @Input() active: boolean = false;
    @Input() props: any;
    @Input() model: any;
    @Input() defaultValue: number = 4;
    @Input() title: string = "lments par ligne";
    @Output()
    onUpdate: EventEmitter<any> = new EventEmitter<any>();

    grids = [{ name: "1", val: 1 }, { name: "2", val: 2 }, { name: "3", val: 3 }, { name: "4", val: 4 }];

    constructor() {}

    ngOnInit() {
        if (typeof this.props.cardsInRow === "undefined") {
            this.props.cardsInRow = this.defaultValue;
        }
    }

    changeElByLine(prevCardsInRow) {
        const nextCardsInRow = this.grids.find(grid => grid.val === Number(prevCardsInRow) + 1) || this.grids[0];
        this.props.cardsInRow = nextCardsInRow.val;
        this.onUpdate.emit({ props: this.props, model: this.model });
    }
}



