import { Pipe, PipeTransform } from "@angular/core";
/**
 * Rend lisible une simple chaine de caracteres (combo replace() + toUpperCase())
 *
 * Takes a string as a value.
 * Usage:
 *  value | strReplace
 * Exemple:
 *  name = 'filter-shadow'
 *  name | strReplace : "filter-" : "true"
 *  => Shadow
 *
 * new strReplacePipe().transform(myData, arg1, arg2)
 */
@Pipe({
    name: "strReplace"
})
export class StrReplacePipe implements PipeTransform {
    transform(
        value: any,
        nameToReplace: string,
        upFirstLetter: boolean = true
    ): any {
        value = value.replace(nameToReplace, "");
        if (upFirstLetter) {
            value = value.charAt(0).toUpperCase() + value.slice(1);
        }
        return value;
    }
}



