import {
    Component,
    OnInit,
    ViewChild,
    ElementRef,
    AfterViewInit,
    ChangeDetectorRef
} from "@angular/core";
import {
    FormControl,
    Validators,
    ValidatorFn,
    AbstractControl
} from "@angular/forms";
import { Router } from "@angular/router";
import {
    SitesService,
    PublicService,
    DomainType,
    NddsService,
    NddName,
    Domain
} from "../../../api";
// import { NddsService, SitesService } from "../../../api";
import { StoreService } from "../../services/store.service";
import { ModalService } from "../../services/modal.service";
import { environment } from "../../../../environments/environment";
import { ErrorsService } from "../../../global/services/errors.service";
import { GetMainDomainPipe } from "../../pipes/get-main-domain.pipe";

@Component({
    selector: "app-domain-handler",
    templateUrl: "./domain-handler.component.html",
    styleUrls: ["./domain-handler.component.scss"]
})
export class DomainHandlerComponent implements OnInit, AfterViewInit {
    subDomain = { val: "" }; // TODO A setter avec un service

    subDomainError: string = null;
    subDomainReserved = ["admin", "test"];

    @ViewChild("someInput")
    el: ElementRef;

    subDomainCtrl: FormControl = new FormControl(this.subDomain.val, [
        Validators.required,
        Validators.minLength(4),
        Validators.maxLength(30),
        this.forbiddenNameValidator()
    ]);

    viewRegisterNDD = false;
    pendingUpdate = false;

    constructor(
        private Modalservice: ModalService,
        private SiteApi: SitesService,
        private storeService: StoreService,
        private errorsService: ErrorsService,
        private cd: ChangeDetectorRef
    ) {}

    ngOnInit() {
        const getMainDomain = new GetMainDomainPipe();
        const main: Domain =
            this.storeService.curSite &&
            getMainDomain.transform(this.storeService.curSite.domains);

        if (main) {
            this.subDomain.val = main.name.replace(
                "." + environment.domain,
                ""
            );
        }
    }

    ngAfterViewInit(): void {}

    registerNDD() {
        this.viewRegisterNDD = true;
    }

    /**
     *  Ajout d'un sous domaine gratos
     */
    checkSubDomain() {
        if (this.pendingUpdate) {
            return;
        }
        this.pendingUpdate = true;
        if (!this.subDomainCtrl.valid) {
            return;
        }
        let site = this.storeService.curSite;

        //console.log("valid ok");
        let year = new Date().getFullYear();
        // mettre "Date | any" dans DomainType si probleme pour la date
        let wanted_domain: DomainType = {
            price: "5.99$",
            name: this.subDomain.val + "." + environment.domain,
            expiration: {
                date: { month: 1, day: 1, year: year + 1 },
                time: {
                    hour: 23,
                    minute: 59
                }
            }
        };
        let matching_domain = {};
        let matching_index = 0;
        // tslint:disable-next-line:forin
        for (let i in site.domains) {
            let replace = ".__domain__";
            replace = replace.replace("__domain__", environment.domain);
            const re = new RegExp(replace, "g");
            if (site.domains[i].name.match(re)) {
                //console.log("test");
                matching_domain = site.domains[i];
                matching_index = Number(i);
                this.SiteApi.apiSitesSlugDomainsDomainPut(
                    wanted_domain,
                    site.id.toString(),
                    matching_domain["id"]
                ).subscribe(
                    e => {
                        let d = new Date();
                        d.setFullYear(year + 1);

                        let dformat = d.toISOString();

                        const domain = {
                            id: matching_domain["id"],
                            price: "5.99$",
                            name: this.subDomain.val + "." + environment.domain
                            // expiration: dformat
                        };
                        site.domains[matching_index] = domain;

                        site.domains = site.domains.map((d, i) => {
                            if (i !== matching_index) {
                                return d;
                            }
                            return {
                                ...domain
                            };
                        });

                        const strMsg = JSON.stringify({
                            domainChange: { matching_index, domain }
                        });
                        parent.postMessage(strMsg, "*");

                        this.pendingUpdate = false;
                        this.Modalservice.close("modalParamsGeneral");
                    },
                    error => {
                        this.subDomainError = error.error;
                        this.errorsService.handleErrorHttp(error);
                    },
                    () => {}
                );
            }
        }
    }

    string_to_slug(str) {
        if (!str) {
            return;
        }
        str = str.replace(/^\s+|\s+$/g, "");
        str = str.toLowerCase();
        let from = "/_,:;";
        let to = "aaaaeeeeiiiioooouuuunc------";

        for (let i = 0, l = from.length; i < l; i++) {
            str = str.replace(new RegExp(from.charAt(i), "g"), to.charAt(i));
        }

        str = str
            .replace(/[^a-z0-9 -]/g, "")
            .replace(/\s+/g, "-")
            .replace(/-+/g, "-");

        return str;
    }

    subdomainChanged($event) {
        this.subDomainError = "";
        if ($event) {
            this.subDomain.val = this.string_to_slug($event);
            //console.log(this.subDomain);
            if (this.el) {
                this.el.nativeElement.value = this.string_to_slug($event);
            }
        }
        this.subDomain.val = this.string_to_slug($event);
    }

    forbiddenNameValidator(): ValidatorFn {
        return (control: AbstractControl): { [key: string]: any } => {
            const forbidden =
                this.subDomainReserved.indexOf(control.value) > -1;
            return forbidden
                ? { forbiddenName: { value: control.value } }
                : null;
        };
    }
}



