import { environment } from "../../../environments/environment";

export function isObject(item) {
    return item && typeof item === "object" && !Array.isArray(item);
}

export function toInteger(value: any): number {
    return parseInt(`${value}`, 10);
}

export function toString(value: any): string {
    return value !== undefined && value !== null ? `${value}` : "";
}

export function getValueInRange(value: number, max: number, min = 0): number {
    return Math.max(Math.min(value, max), min);
}

export function scrollTo(classNames: string[], lastOne = false): void {
    const elementList = document.querySelectorAll("." + classNames.join(", ."));
    let element = elementList[0] as HTMLElement;
    if(lastOne){
        if(elementList.length > 0){
            element = elementList[elementList.length-1] as HTMLElement;
        }
    }
    if (element) {
        element.scrollIntoView({ behavior: "smooth" });
    }
}

export function isString(value: any): value is string {
    return typeof value === "string";
}

export function isNumber(value: any): value is number {
    return !isNaN(toInteger(value));
}

export function isInteger(value: any): value is number {
    return (
        typeof value === "number" &&
        isFinite(value) &&
        Math.floor(value) === value
    );
}

export function displayFieldCss(form, field: string) {
    // console.log("form", form, field, !form.get(field).valid);
    return {
        "we-is-invalid": !form.get(field).valid,
    };
}

export function isDefined(value: any): boolean {
    return value !== undefined && value !== null;
}

/**
 *
 * @param {Object} target - Les donnees par default
 * @param {Object} source - Donnees externe
 * @returns {Object} - Object merge
 */
export function mergeDeep(target, source) {
    const output = Object.assign({}, target);
    if (isObject(target) && isObject(source)) {
        Object.keys(source).forEach((key) => {
            if (isObject(source[key])) {
                if (!(key in target)) {
                    Object.assign(output, { [key]: source[key] });
                } else {
                    output[key] = mergeDeep(target[key], source[key]);
                }
            } else {
                Object.assign(output, { [key]: source[key] });
            }
        });
    }
    return output;
}

/**
 * @param {Object} target - Les donnees par default
 * @param {Object} source - Donnees externe
 */
export function simpleMerge(target, source) {
    for (let key in source) {
        if (source.hasOwnProperty(key)) {
            target[key] = source[key];
        }
    }
    return target;
}

//slug == _  pour etre raccord avec la partie pages
export function slug(str: string, strReplace?: string): string {
    if (typeof str !== "string") {
        console.error("Hola, le slug n'est pas une string");
    }
    str = str.replace(/^\s+|\s+$/g, ""); // trim
    str = str.toLowerCase();

    // remove accents, swap  for n, etc
    const from =
        "a/_,:;";
    const to =
        "AAAAAACCCDEEEEEEEEIIIINNOOOOOORRSTUUUUUYYZaaaaaacccdeeeeeeeeiiiinnooooooorrstuuuuuyyzbBDdBAa------";
    for (let i = 0, l = from.length; i < l; i++) {
        str = str.replace(new RegExp(from.charAt(i), "g"), to.charAt(i));
    }

    let remplacement = "-";
    if (strReplace) {
        remplacement = strReplace;
    }

    str = str
        .replace(/[^a-z0-9 -]/g, "") // remove invalid chars
        .replace(/\s+/g, remplacement) // collapse whitespace and replace by -
        .replace(/-+/g, remplacement); // collapse dashes

    return str;
}

// Safely Accessing Deeply Nested Values

export function get(path: any[], object) {
    return path.reduce((xs, x) => (xs && xs[x] ? xs[x] : null), object);
}

export function findObjectByAttr(
    root,
    attr: string,
    value,
    attrChildren: string,
    exclude?: { key: string; value: string }
) {
    if (
        typeof value !== "undefined" &&
        root[attr] === value &&
        root[exclude.key] !== exclude.value
    ) {
        return root;
    }
    if (root[attrChildren]) {
        for (let i = 0; i < root[attrChildren].length; i++) {
            const cond =
                typeof exclude === "undefined"
                    ? root[attrChildren][i][attr] === value
                    : root[attrChildren][i][attr] === value &&
                    root[attrChildren][i][exclude.key] !== exclude.value;
            if (cond) {
                return { node: root[attrChildren][i], index: i, parent: root };
            } else if (root[attrChildren][i][attrChildren]) {
                const node = findObjectByAttr(
                    root[attrChildren][i],
                    attr,
                    value,
                    attrChildren,
                    exclude
                );
                if (node) {
                    return node;
                }
            }
        }
    }

    return null;
}

export function findObjectByAttrAndDelete(
    root,
    attr: string,
    value,
    attrChildren: string,
    exclude?: { key: string; value: string }
) {
    if (
        typeof value !== "undefined" &&
        root[attr] === value &&
        root[exclude.key] !== exclude.value
    ) {
        return root;
    }
    if (root[attrChildren]) {
        for (let i = 0; i < root[attrChildren].length; i++) {
            const cond =
                typeof exclude === "undefined"
                    ? root[attrChildren][i][attr] === value
                    : root[attrChildren][i][attr] === value &&
                    root[attrChildren][i][exclude.key] !== exclude.value;
            if (cond) {
                // delete root[attrChildren][i];
                root[attrChildren].splice(i, 1);
                return root;
            } else if (root[attrChildren][i][attrChildren]) {
                const node = findObjectByAttr(
                    root[attrChildren][i],
                    attr,
                    value,
                    attrChildren,
                    exclude
                );
                if (node) {
                    root[attrChildren][i][attrChildren].splice(node.index, 1);
                    return root;
                }
            }
        }
    }

    return null;
}

export function moveElementInArray(array, value, positionChange) {
    let oldIndex = array.indexOf(value, 0);

    if (oldIndex > -1) {
        let newIndex = positionChange;
        if (newIndex < 0) {
            newIndex = 0;
        } else if (newIndex >= array.length) {
            newIndex = array.length;
        }

        const arrayClone = array.slice();
        arrayClone.splice(oldIndex, 1);
        arrayClone.splice(newIndex, 0, value);

        return arrayClone;
    }
    return array;
}

export function stristr(haystack, needle, bool) {
    //  discuss at: http://locutus.io/php/stristr/
    // original by: Kevin van Zonneveld (http://kvz.io)
    // bugfixed by: Onno Marsman (https://twitter.com/onnomarsman)
    //   example 1: stristr('Kevin van Zonneveld', 'Van')
    //   returns 1: 'van Zonneveld'
    //   example 2: stristr('Kevin van Zonneveld', 'VAN', true)
    //   returns 2: 'Kevin '

    var pos = 0;

    haystack += "";
    pos = haystack.toLowerCase().indexOf((needle + "").toLowerCase());
    if (pos === -1) {
        return false;
    } else {
        if (bool) {
            return haystack.substr(0, pos);
        } else {
            return haystack.slice(pos);
        }
    }
}

export function debounce(func, wait, immediate: boolean = false) {
    let timeout, result;
    return function () {
        let context = this,
            args = arguments;
        let later = function () {
            timeout = null;
            if (!immediate) {
                func.apply(context, args);
            }
        };
        let callNow = immediate && !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) {
            result = func.apply(context, args);
            context = args = null;
        }
        // Return timeout so debounced function can be cancelled
        result = result || {};
        result.timeout = timeout;

        return result;
    };
}

/**
 * Change si besoin le scheme et le domain de la requete pass en paramtre
 * @param str url de la requete
 */
export function formatDomain(str: string) {
    // On recupere les bonnes variables selon l'environment d'export
    let { scheme, domain, reactProd } = environment;
    let defaultScheme = "https";
    let defaultDomain = "wifeosite.com";

    if (defaultDomain === domain && defaultScheme === scheme) {
        return str;
    }

    let newStr = str;

    if (!reactProd) {
        newStr = newStr.replace(defaultScheme, scheme);
        // console.log("newStr", newStr);

        const pathAws = "awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/";
        const urlSplit = str.indexOf(pathAws) + 1 && str.split(pathAws);
        if (urlSplit) {
            if (urlSplit.length === 2) {
                const pathSplit = urlSplit[1].split("/");
                const subdomain =
                    pathSplit[0] === "platform" ? "api" : pathSplit[0];
                defaultDomain =
                    "awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/" +
                    pathSplit[0];
                domain = subdomain + "." + domain;
            }
        }
    }

    newStr = newStr.replace(defaultDomain, domain);
    // console.log("newStr", newStr);
    return newStr;
}

export function getSrcSet(srcs) {
    if (typeof srcs === "undefined") {
        return srcs;
    }

    let _srcs = null;
    if (typeof srcs === "object") {
        _srcs = srcs;
    } else if (typeof srcs === "string" && srcs.charAt(0) === "{") {
        _srcs = JSON.parse(srcs);
    }

    if (!_srcs) {
        return;
    }

    const sizes = [
        "default",
        160,
        320,
        480,
        640,
        800,
        1024,
        1280,
        1440,
        1600,
        1920,
    ];
    let srcSet = [];
    if (_srcs.default) {
        srcSet.push(`${_srcs.default} 1x`);
    } else {
        sizes.forEach((size) => {
            const _size = _srcs[size];
            if (_size) {
                srcSet.push(`${_srcs[size]} ${size}w`);
            }
        });
    }

    return srcSet.join(",");
}

export function hexToRgb(hex, forCss?, opacity?): any {
    if (hex === "transparent") {
        if (forCss) {
            return hex;
        }
        return { r: 255, g: 255, b: 255, a: 0 };
    }
    function hexToR(h) {
        return parseInt(cutHex(h).substring(0, 2), 16);
    }
    function hexToG(h) {
        return parseInt(cutHex(h).substring(2, 4), 16);
    }
    function hexToB(h) {
        return parseInt(cutHex(h).substring(4, 6), 16);
    }
    function cutHex(h) {
        return h.charAt(0) === "#" ? h.substring(1, 7) : h;
    }
    const r = hexToR(hex);
    const g = hexToG(hex);
    const b = hexToB(hex);
    const rgb = { r, g, b };

    if (typeof opacity !== "undefined") {
        const a = opacity;
        if (forCss) {
            return `rgba(${r}, ${g}, ${b}, ${a})`;
        }
        return { ...rgb, a };
    }

    if (forCss) {
        return `rgb(${r}, ${g}, ${b})`;
    }

    return rgb;
}

/**
 * Certain valeur Hex des themes sont composer de 3 lettres/chiffres uniquement (ex: #369)
 * on les transforme donc avec 6 lettres/chiffres
 */
export function fixHex(hex: string) {
    function insert(str, index, value) {
        return str.substr(0, index) + value + str.substr(index);
    }

    if (hex.length === 4) {
        hex = insert(hex, 1, hex.substr(1, 1)); // #3369
        hex = insert(hex, 3, hex.substr(3, 1)); // #33669
        hex = insert(hex, 5, hex.substr(5, 1)); // #336699
        // console.log("hex update ->", hex);
    }

    return hex;
}

export function encodeQueryData(data) {
    const ret = [];
    for (let d in data) {
        if (typeof data[d] !== "undefined") {
            ret.push(encodeURIComponent(d) + "=" + encodeURIComponent(data[d]));
        }
    }
    return ret.join("&");
}

