import {
    Component,
    OnInit,
    ChangeDetectorRef,
    NgZone,
    ViewChild
} from "@angular/core";
import { NotificationService } from "./global/services/notification.service";
import { ReactService } from "./shared/services/react.service";
import {
    Router,
    RouteConfigLoadStart,
    RouteConfigLoadEnd
} from "@angular/router";
import { StoreService } from "./shared/services/store.service";
import { ScriptService } from "./shared/services/script.service";

import { environment } from "../environments/environment";
import { SitesService, PagesService } from "./api";
import { ErrorsService } from "./global/services/errors.service";
import { ModalComponent } from "./shared/components/modal/modal.component";
import { Subject } from "rxjs/Subject";
import { AppService } from "./global/services/app.service";
import "moment/locale/fr";
import { ModuleService } from "./global/services/module.service";

@Component({
    selector: "app-root",
    templateUrl: "./app.component.html",
    styleUrls: ["./app.component.scss"]
})
export class AppComponent implements OnInit {
    notification: string;
    showNotification: boolean = false;

    reactLoaded = false;

    reactJs = "ReactProd";
    reactDomJs = "ReactDomProd";
    @ViewChild(ModalComponent) modalErrorApp: ModalComponent;
    loadingRouteConfig: boolean;

    constructor(
        private notificationService: NotificationService,
        private cd: ChangeDetectorRef,
        public router: Router,
        public store: StoreService,
        public moduleService: ModuleService,
        private siteApi: SitesService,
        private appService: AppService,
        private scriptService: ScriptService,
        private _ngZone: NgZone,
        private errorsService: ErrorsService,
        private pageApi: PagesService
    ) {
        window["ngModuleDash"] = { component: this, zone: _ngZone };
    }

    ngOnInit() {
        this.initInterceptorAxios();
        if (window) {
            window["__ENV_MODE"] = Object.freeze(environment);
        }

        function receiveMessage(event) {
            // Do we trust the sender of this message?
            // console.log("receiveMessage", event);
            const { scheme, pathParent, apiProd, production } = environment;

            if (!apiProd) {
                if (!production && event.origin !== `http://${pathParent}`) {
                    return;
                }
                if (production && event.origin !== `https://${pathParent}`) {
                    return;
                }
            } else {
                if (event.origin !== `https://editor.wifeosite.com`) {
                    return;
                }
            }

            let msg = JSON.parse(event.data);
            // console.log("msg", msg);
            let site = msg.site;
            let path = msg.path;

            // console.log("receiveMessage", event);

            localStorage.setItem("currentUser", msg.token);
            if (site) {
                localStorage.setItem("webSiteId", site.id);
            }

            // console.log("NgIframeModule :", window);

            window["ngModuleDash"].zone.run(() => {
                window["ngModuleDash"].component.runFromOutside(msg);
            });
        }

        window.addEventListener("message", receiveMessage, false);

        this.notificationService.notification.subscribe(message => {
            this.notification = message;
            // this.showNotification = true;
            this.cd.detectChanges();
        });

        this.startApp();
    }

    startApp() {
        window["storeReact"] = {};
        window["ListenerReact$"] = new Subject(); // a init dans app
        window["ListenerEditor$"] = new Subject();

        localStorage.removeItem("webSiteId");
        // console.log("start apps ");
        this.runFromOutside(JSON.parse(localStorage.getItem("msg")) || {});

        // if (this.store.curSite) {
        //     this.initSite();
        // } else {
        // }

        this.router.events.subscribe(event => {
            if (event instanceof RouteConfigLoadStart) {
                this.loadingRouteConfig = true;
            } else if (event instanceof RouteConfigLoadEnd) {
                this.loadingRouteConfig = false;
            }
        });
    }

    parseJwt(token) {
        let base64Url = token.split(".")[1];
        let base64 = base64Url.replace("-", "+").replace("_", "/");
        return JSON.parse(window.atob(base64));
    }

    getUsername() {
        let e = localStorage.getItem("currentUser");
        if (!e) {
            return "UNDEFINED";
        }
        let token = this.parseJwt(e);
        if (typeof token.username != undefined) {
            return token.username;
        }
        return "UNDEFINED";

    }

    runFromOutside(msg) {
        if (msg.site) {
            this.store.curSite = msg.site;
        }
        if (msg.menu) {
            this.store.curMenu = msg.menu;
        }

        if (this.store.curSite) {
            this.appService.refreshUid();
        }

        this.store.design_config = msg.design_config;
        if (this.store.design_config) {
            this.store.updateThemeSite();
            this.store.loadFont();
        }

        //  console.log(JSON.stringify(msg));
        setTimeout(() => {
            this.store.checkIfSmallDevice();
        }, 100);
        // if (msg.pages) {
        //     this.store.pages = msg.pages;
        // }

        this.store.designModules = msg.designModules;
        // const scriptDesignModule = `window.__CFG_SITE=${JSON.stringify(this.store.designModules)};`;
        // this.scriptService.scriptJsWithContent(scriptDesignModule, "script-design-modules");
        if (msg.curThemeUser) {
            /**
             * On detect si le theme de l'user a changer (changement de site ou changement de theme BS)
             * On recupere d'abord le script "themeBSUser" dans le store des scripts dans scriptService
             *
             * Si la source du script (src) est different alors il y a eu un changement
             *     -> On supprime du store le script "themeBSUser"
             *     -> On le charge a nouveau
             *
             * Si la source du script (src) est identique alors il n'y a eu aucun changement
             *     -> On ne fait rien
             */
            let canLoadScript = false;
            const nameScriptBSUser = "themeBSUser";
            // console.log("theme user", this.scriptService.scripts[nameScriptBSUser]);
            const curThemeInAdmin = this.scriptService.scripts[
                nameScriptBSUser
            ];

            // console.log("--->", curThemeInAdmin, msg.curThemeUser);
            // console.log("--->", JSON.stringify(msg.curThemeUser));
            // premier init -> pas encore de theme setter
            if (!curThemeInAdmin) {
                canLoadScript = true;
            } else {
                this.scriptService.scripts[nameScriptBSUser].loaded = false;
                const curThemeNode = document.getElementById("userBS");
                if (curThemeNode) {
                    const scriptRemoved = this.scriptService.removeScript(
                        nameScriptBSUser,
                        true
                    );
                    if (scriptRemoved) {
                        curThemeNode.remove();
                    }
                }
                canLoadScript = true;
                // }
            }

            if (canLoadScript) {
                this.store.curThemeUser = msg.curThemeUser;
                this.scriptService.addScript({
                    id: "userBS",
                    name: nameScriptBSUser,
                    src: this.store.curThemeUser.url,
                    type: "css"
                });
                // this.scriptService.loader(nameScriptBSUser);
            }
        }
        this.initSite(() => {
            //console.log("initSite", msg);
            if (this.store.curSite) {
                const cd_sitePlan =
                    (this.store.curSite.plan &&
                        this.store.curSite.plan.name) ||
                    "no plan";

                window["__A_UID"] = this.getUsername() + "_" + this.store.curSite.uid;
                window["__A_PLAN"] = cd_sitePlan;
            }


            // console.log('navigate vers ', msg.path)
            // this.router.navigateByUrl(msg.path);

            // /!\ les access direct ne marcheront plus
            if (msg.path) {
                // this.store.updateStore(); // evite une 404 si l'id du site a changer
                this.router.navigateByUrl(msg.path);
            }

            localStorage.setItem("msg", JSON.stringify({ ...msg, path: "" }));

            if (this.store.curSite && this.modalErrorApp) {
                this.modalErrorApp.close();
            }
        });
    }

    /**
     * si l'user n'est pas pass par le dashboard, on teste si il a un id site dans le localStorage
     *
     * @param id id site
     */
    initSiteFromLocal(id) {
        this.siteApi.apiSitesSiteGet(String(id)).subscribe(
            site => {
                this.store.curSite = site;
            },
            error => {
                this.errorsService.handleErrorHttp(error);
                localStorage.removeItem("webSiteId");
                this.router.navigate(["dashboard"]);
            }
        );
    }

    /**
     * init le site en recuperant
     */
    async initSite(callback?: Function) {
        this.setReact();
        await this.loadScripts(callback);
    }

    async loadScripts(callback?: Function) {
        await this.scriptService
            .loadScript("css", "SortableTreeEditorCss")
            .then(async () => {
                await this.scriptService.loadScript("js", "userBoostrapJs");
                await this.scriptService
                    .load("js", this.reactJs)
                    .then(async value => {
                        await this.scriptService
                            .load("js", this.reactDomJs)
                            .then(async value => {
                                await this.scriptService
                                    .load("js", "WifeoEngine")
                                    .then(async () => {
                                        await this.scriptService
                                            .load(
                                                "js",
                                                "MediaLibraryRcpt",
                                                "SortableTreeEditorRcpt"
                                            )
                                            .then(async () => {
                                                await this.scriptService
                                                    .load(
                                                        "js",
                                                        "DraftEditorRcpt"
                                                    )
                                                    .then(() => {
                                                        this.reactLoaded = true;
                                                        if (
                                                            typeof callback ===
                                                            "function"
                                                        ) {
                                                            callback();
                                                        }
                                                    });
                                            });
                                    });
                            });
                    });
            });
    }

    setReact() {
        if (!environment.reactProd) {
            this.reactJs = "ReactDev";
            this.reactDomJs = "ReactDomDev";
        }
    }

    goTo(type: string) {
        const strMsg = JSON.stringify({
            goTo: type
        });
        parent.postMessage(strMsg, "*");
    }

    initInterceptorAxios() {
        window["axios"].interceptors.response.use(
            response => {
                // Do something with response data
                return response;
            },
            error => {
                // https://github.com/axios/axios#handling-errors
                this.errorsService.handleErrorHttp(error.response);
                return Promise.reject(error);
            }
        );
    }
}



