import { Injectable } from "@angular/core";
import { StoreService } from "./store.service";
import { Site } from "../../api/model/site";
import { ModalService } from "./modal.service";
import { environment } from "../../../environments/environment";

// type exceptPermission = string | string[];

// FREE < PRO < PREMIUM
export enum PLAN {
    FREE = "FREE",
    // INTERMEDIAIRE = "INTERMEDIAIRE",
    PRO = "PRO",
    PREMIUM = "PREMIUM",
}

export enum PLAN_CSS {
    FREE = "plan-free",
    // INTERMEDIAIRE = "plan-intermediaire",
    PRO = "plan-pro",
    PREMIUM = "plan-premium",
}

export enum LIMIT {
    NB_PRODUITS_SHOP = "NB_PRODUITS_SHOP",
    NB_FORMATIONS = "NB_FORMATIONS",
    NB_FORMATIONS_ELEVES = "NB_FORMATIONS_ELEVES",
    NB_POSTS_BLOG = "NB_POSTS_BLOG",
    ACTIVATE_ONLINE_PAYMENT_SHOP = "ACTIVATE_ONLINE_PAYMENT_SHOP",
    NB_PAGES = "NB_PAGES",
    NB_SECTIONS_PER_PAGE = "NB_SECTIONS_PER_PAGE",
    SET_FAVICON = "SET_FAVICON",
    SET_DOMAIN = "SET_DOMAIN",
    SET_SEO = "SET_SEO",
    REMOVE_ADS = "REMOVE_ADS",
}

type forPlan = PLAN | PLAN[];
type exceptForPlan = forPlan;
export interface ContentModalLimitation {
    title: string;
    msg: string;
    list: boolean;
    labelBtn?: string;
    avantage?: { msg: string };
}

@Injectable()
export class PermissionService {
    // Nom des plans & des actions si check needed (fichier .TS & .HTLM)
    PLAN = PLAN;
    // Non des limitations
    LIMIT = LIMIT;
    // Tableau  de plans
    PLANS = Object.values(PLAN);
    // id pour la modal
    idModalUpgradePlan = "modalUpgradePlan";
    // Affiche ou non la modal
    showModalPlan = false;

    // Store des limitations selon un plan
    // ES6 : { [yourKeyVariable]: someValueArray}
    limitations: { [key: string]: { [key: string]: number } } = {
        [PLAN.FREE]: {
            [LIMIT.NB_PAGES]: 5,
            [LIMIT.NB_SECTIONS_PER_PAGE]: 8,
            [LIMIT.NB_PRODUITS_SHOP]: 5,
            [LIMIT.NB_POSTS_BLOG]: 10,
            [LIMIT.NB_FORMATIONS]: 1,
            [LIMIT.NB_FORMATIONS_ELEVES]: 50,
        },
        [PLAN.PRO]: {
            [LIMIT.NB_PAGES]: 50,
            [LIMIT.NB_SECTIONS_PER_PAGE]: 20,
            [LIMIT.NB_PRODUITS_SHOP]: 25,
            [LIMIT.NB_POSTS_BLOG]: 100,
            [LIMIT.NB_FORMATIONS]: 5,
            [LIMIT.NB_FORMATIONS_ELEVES]: 50,
        },
        [PLAN.PREMIUM]: {
            [LIMIT.NB_PAGES]: 9999,
            [LIMIT.NB_SECTIONS_PER_PAGE]: 20,
            [LIMIT.NB_PRODUITS_SHOP]: 9999,
            [LIMIT.NB_POSTS_BLOG]: 9999,
            [LIMIT.NB_FORMATIONS]: 9999,
            [LIMIT.NB_FORMATIONS_ELEVES]: 9999,
        },
    };

    // Url du shop wifeo pour ajouter un plan au panier
    // linkPlans = "https://www.wifeocms.com/plans";
    linkPlans = `${environment.scheme}://${environment.hostWifeoCms}/${environment.pathToBuyPlans}`;
    // Msg par defaut du btn de la modal
    msgLinkPlans = "Passer au plan suprieur";
    cssLinkPlans = "link-plans";
    // icon fontawesome a utiliser l ou y'a du plan payant
    iconPlan = "fal fa-crown";

    // Store des msg de limitations selon une limitation
    msgLimitations: { [key: string]: ContentModalLimitation } = {
        [LIMIT.NB_PRODUITS_SHOP]: {
            title: "Augmenter votre nombre de produits",
            msg: "",
            list: true,
            avantage: {
                msg: "Ajouter plus de %val% produits sur votre boutique",
            },
        },
        [LIMIT.NB_FORMATIONS]: {
            title: "Augmenter votre nombre de formations",
            msg: "",
            list: true,
            avantage: {
                msg: "Ajouter plus de %val% formations sur votre boutique",
            },
        }, 
        [LIMIT.NB_FORMATIONS_ELEVES]: {
            title: "Augmenter votre nombre d'lves",
            msg: "",
            list: true,
            avantage: {
                msg: "Ajouter plus de %val% lves par formation",
            },
        },         
        [LIMIT.NB_POSTS_BLOG]: {
            title: "Augmenter votre nombre d'articles",
            msg: "",
            list: true,
            avantage: {
                msg: "Ajouter plus de %val% articles de blog",
            },
        },
        [LIMIT.ACTIVATE_ONLINE_PAYMENT_SHOP]: {
            title: "Activer les paiements en ligne",
            msg: "",
            list: true,
            avantage: {
                msg:
                    "Permetter  vos clients de raliser des paiements en ligne",
            },
        },
        [LIMIT.NB_PAGES]: {
            title: "Augmenter votre nombre de pages",
            msg: "",
            list: true,
            avantage: {
                msg: "Ajouter plus de %val% pages  votre site",
            },
        },
        [LIMIT.NB_SECTIONS_PER_PAGE]: {
            title: "Ajouter plus de sections",
            msg: "",
            list: true,
            avantage: {
                msg: "Ajouter plus de %val% sections par page",
            },
        },
        [LIMIT.SET_DOMAIN]: {
            title: "Ajouter un nom de domaine personnalis",
            msg: "",
            list: true,
            avantage: {
                msg:
                    "Renforcer la confiance de vos visiteurs avec un nom de domaine personnalis",
            },
        },
        [LIMIT.SET_FAVICON]: {
            title: "Ajouter un favicon personnalis",
            // msg:
            //     "Passer  un plan suprieur pour choisir un favicon. Un favicon personnalis ajoute de la lgitimit  votre site et renforce la confiance des visiteurs.",
            msg: "",
            list: true,
            avantage: {
                msg:
                    "Ajouter de la lgitimit  votre site avec un favicon personnalis",
            },
        },
        [LIMIT.SET_SEO]: {
            title: "Profiter d'un meilleur rfrencement",
            msg: "",
            list: true,
            avantage: {
                msg: "Amliorer votre rfrencement",
            },
        },
        [LIMIT.REMOVE_ADS]: {
            title: "Retirer les publicits",
            msg: "",
            list: true,
            avantage: {
                msg: "Retirer les publicits",
            },
        },
    };

    // Content de la modal utilis dans le template et setter selon limitation ou bien custom
    contentModal: ContentModalLimitation = {
        title: "",
        msg: "",
        list: true,
    };

    constructor(
        private store: StoreService,
        private modalService: ModalService
    ) {}

    /**
     * Check si le plan du site appartient aux plans autoris pass en paramtre
     */
    IsAuthorized(
        forPlan: forPlan,
        exceptFor: exceptForPlan,
        site?: Site
    ): boolean {
        let IsAuthorized = false;
        const _site = site ? site : this.store.curSite;

        if (!_site) {
            return false;
        }
        if (!_site.plan) {
            return false;
        }
        const namePlan = _site.plan.name;

        if (typeof forPlan === "string") {
            if (namePlan === forPlan) {
                IsAuthorized = true;
            }
        }

        if (Array.isArray(forPlan)) {
            const nameFound = forPlan.find((name) => name === namePlan);
            if (nameFound) {
                IsAuthorized = true;
            }
        }

        if (typeof exceptFor === "string") {
            if (namePlan !== exceptFor) {
                IsAuthorized = true;
            }
        }

        if (Array.isArray(exceptFor)) {
            const nameFound = exceptFor.find((name) => name === namePlan);
            if (!nameFound) {
                IsAuthorized = true;
            }
        }

        return IsAuthorized;
    }

    /**
     * Check si une limitation est atteinte ou non
     * @param key
     * @param dataToCompare
     */
    hasReachedLimit(key: LIMIT, dataToCompare: number): boolean {
        const limit = this.getLimit(key);
        // console.log("hasReachedLimit", key, dataToCompare);
        // console.log("limit", limit);
        if (typeof limit === "undefined") {
            return false;
        } else {
            return dataToCompare >= limit;
        }
    }

    getLimit(key: LIMIT) {
        const limit = this.limitations[this.store.curSite.plan.name][key];

        return limit;
    }

    /**
     * remove title et msg
     */
    clearModal() {
        this.contentModal.title = "";
        this.contentModal.msg = "";
    }

    /**
     * A chaque ouverture de modal on clear le title et msg
     * on recupere ensuite le bon msg et title ou le contenu custom
     * @param key
     * @param custom
     */
    openModalPlan(key: LIMIT, custom?: ContentModalLimitation) {
        this.clearModal();
        if (key) {
            const msgLim = this.msgLimitations[key];
            if (msgLim) {
                this.contentModal = { ...msgLim };
            }
        }

        if (custom) {
            this.contentModal = { ...custom };
        }

        this.showModalPlan = true;
        this.modalService.open(this.idModalUpgradePlan);
    }

    goToPlans() {
        location.href = this.linkPlans;
    }

    getNamePlanCss(namePlan: PLAN) {
        return PLAN_CSS[namePlan];
    }

    /**
     * Retourne le plan superieur
     */
    getNextNamePlan(namePlan: PLAN) {
        switch (namePlan) {
            case PLAN.FREE:
                return PLAN.PRO;
            case PLAN.PRO:
                return PLAN.PREMIUM;
        }
    }
}



