import { BrowserModule } from "@angular/platform-browser";
import { NgModule, ErrorHandler } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { AppRoutingModule } from "./app-routing.module";

import { BrowserAnimationsModule } from "@angular/platform-browser/animations"; // this is needed!

import { AppComponent } from "./app.component";
import { SharedModule } from "./shared/shared.module";
import { Configuration, ApiModule } from "./api";
import { AuthenticationService } from "./global/services/authentication.service";
import { ErrorsComponent } from "./global/components/errors/errors.component";
import { LoginComponent } from "./global/components/login/login.component";
import { PasswordComponent } from "./global/components/password/password.component";

import { LogoutComponent } from "./global/components/logout/logout.component";
import { ErrorsHandler } from "./global/components/class/errors-handler";
import { HTTP_INTERCEPTORS } from "@angular/common/http";
import { HTTPListener } from "./global/components/class/http-listener.interceptor";
import { ErrorsService } from "./global/services/errors.service";
import { NotificationService } from "./global/services/notification.service";

import { ApiModule as ApiModuleBlog } from "./api-blog";
import { ApiModule as ApiModuleShop } from "./api-shop";
import { ApiModule as ApiModulePaiement } from "./api-paiement";
import { ApiModule as ApiModuleMember } from "./api-member";
import { ApiModule as ApiModuleUser } from "./api-user";
import { ApiModule as ApiModuleForm } from "./api-form";
import { ApiModule as ApiModuleFormation } from "./api-formation";

import { HTTPStatus } from "./global/components/class/http-status";
import { GoogleAnalyticsService } from "./global/services/google-analytics.service";
import { AutoSizeMainDirective } from "./global/directives/auto-size-main.directive";
import { AutoSizeSidebarDirective } from "./global/directives/auto-size-sidebar.directive";
import { AppService } from "./global/services/app.service";
import { ModuleService } from "./global/services/module.service";
import { ApiModule as ApiModuleGallery } from "./api-gallerie";
import { APP_WIFEO } from "./shared/classes/CfgModule";
import { ExternalLoginComponent } from "./global/components/external-login/external-login.component";


const RxJS_Services = [HTTPListener, HTTPStatus];

@NgModule({
    declarations: [
        AppComponent,
        LoginComponent,
        PasswordComponent,
        LogoutComponent,
        ErrorsComponent,
        ExternalLoginComponent,
        AutoSizeMainDirective,
        AutoSizeSidebarDirective
    ],
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        AppRoutingModule,
        FormsModule,
        ReactiveFormsModule,
        ApiModule,
        ApiModuleBlog,
        ApiModuleShop,
        ApiModulePaiement,
        ApiModuleGallery,
        ApiModuleMember,
        ApiModuleForm,
        ApiModuleFormation,
        ApiModuleUser,
 
        SharedModule.forRoot({
            nameApp: APP_WIFEO.APPS
        })
    ],
    providers: [
        AuthenticationService,
        ErrorsService,
        NotificationService,
        GoogleAnalyticsService,
        AppService,
        ModuleService,
        ...RxJS_Services,
        {
            provide: ErrorHandler,
            useClass: ErrorsHandler
        },
        // {
        //     provide: HTTP_INTERCEPTORS,
        //     useClass: HTTPListener,
        //     multi: true
        // },
        {
            provide: Configuration,
            useFactory: (authService: AuthenticationService) =>
                new Configuration({
                    accessToken: authService.GetAccessToken(),
                    apiKeys: authService.getApiKeys()
                }),
            deps: [AuthenticationService],
            multi: false
        }
    ],
    bootstrap: [AppComponent]
})
export class AppModule {}

