import { ErrorHandler, Injectable, Injector } from "@angular/core";
import {
    Location,
    LocationStrategy,
    PathLocationStrategy
} from "@angular/common";
import { HttpErrorResponse } from "@angular/common/http";
import { Observable } from "rxjs/Observable";
import "rxjs/add/observable/of";
// Cool library to deal with errors: https://www.stacktracejs.com
import * as StackTraceParser from "error-stack-parser";
import { Router, Event, NavigationError } from "@angular/router";
import { ModalService } from "../../shared/services/modal.service";
import { StoreService } from "../../shared/services/store.service";
import { NotificationService } from "./notification.service";
@Injectable()
export class ErrorsService {
    errors: CodeError[] = [];
    constructor(
        private injector: Injector,
        private router: Router,
        private modalService: ModalService,
        private notifService: NotificationService
    ) {
        this.initErrors();
        // Listen to the navigation errors
        this.router.events.subscribe((event: Event) => {
            // Redirect to the ErrorComponent
            if (event instanceof NavigationError) {
                if (!navigator.onLine) {
                    return;
                }
                // Redirect to the ErrorComponent
                this.log(event.error);
                // console.log("navigation errors -> event.error", event.error);

                // .subscribe((errorWithContext) => { // if server
                //     this.router.navigate(['/error'], { queryParams: errorWithContext, replaceUrl: true });
                // });
            }
        });
    }

    log(error) {
        // Log the error to the console
        console.error(error);
        // console.error("this.addContextInfo(error)", this.addContextInfo(error));
        // this.router.navigate(['/error'], { queryParams: error, replaceUrl: true }).then(() => console.log('test'));
        // Send error to server
        // const errorToSend = this.addContextInfo(error);
        // return fakeHttpService.post(errorToSend);
    }
    addContextInfo(error) {
        // All the context details that you want (usually coming from other services; Constants, UserService...)
        const store = this.injector.get(StoreService);
        const name = error.name || null;
        const user =
            (store.curSite && store.curSite.uid) ||
            (localStorage && localStorage.getItem("emailUser")) ||
            "anonymous or user not defined";
        const time = new Date().getTime();
        const id = `${user}-${time}`;
        const location = this.injector.get(LocationStrategy);
        const url =
            location instanceof PathLocationStrategy ? location.path() : "";
        const status = error.status || null;
        const message = error.message || error.toString();
        const stack =
            error instanceof HttpErrorResponse
                ? null
                : StackTraceParser.parse(error);
        const errorToSend = {
            name,
            user,
            time,
            id,
            url,
            status,
            message,
            stack
        };
        return errorToSend;
    }

    openModal() {
        this.modalService.open("modalErrorApp");
    }

    handleErrorHttp(error, notify: boolean = false) {
        switch (error.status) {
            case 401:
                if (
                    (error.error &&
                        error.error.message === "Expired JWT Token") ||
                    (error.error && error.error.message === "Invalid JWT Token")
                ) {
                    localStorage.removeItem("currrentUser");
                    this.openModal();
                } else {
                    this.notifService.notify(
                        "Vous n'tes pas autoris  accder  la ressource demande."
                    );
                }
                // console.log("error 401", error);
                break;
            case 500: // todo
                this.notifService.notify("erreur 500 :(");
                // alert("erreur 500 :(");
                break;
            case 502: // todo
            // console.log("error 502", error);
            case 504: // todo
                this.notifService.notify(
                    "La connexion a chou. Veuillez ressayer dans quelques minutes."
                );
                // alert("La connexion a chou. Veuillez ressayer dans quelques minutes.");
                break;
            case 404: // todo
                this.notifService.notify(
                    "La ressource demande n'est pas disponible."
                );
                // alert("La ressource demande n'est pas disponible.");
                break;
            case 400:
            case 402:
                const code = error.error && error.error.code;
                const val = error.error && error.error.query;
                const msg = this.getMsgError(code, val);
                if (msg.length > 0) {
                    if (notify) {
                        this.notifService.notify(msg);
                    }
                    return msg;
                }

                /**
                 * On doit avoir un code d'erreur qu'on fait matcher, et la limitation
                 */
                break;
            default:
                // todo
                this.notifService.notify("Une erreur inconnue est survenue.");
                // alert("Une erreur inconnue est survenue.");
                break;
        }
    }

    getMsgError(code, val) {
        const { msg } =
            this.errors.find(err => err.code === code) ||
            this.errors.find(err => err.code === "LIM_DEFAULT");

        return msg.replace("%val%", val);
    }

    initErrors() {
        const preSentence = "Vous avez atteint la limite de ";
        // const preSentenceDemo = "Le mode <strong>demo</strong> est limit  ";
        this.errors = [
            {
                code: "UNKNOW",
                msg: "Une erreur est survenue."
            },
            {
                code: "URL_PAGE_EXISTS",
                msg: "Une page possde dj cette url."
            },
            {
                code: "LIM_SHOP_1",
                msg: preSentence + "%val% produits/produits dmaterialiss maximum."
            },
            {
                code: "LIM_FORM_1",
                msg: preSentence + "%val% formation maximum."
            },            
            {
                code: "LIM_DEFAULT",
                msg: "Vous ne pouvez pas ajouter plus d'lments."
            }
        ];
    }
}

interface CodeError {
    code: string;
    msg: string;
}



