import { Icon } from "./icon";
import { MenuItemComponent } from "../components/menu-item/menu-item.component";
import { TemplateRef } from "@angular/core";

export class Menu {}

export class PanelOptionsAction {
    name: string;
    icon?: Icon;
    action: string;
    value?: boolean;
    input?: any;
    className?: any;
    visible: boolean;
    title?: string; // attr title DOM
    /* Comment on gere dans la vue les actions provoquant des modifications dans la vue du panel ? */
    /* comme le cas pour l'adresse du lien externe ! */
    /* event: any ? */
}

export class PanelOptions {
    type: string;
    options: PanelOptionsAction[];

    constructor({}) {}
}

export class MenuItem {
    name?: string;
    active?: boolean;
    inEdit?: boolean;
    command?: (event?: any, itemCpt?: MenuItemComponent) => void;
    clickIconL?: (event?: any) => void;
    clickIconR?: (event?: any) => void;
    commandOpt?: (event?: any) => void;
    commandsOptCollapse?: {
        show?: (event?: any) => void;
        shown?: (event?: any) => void;
        hide?: (event?: any) => void;
        hidden?: (event?: any) => void;
    };
    commandsOpt2Collapse?: {
        show?: (event?: any) => void;
        shown?: (event?: any) => void;
        hide?: (event?: any) => void;
        hidden?: (event?: any) => void;
    };
    url?: string;
    routerLink?: any;
    slug?: string;
    queryParams?: { [k: string]: any };
    children?: MenuItem[];
    disabled?: boolean;
    visible?: boolean;
    target?: string;
    routerLinkActiveOptions?: any;
    iconR?: Icon;
    iconL?: Icon;
    iconOpt?: Icon;
    iconOpt2?: Icon;
    style?: any;
    className?: string;
    title?: string;
    id?: string | number;
    action?: string;
    control?: any;
    order?: number;
    linkFill?: boolean;
    nameInEdit?: boolean;
    type: "link" | "page" | "input" | "dropdown" | "default";
    isDnD?: boolean;
    placeholder?: string;
    options?: PanelOptionsAction[];
    page?: any;
    tplOptions?: TemplateRef<any>;
    tplOptions2?: TemplateRef<any>;
    model?: any;
    isNouveaute?: boolean;

    constructor(fields?: any) {
        if (fields) {
            Object.assign(this, fields);
        }
    }
}



