import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { Configuration } from './configuration';
import { HttpClient } from '@angular/common/http';


import { AttributService } from './api/attribut.service';
import { AttributValeurService } from './api/attributValeur.service';
import { CategorieService } from './api/categorie.service';
import { CouponService } from './api/coupon.service';
import { DefaultService } from './api/default.service';
import { EmailService } from './api/email.service';
import { FabricantService } from './api/fabricant.service';
import { FormuleService } from './api/formule.service';
import { FournisseurService } from './api/fournisseur.service';
import { GrtService } from './api/grt.service';
import { MagasinService } from './api/magasin.service';
import { MessageDispoService } from './api/messageDispo.service';
import { OptionpanierService } from './api/optionpanier.service';
import { PreferenceService } from './api/preference.service';
import { ProduitService } from './api/produit.service';
import { ProduitPersonnaliseService } from './api/produitPersonnalise.service';
import { PublicService } from './api/public.service';
import { StatsService } from './api/stats.service';
import { TagService } from './api/tag.service';
import { TransporteurService } from './api/transporteur.service';
import { AuthenticationService } from '../global/services/authentication.service';

@NgModule({
  imports:      [],
  declarations: [],
  exports:      [],
  providers: [
    AttributService,
    AttributValeurService,
    CategorieService,
    CouponService,
    DefaultService,
    EmailService,
    FabricantService,
    FormuleService,
    FournisseurService,
    GrtService,
    MagasinService,
    MessageDispoService,
    OptionpanierService,
    PreferenceService,
    ProduitService,
    ProduitPersonnaliseService,
    PublicService,
    StatsService,
    TagService,
    TransporteurService,
    {
        provide: Configuration,
        useFactory: (authService: AuthenticationService) =>
            new Configuration({ accessToken: authService.GetAccessToken(), apiKeys: authService.getApiKeys() }),
        deps: [AuthenticationService],
        multi: false
    }
]
})
export class ApiModule {
    public static forRoot(configurationFactory: () => Configuration): ModuleWithProviders {
        return {
            ngModule: ApiModule,
            providers: [ { provide: Configuration, useFactory: configurationFactory } ]
        };
    }

    constructor( @Optional() @SkipSelf() parentModule: ApiModule,
                 @Optional() http: HttpClient) {
        if (parentModule) {
            throw new Error('ApiModule is already loaded. Import in your base AppModule only.');
        }
        if (!http) {
            throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
            'See also https://github.com/angular/angular/issues/20575');
        }
    }
}



