import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";

import { FormGroup } from "@angular/forms";
import { MenuItem } from "../../../shared/classes/menu";
import { EventCollapse } from "../../directives/collapse.directive";

let nextIdMenuItem = 0;
declare var $: any;

@Component({
    selector: "app-menu-item",
    templateUrl: "./menu-item.component.html",
    styleUrls: ["./menu-item.component.scss"]
})
export class MenuItemComponent implements OnInit {
    /**
     * Item courant
     */
    @Input() item: MenuItem;

    /**
     * index courant de l'item
     */
    @Input() index: any;

    /**
     * id de l'item
     */
    @Input() id: string = `app-menu-item-${nextIdMenuItem++}`;

    /**
     * id du menu parent
     */
    @Input() idMenuParent: string = "";

    /**
     * le lvl de profondeur a laquel se situe l'item
     */
    @Input() lvl: number = 0;

    /**
     * indique si l'element est un enfant
     */
    @Input() isChild: boolean = false;

    /**
     * le formulaire angular pour l'item
     */
    @Input() formEdit: FormGroup;

    /**
     * container css
     */
    @Input() containerCss: string = "";

    /**
     * Event onAction
     */
    @Output() onAction: EventEmitter<any> = new EventEmitter();

    /**
     * stockage temporaire de variable en edition
     */
    tmp = {
        name: ""
    };

    constructor() {}

    ngOnInit() {}

    /**
     * met a jour la variable temporaire 'name' lors de l'edition du nom de l'item
     */
    editNameItem($event): void {
        this.tmp.name = $event;
    }

    /**
     * ouvrir le panel d'option associ a l'item
     */
    openPanelOption() {
        $("#collapse-" + this.id).weCollapse("show");
    }

    /**
     * ouvrir le panel d'option associ a l'item
     */
    togglePanelOption() {
        $("#collapse-" + this.id).weCollapse("toggle");
    }

    /**
     * Met a jour une option
     */
    setValueOpt($event) {
        this.item[$event.attr] = this.item.options[
            $event.indexOpt
        ].input.value = $event.event;
    }

    onClickItem(item: MenuItem) {
        if (typeof item.command === "function") {
            item.command();
        }
    }

    handleEventCollapse($event: EventCollapse, command, item) {
        if (command) {
            const { show, shown, hide, hidden } = command;
            switch ($event) {
                case EventCollapse.SHOW:
                    if (typeof show === "function") {
                        show(item);
                    }
                    break;
                case EventCollapse.SHOWN:
                    if (typeof shown === "function") {
                        shown(item);
                    }
                    break;
                case EventCollapse.HIDE:
                    if (typeof hide === "function") {
                        hide(item);
                    }
                    break;
                case EventCollapse.HIDDEN:
                    if (typeof hidden === "function") {
                        hidden(item);
                    }
                    break;
            }
        }
    }

    runCommand($event) {
        if (this.item.command) {
            this.item.command($event, this);
        } else if (this.item.action) {
            this.onAction.emit(this.item.action);
        }
    }
}



