const pathImg = "assets/img/icon_animation_section/";
export interface AnimationSection {
    label: string;
    imgSrc: string;
    effect: string;
}
export const AnimationStore: AnimationSection[] = [
    {
        label: "Aucun",
        imgSrc: pathImg + "anim_none.svg",
        effect: "none"
    },
    {
        label: "Slide in",
        imgSrc: pathImg + "anim_slide_in.svg",
        effect: "fadeInDown"
    },
    {
        label: "Fade in",
        imgSrc: pathImg + "anim_fade_in.svg",
        effect: "fadeIn"
    },
    {
        label: "Swing in",
        imgSrc: pathImg + "anim_swing_in.svg",
        effect: "flipInX"
    },
    {
        label: "Puff in",
        imgSrc: pathImg + "anim_puff_in.svg",
        effect: "zoomIn"
    }
];



