import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";

@Component({
    selector: "editor-ratio-media-form",
    templateUrl: "./ratio-media-form.component.html",
    styleUrls: ["./ratio-media-form.component.scss"]
})
export class RatioMediaFormComponent implements OnInit {
    @Input() active: boolean = false;
    @Input() props: any;
    @Input() model: any;
    @Input() title: string = "Proportion des mdias";
    @Input() idxMep: number = 2;
    @Output()
    onUpdate: EventEmitter<any> = new EventEmitter<any>();

    mepColumns = [
        {
            label: "1/6",
            media: "col-md-2",
            text: "col-md-10",
            percentage: "16%"
        },
        {
            label: "1/4",
            media: "col-md-3",
            text: "col-md-9",
            percentage: "25%"
        },
        {
            label: "1/3",
            media: "col-md-4",
            text: "col-md-8",
            percentage: "33%"
        },
        {
            label: "1/2",
            media: "col-md-6",
            text: "col-md-6",
            percentage: "50%"
        },
        {
            label: "2/3",
            media: "col-md-8",
            text: "col-md-4",
            percentage: "66%"
        },
        {
            label: "3/4",
            media: "col-md-9",
            text: "col-md-3",
            percentage: "75%"
        },
        {
            label: "5/6",
            media: "col-md-10",
            text: "col-md-2",
            percentage: "84%"
        }
    ];

    ngOnInit() {
        this.model.mepColumns = this.mepColumns[this.idxMep];
        this.initMepColumn();
    }

    initMepColumn() {
        const mepOpts = this.props.mepOpts;
        if (mepOpts) {
            const mepColumns = mepOpts.columns;
            if (mepColumns) {
                this.model.mepColumns = this.mepColumns.find(
                    mepC =>
                        mepC.media === mepColumns.media &&
                        mepC.text === mepColumns.text
                );
            }
        }
    }

    checkMepOpts() {
        if (!this.props.mepOpts) {
            this.props.mepOpts = {};
        }
    }

    /**
     * Change le ration des elements
     * cad que l'image peut prendre 1/3, 2/3 ou 1/2 de la place sur une ligne de 12 colonnes BS
     */
    changeMepColumns(percentage) {
        this.checkMepOpts();
        const curIdxMep = this.mepColumns.findIndex(
            mep => mep.percentage === percentage
        );
        const nextMep = this.mepColumns[curIdxMep + 1] || this.mepColumns[0];

        this.model.mepColumns = nextMep;
        const { media, text } = nextMep;
        this.props.mepOpts.columns = { media, text };

        this.onUpdate.emit({ props: this.props, model: this.model });
    }
}



