import { Component, OnInit } from "@angular/core";
import {
    PermissionService,
    ContentModalLimitation,
    PLAN
} from "../../services/permission.service";
import { StoreService } from "../../services/store.service";

@Component({
    selector: "app-modal-info-plan",
    templateUrl: "./modal-info-plan.component.html",
    styleUrls: ["./modal-info-plan.component.scss"]
})
export class ModalInfoPlanComponent implements OnInit {
    avantages: any[] = [];
    lastPlanShow: PLAN = null;
    constructor(
        public permService: PermissionService,
        private store: StoreService
    ) {}

    ngOnInit() {}

    initMsg() {
        const namePlan =
            (this.store.curSite && <PLAN>this.store.curSite.plan.name) ||
            this.permService.PLAN.FREE;
        if (this.lastPlanShow === namePlan) {
            return;
        }
        // this.avantages = Object.keys(this.permService.msgLimitations);
        const arrMsgLim = Object.keys(this.permService.msgLimitations);

        arrMsgLim.forEach(key => {
            const msgLimitations = this.permService.msgLimitations[key];
            if (msgLimitations.list) {
                // const nextPlan = this.permService.getNextNamePlan(namePlan);
                // Si on utilise le nextPlan, il faudra surement des phrases du genre ajouter jusqu'...
                // opti possible: retirer les avantages deja inclus dans le plan actuel de l'user et n'afficher que les nouveaux ou avec limite suprieur
                const val = String(
                    this.permService.limitations[namePlan][key] || 0
                );
                this.avantages.push({
                    key,
                    msg: msgLimitations.avantage.msg.replace("%val%", val)
                });
            }
        });
        this.lastPlanShow = namePlan;
    }
}



