import {
    Component,
    OnInit,
    Output,
    EventEmitter,
    Input,
    ViewChild,
    OnChanges,
    SimpleChanges,
    ChangeDetectorRef,
} from "@angular/core";
import { ScriptService } from "../../services/script.service";
import { ReactCptComponent } from "../react-cpt/react-cpt.component";
import { hexToRgb, fixHex } from "../../utils/utils";

import { SketchPicker } from "react-color";
import { ModalComponent } from "../modal/modal.component";
import { FilterCssStore } from "../../store/filterCss.store";
import { ImageLibComponent } from "../image-lib/image-lib.component";
import {
    FormGroup,
    Validators,
    FormControl,
    ValidatorFn,
    AbstractControl,
} from "@angular/forms";

declare var $: any;
let nextIdBgEditor = 0;

/**
 * Background editor de section ou card
 */
@Component({
    selector: "app-background-editor",
    templateUrl: "./background-editor.component.html",
    styleUrls: ["./background-editor.component.scss"],
})
export class BackgroundEditorComponent implements OnInit, OnChanges {
    @ViewChild("rcptColor") rcptColor: ReactCptComponent;
    @ViewChild("rcptGradient") rcptGradient: ReactCptComponent;
    @ViewChild("rcptAngle") rcptAngle: ReactCptComponent;
    @ViewChild("modalMedia") modalMedia: ModalComponent;
    @ViewChild("imgLib") imgLib: ImageLibComponent;
    @Input()
    curBg: any = {};
    @Input()
    disableVideo = false;
    @Input()
    confLib: any = {};
    @Input()
    index: number = 0;
    @Output()
    onSelectedBg: EventEmitter<any> = new EventEmitter();
    @Output()
    onOpenLib: EventEmitter<any> = new EventEmitter();
    @Output()
    onCloseLib: EventEmitter<any> = new EventEmitter();

    displayer = {
        none: false,
        color: false,
        colorOverlay: false,
        image: false,
        video: false,
        rcptColor: false,
        rcptGradient: false,
        rcptsOverlay: false,
    };

    displayMoreFilters: boolean = false;

    cssHandler = {
        presetRoundsCol: "we-col we-pb-1 we-pr-0",
        presetRounds:
            "we-rounded-circle we-round-color-sm we-d-flex we-justify-content-center we-align-items-center",
        stylePresetRounds: "box-shadow: rgb(51, 51, 51) 0px 0px 2px;",
        customRounds: "m-auto pos-abs p-1 rounded-circle",
        styleCustomRounds: "background-color: rgba(255, 255, 255, 0.56)",
        iconCustomRounds: "fas fa-fill-drip",
        formGroup: "we-form-group we-mb-0",
    };

    // PRESETS COLORS & GRADIENTS
    // surcharger dans le initScheme
    colors = [
        "bg-white",
        "bg-primary",
        "bg-secondary",
        "bg-success",
        "bg-info",
    ];

    // les rgb/rgba pour les presets uniquement, le component react calcul le rgba a partir du hex et de l'opacit
    gradients: any = [
        {
            angle: "180deg",
            hex: "transparent",
            hex2: "#000000",
            rgba: "transparent",
            rgba2: "rgba(0, 0, 0, 0.5)",
            opacity: 0,
            opacity2: 0.5,
        },
        {
            angle: "180deg",
            hex: "transparent",
            hex2: "",
            rgba: "transparent",
            rgba2: "",
            opacity: 0,
            opacity2: 0.5,
        },
        {
            angle: "180deg",
            hex: "transparent",
            hex2: "",
            rgba: "transparent",
            rgba2: "",
            opacity: 0,
            opacity2: 0.5,
        },
        {
            angle: "180deg",
            hex: "transparent",
            hex2: "",
            rgba: "transparent",
            rgba2: "",
            opacity: 0,
            opacity2: 0.5,
        },
        {
            angle: "180deg",
            hex: "transparent",
            hex2: "",
            rgba: "transparent",
            rgba2: "",
            opacity: 0,
            opacity2: 0.5,
        },
    ];

    // surcharg par initScheme
    presetUser = [
        { color: "var(--primary)", title: "Couleur primaire" },
        { color: "var(--secondary)", title: "Couleur secondaire" },
        { color: "var(--success)", title: "Couleur tertiaire" },
        { color: "var(--info)", title: "Couleur quaternaire" },
    ];

    rcptPickerColor: any;
    pickerColorLoaded: boolean = false;
    colorCustom = "rgba(255, 255, 255, 1);";
    gradientCustom = {
        angle: "0deg",
        hex: "#21d4fd",
        hex2: "#b721ff",
        rgba: "rgb(33, 212, 253)",
        rgba2: "rgb(183, 33, 255)",
        opacity: 1,
        opacity2: 0.8,
    };
    rcptPickerColorProps: any = {
        color: "#ffffff",
        presetColors: this.presetUser,
        showPopover: true,
        onChangeComplete: (color) => this.handleChange(color),
        onClose: this.handleClose.bind(this),
    };

    rcptPickerColorPropsGradient1: any = {
        ...this.rcptPickerColorProps,
        onChangeComplete: (color) => this.handleChange(color, true),
    };

    rcptPickerColorPropsGradient2: any = {
        ...this.rcptPickerColorPropsGradient1,
        onChangeComplete: (color) => this.handleChange(color, false),
    };

    angleLoaded: boolean = false;

    // todo

    knobIsInit: boolean = false;

    wantAddImg: boolean = false;

    images = [];
    srcImg = null;

    filters = FilterCssStore.filter((f, i) => i % 4 === 0);
    filtersMore = FilterCssStore.filter((f, i) => i % 4 !== 0);

    formGrpVideo = new FormGroup({
        link: new FormControl("", [
            Validators.minLength(1),
            Validators.maxLength(500),
            this.validateYouTubeUrl(),
        ]),
    });

    get link() {
        return this.formGrpVideo.get("link");
    }

    constructor(
        private scriptService: ScriptService,
        private cd: ChangeDetectorRef
    ) {}

    ngOnInit() {
        this.rcptPickerColor = SketchPicker;
        this.pickerColorLoaded = true;

        this.refreshBg();

        this.initScheme();

        this.scriptService.addScript({
            name: "knobJquery",
            src: "assets/js/jquery.knob.min.js",
            type: "js",
            versioning: false,
        });
        this.scriptService.loader("knobJquery").then(() => {
            this.angleLoaded = true;
        });

        this.initVideoLink();
    }

    initVideoLink() {
        if (this.curBg.video) {
            if (this.curBg.video.src) {
                this.formGrpVideo.setValue({ link: this.curBg.video.src });
            }
        }
    }

    validateYouTubeUrl(): ValidatorFn {
        return (control: AbstractControl): { [key: string]: any } | null => {
            const idVideo = this.getVideoIdYoutube(control.value);
            return !idVideo ? { link: { value: control.value } } : null;
        };
    }

    getVideoIdYoutube(url) {
        var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=|\?v=)([^#\&\?]*).*/;
        var match = url.match(regExp);
        if (match && match[2].length == 11) {
            return match[2];
        }
    }

    moreFilters() {
        this.filters = this.filters.concat(this.filtersMore);
        this.displayMoreFilters = true;
    }

    setActiveMenu() {
        if (
            this.curBg.type === "img" ||
            (typeof this.curBg.type === "undefined" &&
                this.curBg.img &&
                this.curBg.img.path !== "")
        ) {
            this.displayer.image = true;
            this.setSrcImg();
            if (this.srcImg === null) {
                this.openLibImage();
            }
        }
        if (this.curBg.type === "color") {
            this.displayer.color = true;
        }
        if (this.curBg.type === "video") {
            this.displayer.video = true;
        }
    }

    setSrcImg() {
        this.srcImg = (this.curBg.img && this.curBg.img.path) || null;
    }

    initScheme() {
        const bodyStyles = getComputedStyle(document.body);
        // bug sur les feuilles css perso, il y a un espace au debut des variables
        const hexPrimary = fixHex(
            bodyStyles.getPropertyValue("--primary").trim()
        );
        const hexSecondary = fixHex(
            bodyStyles.getPropertyValue("--secondary").trim()
        );
        const hexSuccess = fixHex(
            bodyStyles.getPropertyValue("--success").trim()
        );
        const hexInfo = fixHex(bodyStyles.getPropertyValue("--info").trim());

        const rgbaPrimary = hexToRgb(hexPrimary, true, 0.5);
        const rgbaSecondary = hexToRgb(hexSecondary, true, 0.5);
        const rgbaSuccess = hexToRgb(hexSuccess, true, 0.5);
        const rgbaInfo = hexToRgb(hexInfo, true, 0.5);

        this.gradients[1].hex2 = hexPrimary;
        this.gradients[1].rgba2 = rgbaPrimary;

        this.gradients[2].hex2 = hexSecondary;
        this.gradients[2].rgba2 = rgbaSecondary;

        this.gradients[3].hex2 = hexSuccess;
        this.gradients[3].rgba2 = rgbaSuccess;

        this.gradients[4].hex2 = hexInfo;
        this.gradients[4].rgba2 = rgbaInfo;

        this.colors = [
            "#FFFFFF",
            hexPrimary,
            hexSecondary,
            hexSuccess,
            hexInfo,
        ];

        this.presetUser[0].color = hexPrimary;
        this.presetUser[1].color = hexSecondary;
        this.presetUser[2].color = hexSuccess;
        this.presetUser[3].color = hexInfo;
    }

    /**
     * Active ou desactive les elements extras d'un background
     * les classes cres :
     * -> shadow : .shadow || ""
     * -> border : .border || ""
     * -> rounded : .rounded || .rounded-0
     *
     * /!\ rounded marche pas trop (marche pas avec  une img et chelou sur le text) && il faut une bordur bien sur...
     */
    toggleExtra(key: string) {
        const bg = {};
        bg[key] = !this.getCurrentExtra(key);
        this.onSelectedBg.emit(bg);
        // console.log("curBg", this.curBg);
    }

    getCurrentExtra(key: string) {
        if (this.curBg) {
            return this.curBg[key];
        }
        return false;
    }

    removeBg() {
        this.showTpl("none");
        this.onSelectedBg.emit({ class: "", type: "none", overlay: "none" });
    }

    showImage() {
        this.showTpl("image");
        this.setSrcImg();
        if (this.srcImg === null) {
            this.openLibImage();
        }
        this.showColorOverlay();
        this.applyImage();
    }

    showVideo() {
        this.showTpl("video");
    }

    showColorOverlay() {
        if (
            this.srcImg &&
            this.curBg &&
            (this.curBg.overlay === "color" ||
                this.curBg.overlay === "gradient")
        ) {
            this.displayer.colorOverlay = true;
        }
    }

    applyImage() {
        if (this.srcImg && this.srcImg !== "") {
            const opacity = this.curBg.opacity === 1 ? 0.5 : this.curBg.opacity;
            this.onSelectedBg.emit({
                img: { ...this.curBg.img, path: this.srcImg },
                type: "img",
                opacity,
            });
        }
    }

    applyFilter(filter: string) {
        // console.log("applyFilter", filter);
        const img = { path: this.srcImg, filter };
        this.onSelectedBg.emit({ img });
    }

    showRcptColor() {
        if (this.displayer.colorOverlay) {
            this.showTpl("");
            this.displayer.image = true;
            this.displayer.rcptsOverlay = true;
        } else {
            this.showTpl("");
        }

        this.displayer.rcptColor = true;
        this.rcptPickerColorProps.showPopover = true;
        this.applyColor(this.colorCustom);
        if (this.rcptColor) {
            this.rcptColor.binding();
        }
    }

    showRcptGradient() {
        if (this.displayer.colorOverlay) {
            this.showTpl("");
            this.displayer.image = true;
            this.displayer.rcptsOverlay = true;
        } else {
            this.showTpl("");
        }

        this.displayer.rcptGradient = true;
        this.rcptPickerColorProps.showPopover = true;
        this.cd.detectChanges(); // obliger pour que la vue soit a jour et que jquery intervienne

        // fix pour les anciennes sections
        if (this.gradientCustom.angle === "to bottom") {
            // this.gradientCustom.angle = "0deg";
        }
        // console.log("---->", this.gradientCustom);
        // console.log(parseInt(this.gradientCustom.angle, 10));
        $(".dial").val(parseInt(this.gradientCustom.angle, 10));

        if (!this.knobIsInit) {
            // this.knobIsInit = true;
            $(".dial").knob({
                width: 75,
                height: 75,
                step: 5,
                min: 0,
                max: 360,
                release: (v) => {
                    // console.log("release v", v);
                    this.gradientCustom.angle = `${v}deg`;
                    this.applyGradient(this.gradientCustom);
                },
                // change: v => {
                //     console.log("change v", v);
                // }
            });
        } else {
            //  .trigger() ..
            // $('.dial')
            // .val(27)
            // .trigger('change');
        }

        this.applyGradient(this.gradientCustom);
        if (this.rcptGradient) {
            this.rcptGradient.binding();
        }
    }

    openLibImage() {
        this.wantAddImg = true;
        this.onOpenLib.emit();
        this.modalMedia.open();
    }

    showColor(gradient?: string) {
        this.showTpl("color");
        this.getDefaultOverlay();
        const overlay = gradient || this.getDefaultOverlay();
        // console.log('this.curBg', this.curBg)
        this.onSelectedBg.emit({
            class: "",
            type: "color",
            overlay,
        });
    }

    goBack(from: string) {
        if (this.displayer.rcptsOverlay) {
            this.showImage();
            this.displayer.colorOverlay = true;
        } else {
            this.showColor(from);
        }
    }

    showTpl(tplName) {
        for (let key in this.displayer) {
            if (this.displayer.hasOwnProperty(key)) {
                this.displayer[key] = key === tplName;
            }
        }
    }

    applyColor(hex: string, opacity: number = 1) {
        const isInColorOverlay =
            this.displayer.rcptsOverlay || this.displayer.colorOverlay;
        const bg = {
            class: "",
            color: { hex },
            overlay: "color",
            type: isInColorOverlay ? "img" : "color",
            opacity,
        };
        // console.log('apply bg color', bg);
        this.onSelectedBg.emit(bg);
    }

    applyGradient(gradient: {
        angle;
        hex;
        hex2;
        opacity;
        opacity2;
        rgba;
        rgba2;
    }) {
        const { rgba, rgba2, ...rest } = gradient;
        const bg = {
            class: "",
            gradient: rest,
            overlay: "gradient",
            type:
                this.displayer.rcptsOverlay || this.displayer.colorOverlay
                    ? "img"
                    : "color",
        };
        this.onSelectedBg.emit(bg);
    }

    handleChange(color, isFirstPicker?) {
        // console.log("handleChange !!!", color, this.gradientCustom);
        if (this.displayer.rcptColor) {
            this.colorCustom = hexToRgb(color.hex, true, color.rgb.a);
            this.rcptPickerColorProps.color = color.hex;
            this.applyColor(color.hex, color.rgb.a);
        }
        if (this.displayer.rcptGradient) {
            const curGradient = this.getCurGradient();
            const gradient = {
                angle: curGradient.angle || "0deg",
                hex: isFirstPicker ? color.hex : curGradient.hex,
                hex2: isFirstPicker ? curGradient.hex2 : color.hex,
                opacity: isFirstPicker ? color.rgb.a : curGradient.opacity,
                opacity2: isFirstPicker ? curGradient.opacity2 : color.rgb.a,
            };
            // console.log("gradient", gradient);
            const rgba = hexToRgb(gradient.hex, true, gradient.opacity);
            const rgba2 = hexToRgb(
                gradient.hex2 || "transparent",
                true,
                gradient.opacity2
            );
            const rgbaData = hexToRgb(gradient.hex, false, gradient.opacity);
            const rgba2Data = hexToRgb(
                gradient.hex2 || "transparent",
                false,
                gradient.opacity2
            );

            this.rcptPickerColorPropsGradient1.color = rgbaData;
            this.rcptPickerColorPropsGradient2.color = rgba2Data;
            const newGradientCustom = { ...gradient, rgba, rgba2 };
            this.gradientCustom = newGradientCustom;
            // console.log("handleChange 22", curGradient, this.gradientCustom);
            this.applyGradient(newGradientCustom);
        }
    }

    handleClose() {
        // this.rcptPickerColorProps.showPopover = false;
    }

    getCurGradient() {
        if (!this.curBg) {
            this.curBg = {};
        }
        if (!this.curBg.gradient) {
            const { rgba, rgba2, ...rest } = this.gradientCustom;
            this.curBg.gradient = {
                ...rest,
            };
        }
        // console.log("this.curBg.gradient", this.curBg.gradient);
        // console.log("this.gradientCustom", this.gradientCustom);
        const { hex, hex2, opacity, opacity2 } = this.curBg.gradient;
        const rgba = hexToRgb(hex, true, opacity);
        const rgba2 = hexToRgb(hex2 || "transparent", true, opacity2);

        const rgbaData = hexToRgb(hex, false, opacity);
        const rgba2Data = hexToRgb(hex2 || "transparent", false, opacity2);

        this.rcptPickerColorPropsGradient1.color = rgbaData;
        this.rcptPickerColorPropsGradient2.color = rgba2Data;

        if (!this.curBg.gradient.hex2) {
            this.curBg.gradient.hex2 = "transparent";
        }
        if (
            !this.curBg.gradient.angle ||
            this.curBg.gradient.angle === "to bottom"
        ) {
            this.curBg.gradient.angle = "0deg";
        }

        return { ...this.curBg.gradient, rgba, rgba2 };
    }
    getCurColor() {
        if (!this.curBg) {
            this.curBg = {};
        }
        if (!this.curBg.color || !this.curBg.color.hex) {
            this.curBg.color = {
                hex: "#ffffff",
            };
            this.curBg.opacity = 1;
        }
        const {
            color: { hex },
            opacity,
        } = this.curBg;
        return hexToRgb(hex, true, opacity);
    }

    refreshBg() {
        this.gradientCustom = this.getCurGradient();
        this.colorCustom = this.getCurColor();
        this.setActiveMenu();
        this.showColorOverlay();
    }

    ngOnChanges(changes: SimpleChanges): void {
        // console.log("changes", changes);
        if (
            (changes.curBg && changes.curBg.previousValue === null) ||
            (changes.index &&
                changes.index.previousValue !== changes.index.currentValue)
        ) {
            this.showTpl("");
            this.srcImg = null;
            this.refreshBg();
        }
    }

    onShown() {
        if (this.imgLib) {
            this.imgLib.reset();
        }
    }

    closeModalMedia($event) {
        // console.log("closeModalMedia $event", $event);
        this.images = [$event.img];
        this.modalMedia.close();
        this.srcImg = this.images[0].srcOriginal || this.images[0].src;
        this.showColorOverlay();

        this.applyImage();
    }

    onHiddenLib() {
        this.wantAddImg = false;
        this.onCloseLib.emit();
        // if (this.srcImg === null) {
        //     this.setActiveMenu();
        // }
    }

    toggleOverlay($event) {
        this.displayer.colorOverlay = true;

        const overlay =
            this.curBg.overlay === "none" || !this.curBg.overlay
                ? this.getDefaultOverlay()
                : "none";
        const opacity = this.curBg.opacity !== 1 ? this.curBg.opacity : 0.5;
        this.onSelectedBg.emit({ overlay, opacity });
    }

    toggleFilters($event) {
        // console.log("toggleFilters");
        const img = {
            ...this.curBg.img,
            filter: this.curBg.img.filter ? "" : this.filters[0],
        };
        this.onSelectedBg.emit({ img });
    }

    toggleParallax($event) {
        this.onSelectedBg.emit({ parallax: !this.curBg.parallax });
    }

    getDefaultOverlay() {
        const hasColor = this.curBg.color && this.curBg.color.hex;
        const hasGradient = this.curBg.gradient && this.curBg.gradient.hex;
        const defaultOverlay = hasColor
            ? "color"
            : hasGradient
            ? "gradient"
            : "color";
        return defaultOverlay;
    }

    applyVideo() {
        if (this.formGrpVideo.valid) {
            const idVideo = this.getVideoIdYoutube(
                this.formGrpVideo.value.link
            );
            const src = this.formGrpVideo.value.link;
            const bg = {
                class: "",
                type: "video",
                video: {
                    src,
                    idVideo,
                },
            };
            this.onSelectedBg.emit(bg);
        }
    }
}



