import {
    Component,
    ElementRef,
    HostListener,
    HostBinding,
    Input,
    OnInit,
    EventEmitter,
    Renderer2
} from "@angular/core";

export const defaultOptionsTooltip = {
    placement: "top",
    delay: 0,
    "show-delay": 0,
    "hide-delay": 300,
    "hide-delay-mobile": 1500,
    "z-index": 0,
    "animation-duration": 300,
    "animation-duration-default": 300,
    trigger: "hover",
    "tooltip-class": "",
    display: true,
    "display-mobile": true,
    shadow: true,
    theme: "dark",
    offset: 8,
    "max-width": "",
    id: false
};

@Component({
    selector: "tooltip",
    templateUrl: "./tooltip.component.html",
    host: { class: "tooltip" },
    styleUrls: ["./tooltip.component.scss"]
})
export class TooltipComponent implements OnInit {
    _show: boolean = false;

    /* tslint:disable:no-input-rename */

    @Input() data: any;

    /* tslint:enable */

    events = new EventEmitter();

    @HostBinding("style.top") hostStyleTop: string;
    @HostBinding("style.left") hostStyleLeft: string;
    @HostBinding("style.z-index") hostStyleZIndex: number;
    @HostBinding("style.transition") hostStyleTransition: string;
    @HostBinding("style.max-width") hostStyleMaxWidth: string;
    @HostBinding("class.tooltip-show") hostClassShow: boolean;
    @HostBinding("class.tooltip-shadow") hostClassShadow: boolean;
    @HostBinding("class.tooltip-light") hostClassLight: boolean;

    @HostListener("transitionend", ["$event"])
    transitionEnd(event) {
        if (this.show) {
            this.events.emit("shown");
        }
    }

    @Input()
    set show(value: boolean) {
        if (value) {
            this.setPosition();
        }
        this._show = this.hostClassShow = value;
    }
    get show(): boolean {
        return this._show;
    }

    get placement() {
        return this.data.options.placement;
    }

    get element() {
        return this.data.element;
    }

    get elementPosition() {
        return this.data.elementPosition;
    }

    get options() {
        return this.data.options;
    }

    get value() {
        return this.data.value;
    }

    get tooltipOffset(): number {
        return Number(this.data.options.offset);
    }

    get isThemeLight() {
        return this.options["theme"] === "light";
    }

    constructor(private elementRef: ElementRef, private renderer: Renderer2) {}

    ngOnInit() {
        this.setPlacementClass();
        this.setZIndex();
        this.setCustomClass();
        this.setAnimationDuration();
        this.setStyles();
    }

    setPosition(): void {
        const elementHeight = this.element.offsetHeight;
        const elementWidth = this.element.offsetWidth;
        const tooltipHeight = this.elementRef.nativeElement.clientHeight;
        const tooltipWidth = this.elementRef.nativeElement.offsetWidth;
        const scrollY = window.pageYOffset;
        const tooltip = this.elementRef.nativeElement;

        if (this.placement === "top") {
            this.hostStyleTop =
                this.elementPosition.top +
                scrollY -
                (tooltipHeight + this.tooltipOffset) +
                "px";
        }

        if (this.placement === "bottom") {
            this.hostStyleTop =
                this.elementPosition.top +
                scrollY +
                elementHeight +
                this.tooltipOffset +
                "px";
        }

        if (this.placement === "top" || this.placement === "bottom") {
            this.hostStyleLeft =
                this.elementPosition.left +
                elementWidth / 2 -
                tooltipWidth / 2 +
                "px";
        }

        if (this.placement === "left") {
            this.hostStyleLeft =
                this.elementPosition.left -
                tooltipWidth -
                this.tooltipOffset +
                "px";
        }

        if (this.placement === "right") {
            this.hostStyleLeft =
                this.elementPosition.left +
                elementWidth +
                this.tooltipOffset +
                "px";
        }

        if (this.placement === "left" || this.placement === "right") {
            this.hostStyleTop =
                this.elementPosition.top +
                scrollY +
                elementHeight / 2 -
                tooltip.clientHeight / 2 +
                "px";
        }
    }

    setPlacementClass(): void {
        this.renderer.addClass(
            this.elementRef.nativeElement,
            "tooltip-" + this.placement
        );
    }

    setZIndex(): void {
        if (this.options["z-index"] !== 0) {
            this.hostStyleZIndex = this.options["z-index"];
        }
    }

    setCustomClass() {
        if (this.options["tooltip-class"]) {
            this.renderer.addClass(
                this.elementRef.nativeElement,
                this.options["tooltip-class"]
            );
        }
    }

    setAnimationDuration() {
        if (
            Number(this.options["animation-duration"]) !=
            this.options["animation-duration-default"]
        ) {
            this.hostStyleTransition =
                "opacity " + this.options["animation-duration"] + "ms";
        }
    }

    setStyles() {
        this.hostClassShadow = this.options["shadow"];
        this.hostClassLight = this.isThemeLight;
        this.hostStyleMaxWidth = this.options["max-width"];
    }
}



