import { Injectable } from "@angular/core";

import { Observable } from "rxjs/Observable";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

import "rxjs/add/operator/publish";
import { Subject } from "rxjs/Subject";
import { Router, NavigationEnd } from "@angular/router";
import { filter } from "rxjs/operators";

@Injectable()
export class NotificationService {
    // private _notification: BehaviorSubject<string> = new BehaviorSubject(null);
    // readonly notification$: Observable<string> = this._notification.asObservable().publish().refCount();
    notification = new Subject<any>();
    counter: number = 0;

    notifications: { id: number; message: string; type: string }[] = [];

    constructor(private router: Router) {
        // On supprime toute les notifs apres un changement de route
        // bien sur les nouvelles notifs apparaisent qu'apres la suppression
        router.events.pipe(filter(event => event instanceof NavigationEnd)).subscribe((event: NavigationEnd) => {
            if (this.notifications.length > 0) {
                this.reset();
            }
        });
    }

    notify(message, id?: any, type?: string) {
        this.notification.next(message);
        this.add(message, id, type);
        // setTimeout(() => this._notification.next(null), 3000);
    }

    add(message: string, id?: any, type?: string) {
        const notif = this.notifications.find(n => n.message === message);
        if (!notif) {
            this.notifications.push({ id: id || this.counter++, message, type: type || "warning" });
        }
    }

    remove(id: any) {
        // remove modal from array of active modals
        this.notifications = this.notifications.filter(x => x.id !== id);
    }

    reset() {
        this.notifications = [];
    }
}



