import { Injectable, Inject, Optional } from "@angular/core";
import {
    Site,
    SitesService,
    DesignConfig,
    MenuItemType,
    ModulesService
} from "../../api";
import { fixHex } from "../utils/utils";
import { CONFIG_MODULE, CfgModule } from "../classes/CfgModule";
import { ScriptService } from "./script.service";
import { Scripts } from "../store/script.store";
import { Subject } from "rxjs/Subject";
import { ShapeButton } from "../utils/enums";
import { AnimationSection, AnimationStore } from "../store/animation.store";

@Injectable()
export class StoreService {
    blogId = null;
    shopId = null;
    paiementId = null;
    memberId = null;
    formationId = null;
    filesId = null;
    notificationId = null;
    galleryId = null;
    formId = null;

    sites: Site[] = [];
    curSite: Site;
    curMenu: any = {};
    curThemeUser = null;
    assetsSite = [];
    PLACEHOLDER_IMG = "https://mediacache.epicred.fr/4X6DE68rOO9oeeAGN6wlzbLgPTU=/1920x/https://media.epicred.fr/media/cache/original/all/5c1901e87c038.png";

    // theme couleurs bootstrap
    themeColors = [
        {
            label: "Bg Primary",
            value: "",
            className: "bg-primary"
        },
        {
            label: "Bg Secondary",
            value: "",
            className: "bg-secondary"
        },
        {
            label: "Bg Success",
            value: "",
            className: "bg-success"
        },
        { label: "Bg Info", value: "", className: "bg-info" },
        {
            label: "Bg white",
            className: "bg-white"
        },
        { label: "Bg Black", value: "", className: "bg-black" }
        // { label: "Bg Dark", value: "", className: "bg-dark" }
    ];

    widthSidebar = 322;
    styleSidebar: string = `width: ${this.widthSidebar}px; transition: left .5s;`;

    footerPersoHeight: string = "0px;";

    styleMain: string = `margin-left:  ${this.widthSidebar}px; transition: margin-left .5s; margin-bottom: ${this.footerPersoHeight}`;
    styleNavbar: string = `margin-left:  ${this.widthSidebar}px; transition: margin-left .5s;`;
    sidebarIsHidden = false;

    // si on tente de choper le innerwidth du parent -> probleme cross-origin
    smallDevice = false;

    loadingWidthSidebar = this.smallDevice
        ? "width: 50px;"
        : `width: ${this.widthSidebar}px;`;

    initAdmin: boolean;

    openedCustomizer: boolean = false;

    // design module type
    designModules: DesignConfig = {};

    design_config: {
        id: string;
        config: {
            // [key: string]: any;
            config?: {
                // cfg theme bootstrap
                variables: {
                    primary: string;
                    secondary: string;
                    success: string;
                    info: string;
                };
                theme: string;
            };
            layout?: { id: number; name: string };
            fonts?: {
                "headings-font-family": string;
                "paragraphe-font-family": string;
                "web-font-path": string;
                name: string;
            };
            sizesText?: {
                label: string;
                selectors: { for: string; val: string }[];
            };
            baseColor?: string;
            idxPalette?: number;
            btn?: {
                shape?: {
                    type: ShapeButton;
                    css: string;
                };
            };
            animSection?: {
                label: string;
                effect: string;
            };
        };
        type: "site";
    } = null;

    modules = [
        {
            id: 2, // id du module dans api
            name: "shop",
            lastPageReached: 0,
            pages: [], // pages du module de l'user
            stopLoadItems: false,
            inLoading: false
        },
        {
            id: 1,
            name: "blog",
            lastPageReached: 0,
            pages: [],
            stopLoadItems: false,
            inLoading: false
        }
    ];

    groups = [];

    actions$ = new Subject<{ name: string; payload?: any }>();

    curAnimationSection: AnimationSection = AnimationStore[0];

    constructor(
        private siteApi: SitesService,
        private scriptService: ScriptService,
        private moduleApi: ModulesService // @Optional() @Inject(CONFIG_MODULE) private config: CfgModule
    ) {
        this.checkIfSmallDevice();
    }


    /**
     * Retourne un uid de module par nom
     */
    getUIDByName(nomModule){
        if(!this.curSite){
            return false;
        } 
        let uids = this.curSite.uids;
        let sUid = uids.find(d=> d.nom == nomModule);
        return sUid;
    }

    /**
     * Retourne le domaine principal du site en cours
     */
    getMainDomain(){
        if(!this.curSite){
            return false;
        } 
        let domains = this.curSite.domains;
        let mainDomain = domains.find(d=> d.main == true);
        return mainDomain;
     }


    /**
     * Retourne le logo du site courant pour les factures
     */
    getLogoSite(){

        if(!this.curSite){
            return false;
        } 

        let logos = this.curSite.logo;
        if(logos != null){
            logos = JSON.parse(logos);
            return logos['320'];

        }

        return false;

     }     
 
     /**
      * Retourne le plan principal du site en cours
      */
     getMainPlanName(){
         if(!this.curSite){
             return false;
         } 
     
          return this.curSite.plan.name;
      }


    setSites(sites) {
        this.sites = sites;
    }

    getSites() {
        return this.sites;
    }

    openCustomizer() {
        setTimeout(() => {
            const $nav = document.querySelector("#nav-section-editor");
            if ($nav) {
                const val = $nav["offsetHeight"] + "px;";
                // console.log("val", val);
                this.updateMarginBotMain(val);
                // console.log("---->", this.styleMain);
            }
        }, 250);
        // this.store.styleMain
        this.openedCustomizer = true;
    }

    closeCustomizer() {
        this.updateMarginBotMain(0);
        this.openedCustomizer = false;
    }

    toggleSidebar() {
        if (this.sidebarIsHidden) {
            this.styleSidebar = this.styleSidebar.replace(
                "left: -272px;height: 54px;",
                ""
            );
            this.styleMain = this.styleMain.replace(
                "margin-left: 0px;",
                `margin-left:  ${this.widthSidebar}px;`
            );
            this.styleNavbar = this.styleNavbar.replace(
                "margin-left: 50px;",
                `margin-left:  ${this.widthSidebar}px;`
            );
            this.sidebarIsHidden = false;
            // this.activeItemMenu = this.currentCptSidebar;
        } else {
            this.styleSidebar += "left: -272px;height: 54px;";
            this.styleMain = this.styleMain.replace(
                `margin-left:  ${this.widthSidebar}px;`,
                "margin-left: 0px;"
            );
            this.styleNavbar = this.styleNavbar.replace(
                `margin-left:  ${this.widthSidebar}px;`,
                "margin-left: 50px;"
            );
            this.sidebarIsHidden = true;
            // this.activeItemMenu = "";
        }
    }

    checkIfSmallDevice() {
        this.smallDevice = (window && window.innerWidth <= 991) || false;
        // console.log("small device", this.smallDevice);
        // if (this.smallDevice) {
        //     this.toggleSidebar();
        // }

        if (this.smallDevice && !this.sidebarIsHidden) {
            this.toggleSidebar();
        } else if (!this.smallDevice && this.sidebarIsHidden) {
            this.toggleSidebar();
        }
    }

    updateMarginBotMain(value) {
        this.styleMain = this.styleMain.replace(
            `margin-bottom: ${this.footerPersoHeight}`,
            `margin-bottom: ${value}`
        );

        this.footerPersoHeight = value;
    }

    // Recupere la palette de l'user (le theme de l'user doit etre present dans la page)
    // donc la fonction doit etre appeller apres le chargement de celle ci
    getScheme() {
        // color surcharg plus bas
        const presetUser = [
            { color: "var(--primary)", title: "Couleur primaire" },
            { color: "var(--secondary)", title: "Couleur secondaire" },
            { color: "var(--success)", title: "Couleur tertiaire" },
            { color: "var(--info)", title: "Couleur quaternaire" }
        ];

        const bodyStyles = getComputedStyle(document.body);
        // bug sur les feuilles css perso, il y a un espace au debut des variables
        const hexPrimary = fixHex(
            bodyStyles.getPropertyValue("--primary").trim()
        );
        const hexSecondary = fixHex(
            bodyStyles.getPropertyValue("--secondary").trim()
        );
        const hexSuccess = fixHex(
            bodyStyles.getPropertyValue("--success").trim()
        );
        const hexInfo = fixHex(bodyStyles.getPropertyValue("--info").trim());

        const colors = [
            "#FFFFFF",
            hexPrimary,
            hexSecondary,
            hexSuccess,
            hexInfo
        ];

        presetUser[0].color = hexPrimary;
        presetUser[1].color = hexSecondary;
        presetUser[2].color = hexSuccess;
        presetUser[3].color = hexInfo;

        return { colors, presetUser };
    }

    getPagesModule(name) {
        // console.log("getPagesModule -->", name);

        const site = this.curSite;
        const module = this.modules.find(m => m.name === name);
        // console.log("module -->", module);
        if (module.inLoading || module.stopLoadItems) {
            this.sendPropsPagesModules(module);
            return;
        }
        module.inLoading = true;
        // console.log("request start");
        this.moduleApi
            .apiSitesSlugModulesModulePagesGet(
                String(site.id),
                String(module.id),
                ++module.lastPageReached
            )
            .subscribe(paginator => {
                // console.log("paginator menu items", paginator);
                module.pages = [...module.pages, ...paginator["items"]];
                // console.log("request end, module.pages", module.pages);
                if (module.pages.length === paginator["totalItems"]) {
                    module.stopLoadItems = true;
                }
                this.sendPropsPagesModules(module);

                module.inLoading = false;
            });
    }

    sendPropsPagesModules(module) {
        window["ListenerEditor$"].next({
            action: "want_pages_modules",
            data: {
                nameModule: module.name,
                propsMenu: {
                    stopLoadItems: module.stopLoadItems,
                    treeData: this.pagesToMenuItems(module.pages),
                    onAction: data => {
                        // console.log("onAction from react sortable modules", data.action);
                        if (data.action === "loadMenuItems") {
                            this.getPagesModule(module.name);
                        }
                    },
                    currentPage: module.lastPageReached
                }
            }
        });
    }

    pagesToMenuItems(data) {
        const menuItems: MenuItemType[] = [];
        for (let i = 0; i < data.length; i++) {
            const page = data[i];
            const node = {
                page,
                title: page.nom,
                slug: page.slug,
                url: page.url
            };
            menuItems[i] = node;
        }
        return menuItems;
    }

    setDefaultLayout() {
        if (typeof this.design_config.config.layout === "undefined") {
            this.design_config.config.layout = { id: 1, name: "Mep 100%" };
        }
    }

    loadFont() {
        // console.log("loadFont");
        // Setter par main.component.ts de Editor

        if (
            this.design_config &&
            typeof this.design_config.config.fonts !== "undefined"
        ) {
            // this.removeFont();
            const scriptFontCss: Scripts = {
                id: "font-select-css",
                name: this.design_config.config.fonts.name,
                src: this.design_config.config.fonts["web-font-path"]
            };
            this.scriptService.addScript(scriptFontCss);
            if (scriptFontCss.src) {
                this.scriptService
                    .loadScript("css", this.design_config.config.fonts.name)
                    .then((v: any) => {
                        this.insertFontsStyle();
                    });
            } else {
                this.insertFontsStyle();
            }
        } else {
            this.setDefaultFonts();
        }
    }

    /**
     * Insert or update fonts style in DOM
     */
    insertFontsStyle() {
        const sizesText = this.design_config.config.sizesText;
        let sizesTextStyle = "";
        if (sizesText) {
            sizesText.selectors.forEach(el => {
                if (el.for === "p") {
                    sizesTextStyle += `
                    .wrapper-fake-browser div[data-block="true"]{
                        font-size: ${el.val};
                    }
                    .page-preview div[data-block="true"]{
                        font-size: ${el.val};
                    }
                    `;
                }
                sizesTextStyle += `
                            .wrapper-fake-browser ${el.for}{
                                font-size: ${el.val};
                            }
                            `;
            });
        }
        const contentStyle = ` h1,h2,h3,h4, .navbar-brand, .nav-link:not(i) {
                            font-family : ${this.design_config.config.fonts["headings-font-family"]};
                        }
                        .active-fake-browser p, .active-fake-browser div[data-block="true"] {
                            font-family : ${this.design_config.config.fonts["paragraphe-font-family"]};
                        }
                        .not-active-fake-browser p, .not-active-fake-browser div[data-block="true"] {
                            font-family : ${this.design_config.config.fonts["paragraphe-font-family"]};
                        }
                        .page-preview p, .page-preview div[data-block="true"] {
                            font-family : ${this.design_config.config.fonts["paragraphe-font-family"]};
                        }
                        ${sizesTextStyle}
                        `;
        const elScriptCustomizeFont = document.getElementById("customize_font");
        if (elScriptCustomizeFont) {
            elScriptCustomizeFont.innerHTML = contentStyle;
        } else {
            window["$"]("head").append(
                `<style id="customize_font">${contentStyle}</style>`
            );
        }
    }

    setDefaultFonts() {
        if (this.design_config && !this.design_config.config.fonts) {
            this.design_config.config.fonts = {
                "headings-font-family":
                    '-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji"',
                "paragraphe-font-family":
                    '-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji"',
                "web-font-path": "",
                name: "Default"
            };
        }
    }

    removeFont() {
        window["$"]("#customize_font").remove();
    }

    updateThemeSite() {
        window["__THEME_SITE"] = this.design_config.config;
        if (this.design_config.config.animSection) {
            this.curAnimationSection =
                AnimationStore.find(
                    anim =>
                        anim.effect ===
                        this.design_config.config.animSection.effect
                ) || AnimationStore[0];
        } else {
            this.curAnimationSection = AnimationStore[0];
        }
        // todo update react cpt
    }
}



