import {
    Component,
    OnInit,
    Input,
    ViewChild,
    EventEmitter,
    Output,
    ViewChildren,
    QueryList,
    ChangeDetectorRef
} from "@angular/core";

// import { DraggableDirective, DroppableDirective, DragHandleDirective } from '@swimlane/ngx-dnd';
import { MenuItem } from "../../../shared/classes/menu";
import { ReactCptComponent } from "../../../shared/components/react-cpt/react-cpt.component";
import { ScriptService } from "../../../shared/services/script.service";
import { StoreService } from "../../services/store.service";

let nextIdMenu = 0;
declare var $: any;
declare var SortableTreeEditorRcpt: any;

@Component({
    selector: "app-menu",
    templateUrl: "./menu.component.html",
    styleUrls: ["./menu.component.scss"],
    host: { style: "display: flex; flex-direction: column;    height: 100%;" }
})
export class MenuComponent implements OnInit {
    @Input() tplInput = false;
    @Input() idMenu: string | number = `app-menu-${nextIdMenu++}`;

    /**
     * maximum d'item dans un dropdown
     */
    @Input() MAX_ITEM_DROPDOWN: number = 10;

    /**
     * les items afficher dans le menu
     */
    @Input() items: MenuItem[];

    /**
     * Style additionnel a appliquer au menu
     */
    @Input() style: any;

    /**
     * Classes additionnel a appliquer au menu
     */
    @Input() className: string;

    /**
     * la barre de recherche du menu
     */
    @Input() searchBar: any = { active: false };

    /**
     * Props du component react sortable tree
     */
    @Input() STEprops: any = {};

    /**
     * indique si le menu est de type DnD
     */
    @Input() menuSortable: boolean = false;

    /**
     * Input pour ajouter un nouvel item page
     */
    @Input() inputNewItem: any = {
        name: "new_item_menu",
        type: "input",
        placeholder: "Item sans titre",
        className: "we-form-control we-bg-transparent we-caret-light",
        action: "set_name_new_item",
        actionBtnR: "update",
        val: "test",
        focus: true,
        iconR: {
            active: true,
            className: "we-btn-icon fal fa-check we-text-success"
        },
        active: false,
        groupClassNameR: "we-input-group-addon we-bg-transparent we-border-0"
    };

    /**
     * eventEmitter Action
     */
    @Output() onAction: EventEmitter<any> = new EventEmitter();

    /**
     * component React de type Sortable Tree
     */
    @ViewChild("menuST") menuST: ReactCptComponent;

    /**
     * la librairie react sortable tree
     */
    STEditor: any;

    constructor(
        private cd: ChangeDetectorRef,
        private scriptService: ScriptService,
        private store: StoreService
    ) {
        if (this.menuSortable) {
            this.scriptService
                .loadScript("css", "SortableTreeEditorCss")
                .then(data => {
                    this.scriptService
                        .loadScript("js", "SortableTreeEditorRcpt")
                        .then(data => {
                            this.STEditor = SortableTreeEditorRcpt.default;
                        })
                        .catch(error => console.log(error));
                });
            // console.log("menu subscribe to ListenerReact$");
            window["ListenerReact$"].subscribe(e => {
                // console.log("listenerReact$ in menu", e);
                if (e.app !== "editor") {
                    return;
                }
                if (e.action === "want_pages") {
                    // console.log("send props pages");
                    window["ListenerEditor$"].next({
                        action: e.action,
                        data: this.STEprops
                    });
                }
            });
        }
    }

    ngOnInit() {
        if (this.menuSortable) {
            this.STEprops.treeData = this.items;
            this.STEprops.maxSubItems = 10;
            this.STEprops.showBtnCollapse = true;
            this.STEprops.namePlanSite = this.store.curSite.plan.name;
            this.scriptService
                .loadScript("css", "SortableTreeEditorCss")
                .then(data => {
                    this.scriptService
                        .loadScript("js", "SortableTreeEditorRcpt")
                        .then(data => {
                            this.STEditor = SortableTreeEditorRcpt.default;
                        })
                        .catch(error => console.log(error));
                });
        }
    }

    /**
     * envoie toutes les actions recue au parent
     */
    handleAction($event): void {
        this.onAction.emit($event);
    }

    clickedInside($event: Event): void {
        $event.preventDefault();
        $event.stopPropagation(); // <- that will stop propagation on lower layers
    }

    /**
     * met a jour le component react sortable tree
     */
    refresh() {
        if (this.menuSortable) {
            this.menuST.binding();
        }
    }

    reRender() {
        if (this.menuSortable) {
            this.menuST.reRender();
        }
    }
}



