import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Subject } from 'rxjs/Subject';

@Component({
  selector: 'app-text-editable',
  templateUrl: './text-editable.component.html',
  styleUrls: ['./text-editable.component.scss']
})
export class TextEditableComponent implements OnInit {

  @Input() text: string;
  @Input() placeholder: string = 'Insrez votre texte ici...';
  @Input() showPlaceholder: boolean = false;
  @Input() debounceTime: number = 1000;
  @Output() public onEditorKeyup = new EventEmitter();
  @Output() public onRemove = new EventEmitter();
  @Output() public onDelete = new EventEmitter();
  debouncer: Subject<string> = new Subject<string>();


  constructor() {
    this.debouncer
      .debounceTime(this.debounceTime)
      .subscribe(($event) => this.onEditorKeyup.emit($event));
  }

  ngOnInit() {

  }

  _onEditorKeyup($event) {
    this.debouncer.next($event);
  }
  _onRemove($event) {
    this.onRemove.emit($event);
    this.showPlaceholder = this.text.length === 0;
  }
  _onDelete($event) {
    this.onDelete.emit($event);
  }

  editText() {
    this.text = this.placeholder;
    this.showPlaceholder = false;
  }

}



