import { NgModule, ModuleWithProviders, SkipSelf, Optional } from '@angular/core';
import { Configuration } from './configuration';
import { HttpClient } from '@angular/common/http';


import { CategoriesTemplatesService } from './api/categoriesTemplates.service';
import { DesignsService } from './api/designs.service';
import { DesignsModuleService } from './api/designsModule.service';
import { EditorsService } from './api/editors.service';
import { MenusService } from './api/menus.service';
import { ModulesService } from './api/modules.service';
import { NddsService } from './api/ndds.service';
import { PagesService } from './api/pages.service';
import { PlansService } from './api/plans.service';
import { PublicService } from './api/public.service';
import { SitesService } from './api/sites.service';
import { VersionsService } from './api/versions.service';

@NgModule({
  imports:      [],
  declarations: [],
  exports:      [],
  providers: [
    CategoriesTemplatesService,
    DesignsService,
    DesignsModuleService,
    EditorsService,
    MenusService,
    ModulesService,
    NddsService,
    PagesService,
    PlansService,
    PublicService,
    SitesService,
    VersionsService ]
})
export class ApiModule {
    public static forRoot(configurationFactory: () => Configuration): ModuleWithProviders {
        return {
            ngModule: ApiModule,
            providers: [ { provide: Configuration, useFactory: configurationFactory } ]
        };
    }

    constructor( @Optional() @SkipSelf() parentModule: ApiModule,
                 @Optional() http: HttpClient) {
        if (parentModule) {
            throw new Error('ApiModule is already loaded. Import in your base AppModule only.');
        }
        if (!http) {
            throw new Error('You need to import the HttpClientModule in your AppModule! \n' +
            'See also https://github.com/angular/angular/issues/20575');
        }
    }
}



