import { Component, OnInit, Input } from '@angular/core';
import { GridLayout } from '../../classes/GridLayout';

@Component({
  selector: 'app-vertical-pills',
  templateUrl: './vertical-pills.component.html',
  styleUrls: ['./vertical-pills.component.scss']
})
export class VerticalPillsComponent implements OnInit {

  @Input() grid: { colL: GridLayout, colR: GridLayout } =
    {
      colL: new GridLayout({ xs: 'we-col-3', sm: '', md: '', lg: '', xl: '' }),
      colR: new GridLayout({ xs: 'we-col-9', sm: '', md: '', lg: '', xl: '' })
    };

  @Input() styl: { colL: {}, colR: {} } =
    {
      colL: {},
      colR: {}
    };

  constructor() { }

  ngOnInit() {
  }

}



