import {
    Component,
    OnInit,
    Input,
    OnDestroy,
    ElementRef,
    Output,
    EventEmitter,
} from "@angular/core";
import { ReactService } from "../../services/react.service";
import { Subscription } from "rxjs/Subscription";

declare var React: any;
declare var ReactDOM: any;
let nextIdRcpt = 0;

/**
 * Component qui insere dans le dom un react component
 */
@Component({
    selector: "app-react-cpt",
    template: "",
    styleUrls: ["./react-cpt.component.scss"],
})
export class ReactCptComponent implements OnInit, OnDestroy {
    elementRef: any;
    @Input() component: object;
    @Input() cId;
    @Input() app;
    @Input() props;
    @Output() getInstance: EventEmitter<any> = new EventEmitter();
    cptInstance;

    refreshAll$: Subscription;
    refresh$: Subscription;

    constructor(element: ElementRef, private reactService: ReactService) {
        this.elementRef = element.nativeElement;
    }

    getData() {
        return this.cptInstance;
    }

    ngOnInit() {
        this.binding(false, true);
        this.refreshAll$ = this.reactService.refreshAll.subscribe(($event) => {
            this.binding();
        });

        this.refresh$ = this.reactService.refresh.subscribe(($event) => {
            /**
             * Si on a $event.app On reRender tous les flux d'une application prsent sur la page (meme si on a $event.id)
             * Si on a $event.id On reRender juste la section concern
             */
            if ($event.app && this.app === $event.app) {
                this.reRender();
            } else if ($event.id && this.cId === $event.id) {
                this.reRender();
            } else if ($event.app === "all") {
                this.reRender();
            }
        });
    }

    /**
     *
     */
    binding(forceProps?, inCollection?) {
        if (typeof this.component !== "undefined" && this.component !== null) {
            const props = forceProps || this.props;
            const el = React.createElement(this.component, props);
            const rcpt = ReactDOM.render(el, this.elementRef);
            this.cptInstance = rcpt;
            if (inCollection && this.cId) {
                this.reactService.components.push({
                    id: this.cId,
                    rcpt,
                });
                this.reactService.onLoadedRcpt.next({ cId: this.cId });
            }
        }
    }

    ngOnDestroy(): void {
        if (this.refreshAll$) {
            this.refreshAll$.unsubscribe();
        }
        if (this.refresh$) {
            this.refresh$.unsubscribe();
        }

        this.removeRcpt();
    }

    setComponent(c) {
        this.component = c;
    }

    removeRcpt() {
        ReactDOM.unmountComponentAtNode(this.elementRef);
    }

    /**
     * Supprimer le component React et le ressussite
     * utile quand on as besoin de componentDidMount etc...
     */
    reRender() {
        this.removeRcpt();
        this.binding();
    }
}



