import {
    Component,
    OnInit,
    Input,
    OnChanges,
    SimpleChanges,
    Output,
    EventEmitter,
    ElementRef
} from "@angular/core";
import { Site } from "../../../api";
import { PermissionService, PLAN } from "../../services/permission.service";

/**
 * USAGE
 *
 <app-permission [for]="['FREE', 'PRO']">
            <div authorized>
                Cette div sera montr a ceux ayant le role dans la variable For
            </div>
            <div unauthorized>
                Cette div sera montr a ceux n'ayant pas le role requis
            </div>
</app-permission>
 */

@Component({
    selector: "app-permission",
    templateUrl: "./permission.component.html",
    styleUrls: ["./permission.component.scss"]
})
export class PermissionComponent implements OnInit, OnChanges {
    // Soit for soit except pour les plan !
    @Input() for: PLAN | PLAN[] = null;
    @Input() except: PLAN = null;
    @Input() site: Site = null;
    // fait un 'display:none;' sur le component
    @Input() autoHide = false;
    @Output()
    authorized: EventEmitter<any> = new EventEmitter();
    @Output()
    unauthorized: EventEmitter<any> = new EventEmitter();

    IsAuthorized = false;

    constructor(
        public el: ElementRef,
        private permService: PermissionService
    ) {}

    ngOnInit() {}

    ngOnChanges(changes: SimpleChanges): void {
        this.setAuthorization();
    }

    setAuthorization() {
        this.IsAuthorized = this.permService.IsAuthorized(
            this.for,
            this.except,
            this.site
        );

        if (this.IsAuthorized) {
            this.authorized.emit();
        } else {
            this.unauthorized.emit();
            if (this.autoHide) {
                this.el.nativeElement.style.display = "none";
            }
        }
    }
}



