import {
    Component,
    OnInit,
    Output,
    EventEmitter,
    Input,
    OnDestroy,
    TemplateRef
} from "@angular/core";
import { PanelOptionsAction, MenuItem } from "../../classes/menu";

@Component({
    selector: "app-panel-options",
    templateUrl: "./panel-options.component.html",
    styleUrls: ["./panel-options.component.scss"]
})
export class PanelOptionsComponent implements OnInit, OnDestroy {
    @Input() id: string;

    @Input() isCollapse: boolean = false;

    @Input() inline: boolean = false;

    @Input() type: string = "";

    @Input() options: PanelOptionsAction[] = [];

    @Input() parentDOM: string;

    @Input() className: string = "we-small";

    @Input() tplRef: TemplateRef<any>;

    @Input() item: MenuItem;

    @Output() onClick: EventEmitter<any> = new EventEmitter();

    @Output() onValueChange: EventEmitter<any> = new EventEmitter();

    @Output() onDestroy: EventEmitter<any> = new EventEmitter();

    hasTplOutlet: boolean = false;

    constructor() {}

    ngOnInit() {
        if (this.tplRef) {
            this.hasTplOutlet = true;
        }
    }

    ngOnDestroy(): void {
        this.onDestroy.emit();
    }
}



