import {
    Component,
    OnInit,
    Input,
    ViewChild,
    ElementRef,
    AfterViewInit,
    OnDestroy,
    Output,
    EventEmitter
} from "@angular/core";
import { ModalService } from "../../services/modal.service";

let nextIdModal = 0;

declare var $: any;

@Component({
    selector: "app-modal",
    templateUrl: "./modal.component.html",
    styleUrls: ["./modal.component.scss"]
})
export class ModalComponent implements OnInit, AfterViewInit, OnDestroy {
    @Input()
    idModal: string | number = `app-modal-${nextIdModal++}`;

    @Input()
    title: string;

    @Input()
    conf: any;

    @Input()
    zIndex: any;    

    @Input()
    isOpen: boolean = false;

    @Input()
    sizeModal: "" | "lg" | "xl" | "fullscreen" = "";

    @Input()
    className: string = "";

    @Input()
    keyboard: boolean = true;

    @Input()
    backdrop: boolean | "static" = true;

    @Input()
    btnClose: boolean = true;

    @Input()
    header: boolean = true;

    @Input()
    isDraggable: boolean = false;

    @Input()
    styleWrapper: object = {};

    @Input()
    style: object = {};

    @Input()
    modalFor: string = undefined;

    @Input()
    bodyCss: string = "";

    @Input()
    restrictCss: boolean = true;

    @Input()
    scrollable: boolean = false;

    @ViewChild("modalDOM")
    modalDOM: ElementRef;

    // This event is fired when the modal has finished being hidden from the user (will wait for CSS transitions to complete).
    @Output()
    onHidden: EventEmitter<any> = new EventEmitter();

    // This event is fired immediately when the hide instance method has been called.
    @Output()
    onHide: EventEmitter<any> = new EventEmitter();

    // This event fires immediately when the show instance method is called. If caused by a click, the clicked element is available as the relatedTarget property of the event.
    @Output()
    onShow: EventEmitter<any> = new EventEmitter();

    // This event is fired when the modal has been made visible to the user (will wait for CSS transitions to complete). If caused by a click, the clicked element is available as the relatedTarget property of the event.
    @Output()
    onShown: EventEmitter<any> = new EventEmitter();

    useBodyHtml: boolean = false;

    bodyHtml: string = "";

    footerIsHide: boolean = false;

    // _conf: any = {
    //   btnClose: true,
    // };

    constructor(private modalService: ModalService) {}

    ngOnInit() {
        this.modalService.add(this);
    }

    ngAfterViewInit(): void {
        document.body.appendChild(this.modalDOM.nativeElement);
        this.initOnHide();
        this.initOnHidden();
        this.initOnShow();
        this.initOnShown();
        if (this.isOpen) {
            this.open();
        }
        if (this.isDraggable) {
            this.initDraggable();
        }
    }

    ngOnDestroy(): void {
        // this.modalService.remove(this.id);
        this.modalService.close(this.idModal);
        this.modalDOM.nativeElement.remove();
    }

    initOnHide() {
        $("#" + this.idModal).on("hide.bs.modal", e => {
            this.onHide.emit();
        });
    }

    initOnHidden() {
        $("#" + this.idModal).on("hidden.bs.modal", e => {
            if (this.isDraggable) {
                window["$"]("body").css({
                    "overflow-y": ""
                });
                window["$"]("body").removeClass("we-pr-0");
            }
            // parfois il y a la double scrollbar, alors on remet a l'etat initial

            if (this.sizeModal === "fullscreen") {
                window["$"]("body").css({
                    "overflow-y": ""
                });
            }
            this.onHidden.emit();
        });
    }

    initOnShow() {
        $("#" + this.idModal).on("show.bs.modal", e => {
            this.onShow.emit();
        });
    }

    initOnShown() {
        $("#" + this.idModal).on("shown.bs.modal", e => {
            if (this.isDraggable) {
                // console.log("initOnShown");
                window["$"](".modal-backdrop.show").css({
                    "background-color": "rgba(0, 0, 0, 0)"
                });

                window["$"]("body").css({
                    "overflow-y": "auto"
                });

                window["$"]("body").addClass("we-pr-0");
            }

            // parfois il y a la double scrollbar, alors on overflow hidden l'axe Y
            if (this.sizeModal === "fullscreen") {
                window["$"]("body").css({
                    "overflow-y": "hidden"
                });
            }

            this.onShown.emit();
        });
    }

    close() {
        this.modalService.close(this.idModal);
        this.isOpen = false;
    }

    open() {
        this.modalService.open(this.idModal);
        this.isOpen = true;
    }

    setBodyHtml(html: string) {
        this.bodyHtml = html;
        this.useBodyHtml = true;
    }

    showFooter() {
        this.footerIsHide = false;
    }

    hideFooter() {
        this.footerIsHide = true;
    }

    initDraggable() {
        if (!$(".we-modal.in").length) {
            $('.we-modal-dialog[is-draggable="true"]').css({
                margin: 0
                // top: 0,
                // right: 0
            });
        }

        $(`#${this.idModal}`).modal({
            // backdrop: false,
            show: true
        });

        const x2 = $("body").width() - 500;
        const y2 = $("body").height();
        $(`#${this.idModal} .we-modal-dialog`).css({
            top: "17%",
            left: "40%"
            // transform: "translate(-50%, -50%)" // on peut pas utiliser sa car la height de la modal varie et se retrouve au dessus de la limite
        });
        $(`#${this.idModal} .we-modal-dialog`).draggable({
            // handle: ".we-modal-dialog",
            handle: ".we-modal-header",
            // containment: $(".we-editor")
            containment: [0, 54, x2, y2]
        });
    }
}



