import {
    Component,
    OnInit,
    ViewChild,
    Input,
    ChangeDetectorRef,
    EventEmitter,
    Output,
    OnChanges,
    SimpleChanges
} from "@angular/core";
import { ReactCptComponent } from "../react-cpt/react-cpt.component";
import { ModalComponent } from "../modal/modal.component";
import { ImageLibComponent } from "../image-lib/image-lib.component";
import { ModuleService } from "../../../global/services/module.service";
import { SlideType } from "../../../api-gallerie";
import { debounce, getSrcSet } from "../../utils/utils";
import { ScriptService } from "../../services/script.service";
import { AppService } from "../../../global/services/app.service";
// import { debounce } from "../../../../../shared/utils/utils";

@Component({
    selector: "app-gallery",
    templateUrl: "./gallery.component.html",
    styleUrls: ["./gallery.component.scss"]
})
export class GalleryComponent implements OnInit, OnChanges {
    @ViewChild("MasonryRcpt") MasonryRcpt: ReactCptComponent;
    @ViewChild(ModalComponent)
    modalMediaLib: ModalComponent;
    @ViewChild("modalDelete")
    modalDelete: ModalComponent;
    @ViewChild(ImageLibComponent) imgLib: ImageLibComponent;
    @Input() id;
    @Input() src; // single image
    @Input() srcs; // srcset jsonifi
    @Input() sizes = "800px"; // srcset jsonifi
    @Input() useId: boolean = false; // single image
    @Input() itemByRow = 4;
    @Input() limit = 1;
    @Input() maxHeightSrc;
    @Input() placeholder: string;
    // Creation d'une gallery, on envoie l'id
    @Output() onCreate: EventEmitter<any> = new EventEmitter();

    // Update de la src pour une image (donc pas de gallerie)
    @Output() onSrcChange: EventEmitter<any> = new EventEmitter();

    // retourne l'objet quand on change l'image
    @Output() onChange: EventEmitter<any> = new EventEmitter();

    // retourne les items
    @Output() onOrderChange: EventEmitter<any> = new EventEmitter();
    // indique que la gallery effectue une requete
    @Output() onLoading: EventEmitter<any> = new EventEmitter();
    @Output() onEndLoading: EventEmitter<any> = new EventEmitter();

    wantAddImg = false;
    cpt;
    cptLoaded = false;
    props: any = {};
    loaded: boolean = false;
    srcset;

    images = [];
    mode: string = "add";
    msgDelete = "Voulez vous vraiment supprimer cette lment ?";
    itemInEdit = null;
    indexInEdit = null;

    // lors d'une suppression, le cpt react envoie les items qu'il a dans le state;
    // on les garde pour pouvoir Output l'image qui est passer ventuellement passer en premiere position
    tmpItems = [];

    // config de la librairie d'image
    confLibImg = {
        tabs: {
            freeImg: { active: true },
            biblio: { active: true },
            internet: { active: true },
            color: { active: false }
        }
    };
    actions = {
        add_image_masonry: () => this.newItem(),
        click_img_masonry: $event =>
            this.updateItem($event.data.card, $event.data.index),
        delete_img_masonry: $event => this.deleteItem($event.data, false),
        reorder_items_gallery: $event => this.reOrder($event.params.orders),
        first_load_items: $event => this.onLoadedItems($event.data.items)
    };

    tmpOrders = null;

    debounceOrder = debounce(() => {
        let orders = [];
        this.tmpOrders.forEach(order => {
            orders.push({ id: order.id, rank: order.rank });
        });
        this.onOrderChange.emit(this.tmpOrders);
        this.moduleService.reOrderSlide(
            orders,
            this.props.idGallery,
            (gallery, items, id) => {}
        );
    }, 1500);

    constructor(
        private scriptService: ScriptService,
        private cd: ChangeDetectorRef,
        private moduleService: ModuleService,
        private appService: AppService
    ) {}

    ngOnInit() {
        this.scriptService.loader("MasonryRcpt").then(() => {
            this.cpt = window["MasonryRcpt"].default;
            this.cptLoaded = true;
        });
        this.checker();
    }

    onLoadedItems(items) {
        this.tmpItems = items;
    }

    checker() {
        if (this.useId) {
            // On check si on as besoin de crer une gallerie
            if (!this.id) {
                // console.log("need create gallery");
                this.onLoading.emit();

                this.moduleService.createGallery((gallery, slide) => {
                    this.id = gallery.id;
                    this.initProps();
                    // this.cd.detectChanges();
                    this.onCreate.emit(this.id);
                    const item = { ...slide, src: slide.url };

                    this.onOrderChange.emit([{ item }]);
                    this.onEndLoading.emit();
                }, this.placeholder);
            } else {
                this.initProps();
            }
        } else {
            this.handleInitSrcset();
            this.setMode("update");
        }
    }

    handleInitSrcset() {
        if (this.srcs) {
            try {
                this.srcset = JSON.parse(this.srcs);
            } catch (e) {
                this.srcset = {
                    default: this.srcs ? this.srcs : this.appService.NO_IMG
                };
            }
            this.src = null;
        } else {
            if (!this.src) {
                this.src = this.appService.NO_IMG;
            }
        }
    }

    initProps() {
        this.props = {
            idGallery: Number(this.id),
            useAnimation: false,
            prefixCss: "we-",
            type: "bootstrapDefault",
            containerCss: "container-fluid",
            spacingAdmin: true,
            mode: "edit",
            sectionCss: "",
            cardsInRow: this.itemByRow,
            bg: { class: "bg-transparent", img: { path: "", class: "" } },
            blocks: [],
            btnAdd: {
                limit: this.limit
            },
            onAction: $event =>
                this.actions[$event.action] &&
                this.actions[$event.action]($event),
            canResize: false
        };
        // console.log("this.props", this.props);
        this.loaded = true;
    }

    reOrder(orders) {
        // console.log("orders", orders);
        this.tmpOrders = orders;
        this.debounceOrder();
    }

    setMode(mode: "update" | "add") {
        this.mode = mode;
    }

    newItem() {
        // console.log("new item :D");
        this.setMode("add");
        this.openModalLibImg();
    }

    updateItem(card, index) {
        this.indexInEdit = index;
        this.setMode("update");
        this.cd.detectChanges();
        this.setItemInEdit(card);
        this.openModalLibImg();
    }

    deleteItem(data, canDelete) {
        // console.log("data", data);
        if (!canDelete) {
            const { card, items } = data;
            this.tmpItems = items.filter(item => item.id !== card.id);
            this.setItemInEdit(card);
            this.modalDelete.open();
            return;
        }
        this.onLoading.emit();
        this.moduleService.deleteSlideGallery(this.itemInEdit).then(slide => {
            this.MasonryRcpt.reRender();
            const firstItem = this.tmpItems[0];
            const item = { ...firstItem, src: firstItem.url };

            this.onOrderChange.emit([{ item }]);
            this.onEndLoading.emit();
        });
        this.modalDelete.close();
    }

    setItemInEdit(item) {
        this.itemInEdit = item;
    }

    openModalLibImg() {
        if (!this.wantAddImg) {
            this.wantAddImg = true;
        }
        this.modalMediaLib.open();
    }

    onShown(imgLib) {
        if (imgLib) {
            imgLib.reset();
        }
    }

    closeModalMedia($event) {
        // console.log("$event", $event);
        this.images = [$event.img];
        this.modalMediaLib.close();
        if (this.mode === "add") {
            const slideType: SlideType = {
                cover: false,
                rank: 0,
                title: "",
                type: "img",
                url: this.images[0].srcOriginal || this.images[0].src,
                srcSet:
                    this.images[0].srcSet || // from free image
                    (this.images[0].srcs && JSON.stringify(this.images[0].srcs)) // from biblio
            };
            if (this.id) {
                this.onLoading.emit();
                this.moduleService
                    .createSlideGallery(slideType, this.props.idGallery)
                    .then(slide => {
                        this.MasonryRcpt.reRender();
                        // si quand on ajoute une image, elle passe en premiere position alors on emit onOrderChange
                        const item = { ...slide, src: slide.url };

                        this.onOrderChange.emit([{ item }]);
                        this.onEndLoading.emit();
                        this.indexInEdit = null;
                    });
            } else {
                this.onChange.emit(this.images[0]);
                this.onSrcChange.emit(this.images[0].src);
            }
        } else if (this.mode === "update") {
            const slider = JSON.parse(JSON.stringify(this.itemInEdit));
            const image = JSON.parse(JSON.stringify(this.images[0]));
            delete slider["visible"];
            delete slider["srcSet"];
            delete slider["src_set"];
            delete slider["src"];

            if (this.id) {
                this.onLoading.emit();
                this.moduleService
                    .updateSlideGallery(this.images[0], slider)
                    .then(slide => {
                        this.MasonryRcpt.reRender();
                        if (this.indexInEdit === 0) {
                            const item = { ...slide, src: slide["url"] };
                            this.onOrderChange.emit([{ item }]);
                        }

                        this.onEndLoading.emit();
                        this.indexInEdit = null;
                    });
            } else {
                this.onChange.emit(this.images[0]);
                this.onSrcChange.emit(this.images[0]);
            }

            // this.update.emit({ image: this.images[0], itemInEdit: this.itemInEdit });
        }
    }

    saveImg() {
        // this.cd.detectChanges();
        this.imgLib.save();
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { id, src, srcs } = changes;
        if (id && !id.currentValue && this.useId && !id.firstChange) {
            this.checker();
        }
        if (src && !src.firstChange && src.currentValue === null) {
            this.src = this.appService.NO_IMG;
        }
        if (srcs && !srcs.firstChange) {
            this.handleInitSrcset();
        }
    }

    deleteSrcItem() {
        const img = {
            src: ""
        };
        this.onChange.emit(img);
        this.onSrcChange.emit(null);
    }

    getCover() {
        const item = this.tmpItems.find(item => item.rank === 0) || {};

        return item.src_set || item.url || "";
    }
}



