import { CanDeactivate } from "@angular/router";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";

export interface ComponentCanDeactivate {
    canDeactivate: () => boolean | Observable<boolean>;
}

// https://stackoverflow.com/questions/35922071/warn-user-of-unsaved-changes-before-leaving-page

@Injectable()
export class PendingChangesGuard
    implements CanDeactivate<ComponentCanDeactivate> {
    canDeactivate(
        component: ComponentCanDeactivate
    ): boolean | Observable<boolean> {
        if (
            !component ||
            (component && typeof component.canDeactivate !== "function")
        ) {
            return true;
        }
        // if there are no pending changes, just allow deactivation; else confirm first
        return component.canDeactivate()
            ? true
            : // NOTE: this warning message will only be shown when navigating elsewhere within your angular app;
              // when navigating away from your angular app, the browser will show a generic warning message
              // see http://stackoverflow.com/a/42207299/7307355
              confirm(
                  "Attention: Vous avez des changements non enregistrs. Appuyez sur Annuler pour revenir en arrire et enregistrer ces modifications, ou sur OK pour les perdre."
              );
    }
}



