import {
    Component,
    OnInit,
    Input,
    ViewChild,
    AfterViewInit,
    OnDestroy,
    ComponentFactoryResolver,
    Injector,
    ApplicationRef,
    ViewContainerRef,
    ChangeDetectorRef
} from "@angular/core";

import { DomPortalHost, TemplatePortal, PortalHost, CdkPortal } from "@angular/cdk/portal";

import { StoreService } from "../../services/store.service";
import { NavHeaderComponent } from "../nav-header/nav-header.component";
import { NotificationService } from "../../../global/services/notification.service";

@Component({
    selector: "app-main-content",
    templateUrl: "./main-content.component.html",
    styleUrls: ["./main-content.component.scss"]
})
export class MainContentComponent implements OnInit, AfterViewInit, OnDestroy {
    @Input()
    style: string = "";

    @Input()
    showParams: boolean = false;

    @Input() warnBeforeLeft: () => {
        warning: boolean;
        acceptWarning?: boolean;
    } = () => {
        return { warning: false };
    };

    @Input() showBreadcrum: boolean = false;

    @Input() innerNavbar: boolean = false;

    @Input() canOpenCustomizer: boolean = false;

    @Input() repeatBtns: boolean = false;

    @ViewChild("navHeader") navHeader: NavHeaderComponent;

    @ViewChild("navHeaderParams") navHeaderParams: NavHeaderComponent;

    @ViewChild("sectionEditorTpl")
    sectionEditorTplRef;

    activePortal = true;

    notification: string;
    showNotification: boolean = false;

    private portalHost: PortalHost;
    private portal;

    constructor(
        public store: StoreService,
        private componentFactoryResolver: ComponentFactoryResolver,
        private injector: Injector,
        private appRef: ApplicationRef,
        private viewContainerRef: ViewContainerRef
    ) {}

    ngOnInit() {}

    ngAfterViewInit(): void {
        if (!this.canOpenCustomizer || !this.activePortal) {
            return;
        }
        // Create a portalHost from a DOM element
        // locate at page.component.html
        this.portalHost = new DomPortalHost(
            document.querySelector("#section-editor-content"),
            this.componentFactoryResolver,
            this.appRef,
            this.injector
        );
        // Locate the component factory for the HeaderComponent
        this.portal = new TemplatePortal(this.sectionEditorTplRef, this.viewContainerRef);

        // Attach portal to host
        this.portalHost.attach(this.portal);
    }

    ngOnDestroy(): void {
        if (!this.canOpenCustomizer || !this.activePortal) {
            return;
        }
        if (this.portalHost) {
            this.portalHost.detach();
        }
    }
}



