import { Component, OnInit, ChangeDetectorRef, Input } from "@angular/core";
import { NotificationService } from "../../../global/services/notification.service";

@Component({
    selector: "app-notify-messenger",
    templateUrl: "./notify-messenger.component.html",
    styleUrls: ["./notify-messenger.component.scss"]
})
export class NotifyMessengerComponent implements OnInit {
    @Input() css: string = "";
    constructor(public notifService: NotificationService, private cd: ChangeDetectorRef) {}

    ngOnInit() {}
}



