import { Pipe, PipeTransform } from "@angular/core";
import { PermissionService, LIMIT } from "../services/permission.service";

@Pipe({
    name: "getLimit"
})
export class GetLimitPipe implements PipeTransform {
    constructor(private permService: PermissionService) {}

    transform(key: LIMIT): any {
        return this.permService.getLimit(key);
    }
}



