import { Injectable } from "@angular/core";
import { ScriptService } from "../../shared/services/script.service";
import { ReactService } from "../../shared/services/react.service";
import { StoreService } from "../../shared/services/store.service";

@Injectable()
export class AppService {
    PLACEHOLDER_IMG =
        "https://mediacache.epicred.fr/4X6DE68rOO9oeeAGN6wlzbLgPTU=/1920x/https://media.epicred.fr/media/cache/original/all/5c1901e87c038.png";
    NO_IMG =
        "https://mediacache.epicred.fr/p8CBOfY37Ag_rwZ9TG5iAvJJw_U=/480x/https://media.epicred.fr/media/cache/original/all/5c9ca0b88a51e.png";

    constructor(
        private scriptService: ScriptService,
        private reactService: ReactService,
        private store: StoreService
    ) {}

    refreshUid() {
        if (this.store.curSite["uids"]) {
            const scriptUidSite = `window.__UID_SITE=${JSON.stringify(this.store.curSite["uids"])};`;
            this.scriptService.scriptJsWithContent(scriptUidSite, "script-uid-site");
            this.reactService.refreshAll.next();
        }
    }
}



