import {
    Directive,
    ElementRef,
    Renderer,
    Input,
    Output,
    EventEmitter,
    forwardRef,
    AfterViewInit,
    OnDestroy
} from "@angular/core";
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from "@angular/forms";

declare let CKEDITOR: any;

/**
 * USE CASE
 * <div appCkeditor > //will focus
 * appCkeditor="true" //will focus
 * appCkeditor="false" //will not focus
 */

@Directive({
    selector: "[appCkeditor]",
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => CkeditorDirective),
            multi: true
        }
    ]
})
export class CkeditorDirective
    implements ControlValueAccessor, AfterViewInit, OnDestroy {
    private element;
    private editor: any;
    private _value: string;
    private fonts: string[] = [];
    private init = false;
    private firstOnChange: boolean = false; // ckeditor envoie un event onEditorKeyup a ledit, donc on bloque la premiere fois
    @Input() ckoptions: any;
    @Input() inContent: any;
    @Input() focus: boolean;
    @Output() public onEditorKeyup = new EventEmitter();
    @Output() public onRemove = new EventEmitter();
    @Output() public onDelete = new EventEmitter();

    get value() {
        return this._value;
    }
    set value(value) {
        this._value = value;
        this.onChange(value);
        this.onTouched();
    }

    constructor(elementRef: ElementRef, private renderer: Renderer) {
        this.element = elementRef.nativeElement;
        this.renderer.setElementAttribute(
            this.element,
            "contenteditable",
            "true"
        );
        // this.fonts = this.editorService.getFonts();
    }

    ngAfterViewInit() {
        if (typeof CKEDITOR === "undefined") {
            console.warn("CKEditor not available");
        } else {
            // stop auto-replacing inline components
            CKEDITOR.disableAutoInline = true;

            // CKEditor inline
            // https://ckeditor.com/ckeditor-4/#inline
            this.editor = CKEDITOR.inline(
                this.element,
                this.getConf(this.ckoptions)
            );
            this.value = this.inContent;
            // Set initial value
            this.editor.setData(this.value);

            // CKEditor change event
            this.editor.on("change", () => {
                this.onTouched();
                this.value = this.editor.getData();
            });

            this.editor.on("blur", () => {
                this.onBlur();
            });
            this.editor.on("destroy", () => {
                this.onDestroy();
            });
        }
    }

    /**
     * On component destroy
     */
    ngOnDestroy() {
        if (this.editor) {
            this.editor.focusManager.blur(true);
            this.deleteAllInstances();
            this.editor = null;
        }
    }

    /**
     * Implements ControlValueAccessor
     */
    writeValue(value) {
        this._value = value;
        if (this.editor) {
            this.editor.setData(value);
        }
    }

    onChange(_) {
        if (this.firstOnChange) {
            this.onEditorKeyup.emit(this._value);
        } else {
            this.firstOnChange = true;
        }
    }
    onTouched() {}
    onBlur() {
        this.onRemove.emit(this._value);
    }
    onDestroy() {
        this.onDelete.emit(this._value);
    }
    registerOnChange(fn) {
        this.onChange = fn;
    }
    registerOnTouched(fn) {
        this.onTouched = fn;
    }
    registerOnBlur(fn) {
        this.onBlur = fn;
    }
    registerOnDestroy(fn) {
        this.onDestroy = fn;
    }

    getConf(conf) {
        switch (conf) {
            case 1:
                return {
                    startupFocus: this.focus ? true : false,
                    enterMode: CKEDITOR.ENTER_DIV,
                    format_tags: "p;h1;h2;h3;h4",
                    contentsCss: [this.getContentCss()],
                    font_names: this.getFontNames(),
                    toolbar: [
                        {
                            name: "clipboard",
                            items: ["PasteText", "PasteFromWord"]
                        },
                        { name: "editing", items: ["Scayt", "RemoveFormat"] },
                        {
                            name: "basicstyles",
                            items: ["Bold", "Italic", "Underline", "Strike"]
                        },
                        {
                            name: "paragraph",
                            items: [
                                "JustifyLeft",
                                "JustifyCenter",
                                "JustifyRight",
                                "JustifyBlock"
                            ]
                        },
                        {
                            name: "list",
                            items: ["NumberedList", "BulletedList"]
                        },
                        {
                            name: "styles",
                            items: ["Format", "Font", "FontSize"]
                        },
                        { name: "links", items: ["Link", "Unlink", "Anchor"] },
                        {
                            name: "colors",
                            items: ["TextColor", "BGColor", "Image"]
                        }
                    ]
                };
            case 2:
                return {
                    startupFocus: this.focus ? true : false,
                    enterMode: CKEDITOR.ENTER_DIV,
                    format_tags: "p;h1;h2;h3;h4",
                    contentsCss: [this.getContentCss()],
                    font_names: this.getFontNames(),
                    toolbar: [
                        {
                            name: "basicstyles",
                            items: ["Bold", "Italic", "Underline", "Strike"]
                        },
                        {
                            name: "paragraph",
                            items: [
                                "JustifyLeft",
                                "JustifyCenter",
                                "JustifyRight",
                                "JustifyBlock"
                            ]
                        },
                        {
                            name: "list",
                            items: ["NumberedList", "BulletedList"]
                        }
                    ]
                };
        }
    }

    getFontNames() {
        let fontFormats = "";
        let defaultFonts =
            "Arial;Comic Sans MS;Courier New;Georgia;Lucida Sans Unicode;Tahoma;Times New Roman;Trebuchet MS;Verdana;";
        for (let i = 0; i < this.fonts.length; i++) {
            let font = this.fonts[i];
            fontFormats += font + ";";
        }
        return fontFormats + defaultFonts;
    }

    getContentCss() {
        let fontNames = "";
        for (let i = 0; i < this.fonts.length; i++) {
            let font = this.fonts[i];
            fontNames += font + "|";
        }
        return (
            "https://fonts.googleapis.com/css?family=" + encodeURI(fontNames)
        );
    }

    deleteAllInstances() {
        for (const name in CKEDITOR.instances) {
            if (CKEDITOR.instances.hasOwnProperty(name)) {
                CKEDITOR.instances[name].destroy();
            }
        }
    }
}



