import { Injectable } from "@angular/core";
import {
    SitesService,
    PageType,
    PagesService,
    VersionsService,
    PageHistory,
    ContainerType,
    Meta,
    Page,
    DesignConfigType,
    DesignsModuleService
} from "../../api";
import { ModulesService } from "../../api/api/modules.service";
import { StoreService } from "../../shared/services/store.service";
import { slug, simpleMerge } from "../../shared/utils/utils";
import { ErrorsService } from "./errors.service";
import {
    GalleriesService,
    Gallerie,
    SlideType,
    SlidesService
} from "../../api-gallerie";
import { AppService } from "./app.service";
import { ScriptService } from "../../shared/services/script.service";
import { PreferenceService } from "../../api-shop";
import { ReactService } from "../../shared/services/react.service";

@Injectable()
export class ModuleService {
    pendingPostModules = false;

    constructor(
        private store: StoreService,
        private appService: AppService,
        private errorsService: ErrorsService,
        private siteApi: SitesService,
        private modulesApi: ModulesService,
        private pageApi: PagesService,
        private galleryApi: GalleriesService,
        private gallerySliderApi: SlidesService,
        private versionPageApi: VersionsService,
        private designModuleApi: DesignsModuleService,
        private scriptService: ScriptService,
        private preferenceShopApi: PreferenceService,
        private reactService: ReactService
    ) { }

    newPage(
        nom,
        url,
        isService,
        module_page_type,
        module_resource = null
    ): PageType {
        return {
            nom,
            url,
            is_service: isService,
            module_page_type,
            module_resource
        };
    }

    // cre le design module shop avec une fiche produit et l'id shop
    initDesignShop(idShop, onSuccess: Function) {
        this.store.designModules.config["shop"] = {
            commun: {
                idShop,
                ribbons: [
                    { type: "promo", bgColor: "#f4253e", txt: "Promo" },
                    { type: "soon", bgColor: "#50A6E0", txt: "Bientt dispo" },
                    { type: "new", bgColor: "#49D383", txt: "Nouveau" }
                ]
            },
            ficheProduit: {
                singleProduct: {
                    tpl: "Tpl02",
                    mepId: 2,
                    cta: {
                        name: "cta",
                        tpl: "BtnBootstrap",
                        type: "primary",
                        class: "btn-primary btn-block",
                        link: "",
                        txt: "Ajouter au panier",
                        visible: true
                    },
                    galleryProps: {
                        styleME: {
                            maxHeight: "500px",
                            margin: "auto",
                            objectFit: "cover"
                        },
                        mepPaging: "thumbnails",
                        delay: 4000,
                        animationSlide: "defaut",
                        bg: {
                            class: "bg-transparent",
                            img: { path: "", class: "" }
                        },
                        autoplay: false,
                        showArrows: false,
                        usePaddingY: false,
                        valPaddingB: 0,
                        valPaddingT: 0
                    },
                    socialLinks: [],
                    ui: {
                        mep: { slider: "left" },
                        price: true,
                        reference: true,
                        categories: false,
                        tags: false,
                        quantitySelector: true,
                        navigation: false,
                        socialLinks: true,
                        btnAddToBasket: true,
                        refUnit: true,
                        remainingStock: false,
                        fabricant: false,
                        fournisseurs: false,
                        comments: false,
                        styleAttributs: "btn"
                    }
                }
            }
        };

        this.preferenceShopApi
            .apiMagasinsSlugPreferenceGeneralesGet(idShop.toString())
            .subscribe(e => {
                this.store.designModules.config["shop"].commun = simpleMerge(
                    this.store.designModules.config["shop"].commun,
                    e
                );
                // console.log("initDesignShop depuis admin");
                this.updateDesignModule(onSuccess);
            });
    }

    updateDesignModule(onSuccess?: Function) {
        const config = JSON.stringify(this.store.designModules.config);
        const dcType: DesignConfigType = { config, type: "modules" };
        this.designModuleApi
            .apiSitesSlugDesignsDesignModulePut(
                dcType,
                String(this.store.curSite.id),
                String(this.store.designModules.id)
            )
            .subscribe(dc => {


                console.log("Update Conf :", dc);
                if (typeof onSuccess === "function") {
                    onSuccess();
                }
                // console.log("on poste le msg");
                const strMsg = JSON.stringify({
                    designModules: this.store.designModules
                });
                parent.postMessage(strMsg, "*");
            });
    }

    pagesBlog(resources, onSuccess?: Function) {
        this.postPages(resources, 1, onSuccess);
    }

    postPages(resources, idModule, onSuccess?: Function) {
        this.modulesApi
            .apiSitesSlugModulesModulePagesPost(
                { resources },
                String(this.store.curSite.id),
                String(idModule)
            )
            .subscribe(() => {
                if (typeof onSuccess === "function") {
                    onSuccess();
                }
            });
    }

    // cre une meta description
    postDescMeta(idVersionPage, descMeta, onSuccess?: Function) {
        const { id, ...meta } = descMeta;
        this.versionPageApi
            .apiVersionsSlugMetasPost(meta, String(idVersionPage))
            .subscribe(meta => {
                if (typeof onSuccess === "function") {
                    onSuccess(meta);
                }
            });
    }

    createContainer(pageVersion: PageHistory, onSuccess?: Function) {
        const container: ContainerType = {
            content: this.getContentReact(),
            typologie: "content",
            rang: 0,
            soustype: "react"
        };

        this.versionPageApi
            .apiVersionsSlugContainersPost(container, String(pageVersion.id))
            .subscribe(e => {
                // console.log("container ajout !", e);
                if (typeof onSuccess === "function") {
                    onSuccess(pageVersion, e);
                }
            });
    }

    getContentReact() {
        return '{"type":"react","props":{"prefixCss":"","sectionCss":"","containerCss":"container-fluid","bg":{"class":"","img":{"path":"","class":""},"type":"color"},"blocks":[],"uid":""},"form":"empty-rcpt-form","componentName":"EmptyRcpt","tplName":"EmptyRcpt"}';
    }

    updateMeta(meta: Meta, idVersionPage, onSuccess?: Function) {
        // const idMeta = meta["id"];
        const { id, ..._meta } = meta;
        // console.log("id", id);
        // console.log("_meta", _meta);
        // console.log("meta", meta);
        // const _meta = {}
        this.versionPageApi
            .apiVersionsSlugMetasMetaPut(
                _meta,
                String(idVersionPage),
                String(id)
            )
            .subscribe(
                meta => {
                    if (typeof onSuccess === "function") {
                        onSuccess(meta);
                    }
                },
                error => {
                    const msg = this.errorsService.handleErrorHttp(error);
                    alert(msg + " (#ERR110)");
                }
            );
    }

    // Version de la page pour le SEO
    // online si l'entity/module ne travail pas avec du versioning
    getVersionPage(page: Page, online: boolean, onSuccess: Function) {

        console.log("getVersionPage",page, online, onSuccess);
        
        if (online) {
            this.pageApi
                .apiPagesSlugVersionsOnlineGet(String(page.id))
                .subscribe(pageHistory => {
                    onSuccess(pageHistory);
                });
        } else {
            this.pageApi
                .apiPagesSlugVersionsLastGet(String(page.id))
                .subscribe(pageHistory => {
                    onSuccess(pageHistory);
                });
        }
    }

    createGallery(onSuccess: Function, imageUrl?: string) {
        let galleryId = this.store.curSite.uids.find(u => u.nom === "gallerie");
        this.galleryApi
            .apiGalleriesStorePost(galleryId.distant_id.toString())
            .subscribe((gallery: Gallerie) => {
                const sliderItem: SlideType = {
                    cover: false,
                    rank: 0,
                    title: "",
                    type: "img",
                    url: imageUrl || this.appService.PLACEHOLDER_IMG
                    // srcSet: imgsGallery[i]["srcSet"]
                };
                this.createSlideGallery(sliderItem, gallery.id).then(() => {
                    onSuccess(gallery, sliderItem);
                });
            });
    }

    // cree un slide dans une gallerie
    createSlideGallery(sliderItem, id): any {
        return new Promise(resolve => {
            this.galleryApi
                .apiGalleriesGallerieSlidesPost(sliderItem, String(id))
                .subscribe(slide => {
                    resolve(slide);
                });
        });
    }

    deleteSlideGallery(sliderItem) {
        const { id } = sliderItem;
        return new Promise(resolve => {
            this.gallerySliderApi
                .apiSlidesSlideDelete(String(id))
                .subscribe(slide => {
                    resolve(slide);
                });
        });
    }

    // met a jour l'url d'un slide de la Gallery
    updateSlideGallery(image, sliderItem) {
        const { id, img, created, updated, ..._sliderItem } = sliderItem;
        _sliderItem.url = image.srcOriginal || image.src;
        _sliderItem.srcSet =
            image.srcSet || (image.srcs && JSON.stringify(image.srcs));
        return new Promise(resolve => {
            this.gallerySliderApi
                .apiSlidesSlidePut(_sliderItem, String(id))
                .subscribe(slide => {
                    resolve(slide);
                });
        });
    }

    reOrderSlide(items, id, onSuccess?: Function) {
        // console.log('items', items)
        // console.log('id', id)
        this.gallerySliderApi
            .apiSlidesGallerieOrderPut(items, String(id))
            .subscribe(gallery => {
                // console.log('gallery', gallery);
                if (typeof onSuccess === "function") {
                    onSuccess(gallery, items, id);
                }
            });
    }

    rebuildUids(nameModule, onSuccess: Function) {
        this.pendingPostModules = true;
        setTimeout(() => {
            this.modulesApi
                .apiSitesSiteModulesActivationsPost(
                    { nom: nameModule },
                    this.store.curSite.id.toString()
                )
                .subscribe(
                    e => {
                        // console.log('SHOP UIDS MODULES', e);
                        // console.log("STORE REBUILD :", this.store);
                        this.store.curSite["uids"] = e;
                        this.store.memberId = this.setIdModule("membership");
                        this.store.paiementId = this.setIdModule("paiement");
                        this.store.shopId = this.setIdModule("shop");
                        this.store.blogId = this.setIdModule("blog");
                        this.store.formationId = this.setIdModule("formation");
                        onSuccess(e);
                        const strMsg = JSON.stringify({
                            uidSite: this.store.curSite["uids"]
                        });
                        parent.postMessage(strMsg, "*");
                        this.refreshUid();
                        this.pendingPostModules = false;
                    },
                    err => {
                        this.errorsService.handleErrorHttp(err);
                        this.pendingPostModules = false;
                    }
                );
        }, 0);
    }

    setIdModule(name) {
        const uidModule = this.store.curSite["uids"].find(
            word => word.nom === name
        );
        return uidModule && uidModule.distant_id;
    }

    activateModule(nameModule) {
        return new Promise((resolve, reject) => {
            const moduleIsActivate =
                this.store.curSite["uids"].filter(uid => uid.nom === nameModule)
                    .length !== 0;
            if (!moduleIsActivate) {
                this.rebuildUids(nameModule, () => {
                    resolve();
                    // this.shopLoaded = true;
                });
            } else {
                let needsRebuild = false;

                switch (nameModule) {
                    case "shop":
                        let memberEntry = this.store.curSite["uids"].filter(
                            word => word.nom == "membership"
                        );
                        if (memberEntry.length > 0) {
                            this.store.memberId = memberEntry[0].distant_id;
                        } else {
                            needsRebuild = true;
                        }

                        let paiementEntry = this.store.curSite["uids"].filter(
                            word => word.nom == "paiement"
                        );
                        if (paiementEntry.length > 0) {
                            this.store.paiementId = paiementEntry[0].distant_id;
                        } else {
                            needsRebuild = true;
                        }

                        let shopEntry = this.store.curSite["uids"].filter(
                            word => word.nom == "shop"
                        );
                        if (shopEntry.length > 0) {
                            this.store.shopId = shopEntry[0].distant_id;
                        } else {
                            needsRebuild = true;
                        }
                        break;
                    case "blog":
                        let blogEntry = this.store.curSite["uids"].filter(
                            word => word.nom == "blog"
                        );
                        if (blogEntry.length > 0) {
                            this.store.blogId = blogEntry[0].distant_id;
                        } else {
                            needsRebuild = true;
                        }
                        break;
                    case "formation":
                        let FormationEntry = this.store.curSite["uids"].filter(
                            word => word.nom == "formation"
                        );
                        if (FormationEntry.length > 0) {
                            this.store.formationId = FormationEntry[0].distant_id;
                        } else {
                            needsRebuild = true;
                        }
                        break;
                }

                if (needsRebuild) {
                    this.rebuildUids(nameModule, () => {
                        resolve();
                        // this.shopLoaded = true;
                    });
                } else {
                    resolve();
                    // this.shopLoaded = true;
                }
            }
        });
    }

    refreshUid() {
        this.appService.refreshUid();
    }
}



