import { NgModule, ModuleWithProviders } from "@angular/core";
import { CommonModule } from "@angular/common";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { AuthGuard } from "./_guards";
import { FilterPipe } from "./pipes/filter.pipe";
import { SafeHtmlPipe } from "./pipes/safe-html.pipe";
import { SafeResUrlPipe } from "./pipes/safe-res-url.pipe";
import { SafeStylePipe } from "./pipes/safe-style.pipe";
import { SafeUrlPipe } from "./pipes/safe-url.pipe";
import { AutofocusDirective } from "./directives/auto-focus.directive";
import { CkeditorDirective } from "./directives/ckeditor.directive";
import { CollapseDirective } from "./directives/collapse.directive";
import { DebounceDirective } from "./directives/debounce.directive";
import { ButtonComponent } from "./components/button/button.component";
import { ImageLibComponent } from "./components/image-lib/image-lib.component";
import { InputComponent } from "./components/input/input.component";
import { ModalComponent } from "./components/modal/modal.component";
import { NavbarComponent } from "./components/navbar/navbar.component";
import { PanelOptionsComponent } from "./components/panel-options/panel-options.component";
import { ReactCptComponent } from "./components/react-cpt/react-cpt.component";
import { VerticalPillsComponent } from "./components/vertical-pills/vertical-pills.component";
import { TextEditableComponent } from "./components/text-editable/text-editable.component";
import { RouterModule } from "@angular/router";
import { ModalService } from "./services/modal.service";
import { ReactService } from "./services/react.service";
import { ImageLibService } from "./services/image-lib.service";
import { HttpClientModule, HTTP_INTERCEPTORS } from "@angular/common/http";
import { ModalDirective } from "./components/modal/modal.directive";
import { OrderModule } from "ngx-order-pipe";
import { ScriptService } from "./services/script.service";
import { StoreService } from "./services/store.service";
import { LoadingComponent } from "./components/loading/loading.component";
import { MenuComponent } from "./components/menu/menu.component";
import { MenuItemComponent } from "./components/menu-item/menu-item.component";
import { ThemeSelectorComponent } from "./components/theme-selector/theme-selector.component";
import { DomainHandlerComponent } from "./components/domain-handler/domain-handler.component";
import { HttpEventInterceptor } from "./classes/HttpEventInterceptor";
// import { TooltipModule } from "ng2-tooltip-directive";
import { TooltipIconComponent } from "./components/tooltip-icon/tooltip-icon.component";
import { TooltipDirective } from "./directives/tooltip.directive";
import { TooltipComponent } from "./components/tooltip/tooltip.component";
import { DebounceClickDirective } from "./directives/debounce-click.directive";
import { BackgroundEditorComponent } from "./components/background-editor/background-editor.component";
import { StrReplacePipe } from "./pipes/str-replace.pipe";
import { SrcsetPipe } from "./pipes/srcset.pipe";
import { ViewportDetectorComponent } from "./components/viewport-detector/viewport-detector.component";
import { RouterParamsService } from "./services/router-params.service";
import { NotifyMessengerComponent } from "./components/notify-messenger/notify-messenger.component";
import { FormatDatePipe } from "./pipes/format-date.pipe";
import { GetMainDomainPipe } from "./pipes/get-main-domain.pipe";
import { TagInputModule } from "ngx-chips";
import { MediaEditorComponent } from "./components/media-editor/media-editor.component";
import { PermissionComponent } from "./components/permission/permission.component";
import { HasReachedLimitPipe } from "./pipes/has-reached-limit.pipe";
import { GetLimitPipe } from "./pipes/get-limit.pipe";
import { PermissionService } from "./services/permission.service";
import { LinkToPlansComponent } from "./components/link-to-plans/link-to-plans.component";
import { ModalInfoPlanComponent } from "./components/modal-info-plan/modal-info-plan.component";
import { FormEditSiteComponent } from "./components/form-edit-site/form-edit-site.component";

import { MomentDatePipe } from "./pipes/moment-date.pipe";
import { ObjectsIteratePipe } from "./pipes/objects-iterate.pipe";
import { TooltipBootstrapDirective } from "./directives/tooltip-bootstrap.directive";
import { ClickOutsideDirective } from "./directives/click-outside.directive";
import { GalleryComponent } from "./components/gallery/gallery.component";
import { MetaPageComponent } from "./components/meta-page/meta-page.component";
import { SidebarComponent } from "./components/sidebar/sidebar.component";
import { MainContentComponent } from "./components/main-content/main-content.component";
import { NavHeaderComponent } from "./components/nav-header/nav-header.component";
import { BreadcrumbComponent } from "./components/breadcrumb/breadcrumb.component";
import { DraftEditorComponent } from "./components/draft-editor/draft-editor.component";
import { ElementByLineFormComponent } from "./components/form/element-by-line-form/element-by-line-form.component";
import { FormeImageFormComponent } from "./components/form/forme-image-form/forme-image-form.component";
import { PosBtnFormComponent } from "./components/form/pos-btn-form/pos-btn-form.component";
import { RatioMediaFormComponent } from "./components/form/ratio-media-form/ratio-media-form.component";

import { PerfectScrollbarModule } from "ngx-perfect-scrollbar";
import { PERFECT_SCROLLBAR_CONFIG } from "ngx-perfect-scrollbar";
import { PerfectScrollbarConfigInterface } from "ngx-perfect-scrollbar";
import { PendingChangesGuard } from "./_guards/pending-changes.guard";
import { CfgModule, CONFIG_MODULE } from "./classes/CfgModule";
import { FormFieldFormatPipe } from "./pipes/form-field-format.pipe";
import { IsocodeCountryToNamePipe } from "./pipes/isocode-country-to-name.pipe";
import { FontFamilyDirective } from "./directives/font-family.directive";

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
    suppressScrollX: true
};

const imports = [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule,
    HttpClientModule,
    OrderModule,
    TagInputModule,
    PerfectScrollbarModule
    // TooltipModule
];
const declarations = [
    FilterPipe,
    IsocodeCountryToNamePipe,
    SrcsetPipe,
    MomentDatePipe,
    FormatDatePipe,
    GetMainDomainPipe,
    ObjectsIteratePipe,
    SafeHtmlPipe,
    SafeResUrlPipe,
    SafeStylePipe,
    SafeUrlPipe,
    AutofocusDirective,
    CkeditorDirective,
    CollapseDirective,
    TooltipBootstrapDirective,
    DebounceDirective,
    FontFamilyDirective,
    ClickOutsideDirective,
    ButtonComponent,
    ImageLibComponent,
    GalleryComponent,
    MetaPageComponent,
    InputComponent,
    ModalComponent,
    NavbarComponent,
    PanelOptionsComponent,
    ReactCptComponent,
    VerticalPillsComponent,
    SidebarComponent,
    TextEditableComponent,
    ModalDirective,
    LoadingComponent,
    MenuComponent,
    MenuItemComponent,
    ThemeSelectorComponent,
    DomainHandlerComponent,
    MainContentComponent,
    NavHeaderComponent,
    TooltipIconComponent,
    NotifyMessengerComponent,
    TooltipComponent,
    TooltipDirective,
    DebounceClickDirective,
    BreadcrumbComponent,
    BackgroundEditorComponent,
    DraftEditorComponent,
    StrReplacePipe,
    FormFieldFormatPipe,
    ElementByLineFormComponent,
    FormeImageFormComponent,
    PosBtnFormComponent,
    RatioMediaFormComponent,
    PermissionComponent,
    HasReachedLimitPipe,
    GetLimitPipe,
    LinkToPlansComponent,
    ModalInfoPlanComponent,
    FormEditSiteComponent,
    ViewportDetectorComponent,
    MediaEditorComponent
];
const _exports = [...imports, ...declarations];

/**
 * Quand on genere un component via la commande (ng g c, ng g p...), sa rajoute declarations dans @NgModule,
 * Il faut supprimer la ligne 'declarations: [...nameCpt]' (celle aprs entryComponents: [TooltipComponent])
 * et mettre le component dans l'objet declarations plus haut,
 * il sera alors exporter directement via la ligne 115 _exports...
 */

@NgModule({
    imports,
    declarations,
    exports: _exports,
    entryComponents: [TooltipComponent]
})
export class SharedModule {
    static forRoot(config: CfgModule = {}): ModuleWithProviders {
        return {
            ngModule: SharedModule,
            providers: [
                {
                    provide: CONFIG_MODULE,
                    useValue: config
                },
                AuthGuard,
                ModalService,
                ImageLibService,
                ScriptService,
                StoreService,
                ReactService,
                RouterParamsService,
                PermissionService,
                PendingChangesGuard,
                GetMainDomainPipe, // pour l'utiliser en tant que service aussi
                {
                    provide: HTTP_INTERCEPTORS,
                    useClass: HttpEventInterceptor,
                    multi: true
                },
                {
                    provide: PERFECT_SCROLLBAR_CONFIG,
                    useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
                }
            ]
        };
    }
}



