import { Pipe, PipeTransform } from "@angular/core";
import { Domain } from "../../api";

@Pipe({
    name: "getMainDomain"
})
export class GetMainDomainPipe implements PipeTransform {
    transform(domains: Domain[], key?: any): any {
        if (domains) {
            const main = domains.find(d => d.main) || domains[0];
            return key ? main[key] : main;
        }
    }
}



