import {
    Component,
    OnInit,
    Output,
    EventEmitter,
    Input,
    ViewChild,
    OnChanges,
    SimpleChanges,
    ChangeDetectorRef
} from "@angular/core";

declare var $: any;

@Component({
    selector: "app-media-editor",
    templateUrl: "./media-editor.component.html",
    styleUrls: ["./media-editor.component.scss"]
})
export class MediaEditorComponent implements OnInit, OnChanges {
    @Input()
    item: any = {};
    @Input()
    index: number = 0;
    @Output()
    onUpdate: EventEmitter<any> = new EventEmitter();

    tplSelected: string = null;
    optHeightSelected: string = null;
    optPositionSelected: string = null;
    model = {
        posX: 50,
        posY: 50,
        height: 230,
        autoHeight: true
    };

    lastSizeCustom = {
        x: null,
        y: null
    };

    constructor(private cd: ChangeDetectorRef) {}

    ngOnInit() {
        this.initItem();
    }

    ngOnChanges(changes: SimpleChanges): void {
        console.log("changes", changes);
        if (changes.index && changes.index.previousValue !== changes.index.currentValue) {
            // this.showTpl("");
            // this.srcImg = null;
            // this.refreshBg();
            this.initItem();
        }
    }

    initItem() {
        console.log("index", this.index);
        console.log("item", this.item);
        this.tplSelected = this.item.tpl;
        if (this.item.style) {
            if (this.item.style.objectPosition) {
                const splitPos = this.item.style.objectPosition.split("%");
                this.model.posX = parseInt(splitPos[0], 10);
                this.model.posY = parseInt(splitPos[1], 10);
            }
        }
    }

    handleChangeOptHeight($event) {
        const val = $event.target.value;
        this.optHeightSelected = val;

        if (val === "customSize") {
            // this.pushCustomSize();
        } else {
            // this.pushDefaultSize();
        }
    }

    changeStyle($event, key: "posX" | "posY" | "height") {
        this.model[key] = $event;

        this.pushCustomSize();
    }
    pushDefaultSize() {
        this.onUpdate.emit({
            height: this.model.autoHeight ? "auto" : `${this.model.height}px`
        });
    }
    pushCustomSize() {
        const style = {
            objectFit: "none",
            objectPosition: `${this.model.posX}% ${this.model.posY}%`,
            width: "100%",
            height: this.model.autoHeight ? "auto" : `${this.model.height}px`
        };
        this.onUpdate.emit(style);
    }

    zoom() {}

    dezoom() {}
}



