import { Component, OnInit, Input } from "@angular/core";
import {
    ActivatedRoute,
    Router,
    NavigationEnd,
    PRIMARY_OUTLET
} from "@angular/router";
import { filter, distinctUntilChanged, map } from "rxjs/operators";

@Component({
    selector: "app-breadcrumb",
    templateUrl: "./breadcrumb.component.html",
    styleUrls: ["./breadcrumb.component.scss"]
})
export class BreadcrumbComponent implements OnInit {
    @Input() breadcrumbs: Breadcrumb[] = [];

    @Input() autoDetect: boolean = true;

    @Input() addHome: boolean = true;

    bureauAdded = false;

    /**
  /*.filter(event => event instanceof NavigationEnd)
 .distinctUntilChanged()
 .map(event =>  this.buildBreadCrumb(this.activatedRoute.root)); */

    constructor(private router: Router, private route: ActivatedRoute) {}

    ngOnInit() {
        this.checkUrl();

        this.route.params.subscribe(routeParams => {
            this.checkUrl(true);
        });
    }

    checkUrl(refresh?: boolean) {
        if (
            this.autoDetect &&
            (this.breadcrumbs.length === 0 || refresh === true)
        ) {
            this.autoCheckBreadcrum();
        }

        if (this.addHome) {
            if (!this.bureauAdded || refresh === true) {
                this.breadcrumbs.unshift({ label: "Bureau", url: "/" });
                this.bureauAdded = true;
            }
        }
    }

    autoCheckBreadcrum() {
        const pathName = document.location.pathname.substring(1);
        const paths = pathName.split("/");
        let url = "";

        this.breadcrumbs = paths.map(p => {
            url = url + "/" + p;
            return { label: p, url };
        });
    }
}

export interface Breadcrumb {
    label: string;
    url: string;
}



