import { Component, OnInit, Input } from "@angular/core";

@Component({
    selector: "app-tooltip-icon",
    templateUrl: "./tooltip-icon.component.html",
    styleUrls: ["./tooltip-icon.component.scss"]
})
export class TooltipIconComponent implements OnInit {
    @Input()
    msg: string;
    @Input()
    iconCss: string = "fal fa-question-circle";
    @Input()
    options: {
        placement?: string;
        delay?: number;
        "show-delay"?: number;
        "hide-delay"?: number;
        "hide-delay-mobile"?: number;
        "z-index"?: number;
        "animation-duration"?: number;
        "animation-duration-default"?: number;
        trigger?: string;
        "tooltip-class"?: string;
        display?: boolean;
        "display-mobile"?: boolean;
        shadow?: boolean;
        theme?: string;
        offset?: number;
        id?: boolean;
    };

    constructor() {}

    ngOnInit() {}
}



