import {
    Component,
    OnInit,
    Input,
    EventEmitter,
    Output,
    ViewChild,
    ElementRef,
    AfterViewInit,
    OnChanges,
    SimpleChanges
} from "@angular/core";
import { Icon } from "../../classes/icon";
import { PermissionService, PLAN_CSS } from "../../services/permission.service";
import { Site } from "../../../api";
import { StoreService } from "../../services/store.service";

@Component({
    selector: "app-button",
    templateUrl: "./button.component.html",
    styleUrls: ["./button.component.scss"]
})
export class ButtonComponent implements OnInit, AfterViewInit, OnChanges {
    @Input() type: string = "";

    @Input() tooltipContent: string = "";

    @Input() iconPos: string = "left";

    @Input() icon: Icon;

    @Input() style: any;

    @Input() label: string;

    @Input() disabled: boolean;

    @Input() tooltipOptions: any = { display: false };

    @Input() className: string;

    // tableau de data attribut a setter au bouton
    @Input() attr: any = [];

    @Input() activeBadge: boolean = false;

    @Input() badge: { title: string; node: string } = { title: "", node: "" };

    @Input() useCssPlan = false;

    @Input() site: Site;

    @Output() onClick: EventEmitter<any> = new EventEmitter();

    @Output() onFocus: EventEmitter<any> = new EventEmitter();

    @Output() onBlur: EventEmitter<any> = new EventEmitter();

    @ViewChild("button") button: ElementRef;

    constructor(private store: StoreService) {}

    ngOnInit() {
        if (this.useCssPlan) {
            const _site = this.site ? this.site : this.store.curSite;
            const cssPlan = PLAN_CSS[_site.plan.name];
            this.className += ` .${cssPlan}`;
        }
    }

    ngAfterViewInit() {
        this.setAttr();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.attr && !changes.attr.firstChange) {
            this.setAttr();
        }
    }

    setAttr() {
        for (let i = 0; i < this.attr.length; i++) {
            this.button.nativeElement.setAttribute(
                this.attr[i].name,
                this.attr[i].value
            );
        }
    }
}



