import {
    Component,
    OnInit,
    Input,
    Output,
    EventEmitter,
    ChangeDetectorRef,
    AfterViewInit,
    ViewChild,
    ElementRef,
    OnDestroy,
    OnChanges,
    SimpleChanges
} from "@angular/core";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { Icon } from "../../classes/icon";

declare var $: any;

let nextIdModal = 0;

@Component({
    selector: "app-input",
    templateUrl: "./input.component.html",
    styleUrls: ["./input.component.scss"],
    host: { style: "width: 100%" }
})
export class InputComponent
    implements OnInit, AfterViewInit, OnDestroy, OnChanges {
    @Input() id: string = `input_${nextIdModal++}`;

    @Input() type: string = "text";

    @Input() placeholder: string = "";

    @Input() name: string;

    @Input() iconR: Icon;

    @Input() iconL: Icon;

    @Input() pattern: string;

    @Input() disabled: boolean;

    @Input() readonly: boolean;

    @Input() required: boolean;

    @Input() value: string | number;

    @Input() style: any;

    @Input() className: string;

    @Input() focus: boolean = false;

    @Input() debounce: number = 0;

    @Input() groupClassNameR: string = "we-input-group-addon";

    @Input() groupClassNameL: string = "we-input-group-addon";

    @Input() control: any = { active: false, msg: "" };

    @Input() controls: any = [];

    @Input() ngStyleForm = {};

    @Input() form: FormGroup = new FormGroup({
        input: new FormControl("")
    });

    @Output() onClick: EventEmitter<any> = new EventEmitter();

    @Output() onSubmit: EventEmitter<any> = new EventEmitter();

    @Output() onFocus: EventEmitter<any> = new EventEmitter();

    @Output() onBlur: EventEmitter<any> = new EventEmitter();

    @Output() onValueChange: EventEmitter<any> = new EventEmitter();

    @Output() onClickBtnL: EventEmitter<any> = new EventEmitter();

    @Output() onClickBtnR: EventEmitter<any> = new EventEmitter();

    @Output() onFail: EventEmitter<any> = new EventEmitter();

    @ViewChild("input") input: ElementRef;

    detectChangesIsFired = false; // pr declencher qu'une fois le detectChanges;

    // besoin du detect change la premiere fois ou il y a une erreur, sans, sa declenche une erreur de rafraichissement du template.
    constructor(private cd: ChangeDetectorRef) {}

    ngAfterViewInit(): void {
        // this.input.nativeElement.removeAttribute('data-toggle');
        $("#" + this.id).tooltip("disable");
    }

    ngOnDestroy(): void {
        $("#" + this.id).tooltip("hide");
        $("#" + this.id).tooltip("disable");
        $("#" + this.id).tooltip("dispose");
    }

    ngOnInit() {
        if (typeof this.form === "undefined") {
            this.form = new FormGroup({
                input: new FormControl("")
            });
        }
    }

    openModal(condition) {
        if (condition && !this.detectChangesIsFired) {
            this.detectChangesIsFired = true;
            this.cd.detectChanges();
        }
        return condition;
    }

    onKey($event) {
        if ($event.keyCode === 13 || $event.keyCode === "13") {
            // this.onSubmit.emit({ yo: 'yo' });
        }
    }

    onDebounce($event) {
        this.onValueChange.emit($event);
        this.tooltipShow();
    }

    tooltipShow() {
        if (
            typeof this.control !== "undefined" &&
            this.control.active &&
            this.form.controls["input"].invalid &&
            this.form.controls["input"].dirty
        ) {
            // this.input.nativeElement.setAttribute('data-toggle', 'tooltip');
            $("#" + this.id).tooltip("enable");
            $("#" + this.id).tooltip("show");
            // $('#' + this.id).tooltip('update');
        } else {
            $("#" + this.id).tooltip("hide");
            $("#" + this.id).tooltip("disable");
            // $('#' + this.id).tooltip('dispose');
            //  $('#' + this.id).wePopover('update')
        }
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.control) {
            $("#" + this.id).tooltip("dispose");

            setTimeout(() => {
                this.tooltipShow();
            }, 200);
        }
    }
}



