import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../../services/authentication.service';
import { slug } from '../../../shared/utils/utils';
import { ErrorsService } from '../../services/errors.service';
import 'rxjs/add/operator/finally';


declare var axios: any;
declare var $: any;

@Component({
    moduleId: module.id,
    templateUrl: 'password.component.html'
})



export class PasswordComponent implements OnInit {
    model: any = {
        password2: '',
        password: ''
    };

    loading = false;
    reset = false;
    loginAction = true;
    email_sended = false;
    token = '';
    error = '';
    returnUrl: string;

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private authenticationService: AuthenticationService,
        private errService: ErrorsService,
    ) {

        this.route.params.subscribe( token =>
            { this.token = token.params; }
        );

    }



    onResetPassword(){

        this.reset = true;

    }

    ngOnInit() {
        // reset login status
        this.authenticationService.logout();
        // get return url from route parameters or default to '/'
        this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
    }

    setAction(action) {
        this.loginAction = action === 'login';
        this.error = '';
    }


    onModified(){
        this.error = '';
    }


    onEmailReceived(){
        this.email_sended = false;
        this.reset = false;
        this.loginAction = true;
        this.router.navigate(['/']);
    }


    resetting() {


        if(this.model.password !== this.model.password2) {
            this.error = 'Les 2 mots de passe doivent etre identiques';
            this.model.password2 = '';
            return ;
        }


        this.loading = true;
        this.authenticationService.passwordUpdate(this.model.password,this.model.password,this.token)
            .finally(() => this.loading = false)
            .subscribe(
                result => {
                    if(result['status']){
                       // this.router.navigate(['/']);
                       this.email_sended = true;
                    }else{
                        this.error = "Le lien de rinitialisation est inconnu ou expir. Recommencer la procdure de modification de mot de passe."
                    }
                },
                error => {
                    this.errService.handleErrorHttp(error);
                    this.error = error.error && error.error.error || 'Erreur inconnue';
                }
            );
    }




}



