import { Injectable, ApplicationRef } from "@angular/core";
import { Router } from "@angular/router";
declare let $;

@Injectable()
export class ModalService {
    private modals: any[] = [];

    constructor(private router: Router, private appRef: ApplicationRef) {}

    add(modal: any) {
        // add modal to array of active modals
        this.modals.push(modal);
    }

    remove(id: any) {
        // remove modal from array of active modals
        this.modals = this.modals.filter(x => x.id !== id);
    }

    open(id: any) {
        this.appRef.tick(); // detect les changements (ngIf notemmment)
        $("#" + id).modal("show");
    }

    openByRouter(id: any) {
        this.router.navigate([{ outlets: { modal: ["modal", id] } }]);
    }

    close(id: any) {
        $("#" + id).modal("hide");
        // this.router.navigate([{ outlets: { modal: null } }]);
    }

    onHidden(id: any) {
        this.router.navigate([{ outlets: { modal: null } }]);
    }

    /**
     * Manually readjust the modals position if the height of a modal changes while it is open (i.e. in case a scrollbar appears).
     * @param id
     */
    handleUpdate(id: any) {
        $("#" + id).modal("handleUpdate");
    }

    /**
     * Destroys an elements modal.
     * @param id
     */
    dispose(id: any) {
        $("#" + id).modal("dispose");
    }
}



