import { Pipe, PipeTransform } from "@angular/core";
import { getSrcSet } from "../utils/utils";
/**
 * Renvoie une chaine correspondant a l'attribut srcset HTML5
 *
 */
@Pipe({
    name: "srcset"
})
export class SrcsetPipe implements PipeTransform {
    transform(value: any): any {
        let data = null;
        try {
            data = JSON.parse(value);
        } catch (e) {
            data = value;
        }
        return getSrcSet(data);
    }
}



