import {
    Component,
    OnInit,
    Input,
    EventEmitter,
    Output,
    SimpleChanges,
    OnChanges
} from "@angular/core";
import { Meta, PageType, SitesService, PagesService, VersionsService } from "../../../api";
import { ModuleService } from "../../../global/services/module.service";
import { ModulesService } from "../../../api/api/modules.service";
import { StoreService } from "../../services/store.service";
import { ErrorsService } from "../../../global/services/errors.service";
import { slug } from "../../utils/utils";
import { PermissionService } from "../../services/permission.service";

@Component({
    selector: "app-meta-page",
    templateUrl: "./meta-page.component.html",
    styleUrls: ["./meta-page.component.scss"]
})
export class MetaPageComponent implements OnInit, OnChanges {
    @Input() entity;
    @Input() metadata; //infos extra  envoyer  la crea de la page
    @Input() idModule;
    @Input() idResource;
    @Input() pageType;
    @Input() pathModule;
    @Input() labelDesc: string = "";
    @Output() onChangeSlug: EventEmitter<any> = new EventEmitter();
    @Output() onSave: EventEmitter<any> = new EventEmitter();
    // indique que les metas effectue une requete
    @Output() onLoading: EventEmitter<any> = new EventEmitter();
    @Output() onEndLoading: EventEmitter<any> = new EventEmitter();

    versionPage;
    titleMeta: Meta = { content: "", name: "title" };
    descMeta: Meta = { content: "", name: "description" };
    imageSocialMeta: Meta = { content: "", name: "social_image" };
    page;
    slugPage;
    originalSlug;
    urlPage;

    urlInError: boolean = false;
    checkerInLoading: boolean = false;

    constructor(
        private siteApi: SitesService,
        private moduleService: ModuleService,
        private moduleApi: ModulesService,
        private pageApi: PagesService,
        private store: StoreService,
        private errorsService: ErrorsService,
        private modulesApi: ModulesService,
        public permService: PermissionService,
        private versionService: VersionsService
    ) { }

    ngOnInit() {
        this.init();
    }

    init() {
        // On check dans le store si la page existe deja

        this.modulesApi
            .apiSitesSlugModulesModulePagesModuleResourceGet(
                String(this.store.curSite.id),
                this.idModule,
                this.idResource
            )
            .subscribe(
                page => {
                    //console.log("Meta Page : page -->", page);
                    this.page = page;
                    this.onLoading.emit();

                    this.pageApi
                    .apiPagesSlugVersionsOnlineGet(String(page.id))
                    .subscribe(pageHistory => {
                            this.versionPage = pageHistory;
                            // console.log("versionPage", pageHistory);
                            this.titleMeta = pageHistory.metas.find(
                                m => m.name === "title"
                            );
                            // console.log("meta title sans id #2", this.titleMeta);
                            this.descMeta = pageHistory.metas.find(
                                m => m.name === "description"
                            );

                            this.imageSocialMeta = pageHistory.metas.find(
                                m => m.name === "social_image"
                            );

                            if (!this.descMeta) {
                                this.moduleService.postDescMeta(
                                    this.versionPage.id,
                                    { content: "", name: "description" },
                                    descMeta => {
                                        this.descMeta = descMeta;
                                        this.onEndLoading.emit();
                                    }
                                );
                            } else {
                                this.onEndLoading.emit();
                            }
                            // console.log("meta descMeta sans id #2", this.descMeta);
                            const slugP = slug(
                                this.entity.slug ||
                                this.entity.nom ||
                                this.entity.titre ||
                                this.entity.title
                            );
                            this.updateSlug(slugP);
                            this.originalSlug = slugP;
                        },
                        (errorVersion) =>{
                            console.log("errorVersion pagehistory not found:", errorVersion );
                            
                        }
                    );
                },
                error => {
                    // si pas de pages, on en cre une
                    if (error && error.status === 404) {
                        //console.warn("crea page!!", this.metadata);
                        this.urlPage = "";
                        const metadata = this.metadata;
                        this.createPage(metadata);
                    } else {
                        this.errorsService.handleErrorHttp(error);
                    }
                }
            );
    }


    /**
     * Virer le superflus de Nicolas du moduleService
     * Creer une promise pour la meta
     * Update ou creer la meta
     * @param meta 
     */
    getPromiseForMeta(meta) {

        let callback = (resolve, reject) => { };
        if (meta.id) {
            let _meta = { ...meta };
            delete _meta['id'];
            callback = (resolve, reject) =>

                this.versionService
                    .apiVersionsSlugMetasMetaPut(
                        _meta,
                        String(this.versionPage.id),
                        String(meta.id)
                    )
                    .subscribe(
                        meta => {
                            //console.log("PUT Metas");
                            return resolve(meta);
                        },
                        error => {

                            return reject(error);
                        }
                    );

        } else {
            callback = (resolve, reject) =>

                this.versionService
                    .apiVersionsSlugMetasPost(meta, String(this.versionPage.id))
                    .subscribe(meta => {
                        //console.log("POST Metas");
                        return resolve(meta);
                    }, e => {
                        return reject(e);
                    });
        }

        return new Promise((resolve, reject) => {
            return callback(resolve, reject);
        });
    }

    extractSocialImageFromProduct() {

        let parsedImage = "";
        let tempImage = "";

        try {
            tempImage = JSON.parse(this.entity.imageUrl);
        } catch (e) {
            tempImage = this.entity.imageUrl;
        }

        if (tempImage) {
            if (typeof tempImage['1280'] != "undefined") {
                parsedImage = tempImage['1280'];
            } else {
                parsedImage = tempImage;
            }
        }

        //console.log("socialImage :", parsedImage);
        return parsedImage;

    }

    save(entity) {
        this.onLoading.emit();
        this.entity = entity;

       // console.log("entity To save:", entity);
        // todo apiPagesSlugVersionsOnlineGet
        return new Promise<any>((resolve, reject) => {
            if (this.urlInError) {
                this.onSave.emit();
                this.onEndLoading.emit();
                resolve();
                return;
            }
            const page = this.newPageType();
            this.pageApi.apiPagesPagePut(page, this.page.id).subscribe(
                p => {

                   // console.log("this.titleMeta", this.titleMeta, "this.versionPage", this.versionPage);

                    this.moduleService.updateMeta(
                        this.titleMeta,
                        this.versionPage.id,
                        metaTitle => {
                            this.titleMeta = metaTitle;
                             //console.log("meta title sans id #3", this.titleMeta);
                             //console.log("meta desc sans id #3", this.descMeta);
                            this.moduleService.updateMeta(
                                this.descMeta,
                                this.versionPage.id,
                                metaDesc => {
                                    if ("FREE" != this.store.getMainPlanName()) {
                                        this.imageSocialMeta = { "content": this.extractSocialImageFromProduct(), "name": "social_image" };
                                        let promise1 = this.getPromiseForMeta(this.imageSocialMeta);
                                        promise1.then((values) => {
                                            console.log("promises values :", values);
                                            this.descMeta = metaDesc;
                                            this.onSave.emit();
                                            this.onEndLoading.emit();
                                            resolve();
                                        });
                                    } else {
                                        this.descMeta = metaDesc;
                                        this.onSave.emit();
                                        this.onEndLoading.emit();
                                        resolve();
                                    }

                                }
                            );
                        }
                    );
                },
                error => {
                    this.errorsService.handleErrorHttp(error, true);
                    this.onEndLoading.emit();
                    reject();
                }
            );
        });
    }

    newPageType(isNew?: boolean) {
        const nom = this.entity.nom || this.entity.titre || this.entity.title;
        let slugPage = this.entity.slug || slug(nom);
        if (isNew) {
            this.originalSlug = slugPage;
        }

        const page: PageType = {
            nom,
            url: this.urlPage || this.pathModule + "/" + slugPage,
            editor: "10", // choper lediteur sur le site
            is_home: false,
            is_error: false,
            module: this.idModule,
            module_resource: this.idResource,
            module_page_type: this.pageType,
            is_service: false
        };
        this.updateSlug(slug(slugPage));
        return page;
    }

    // Cre une page 
    createPage(metadata) {
        
        this.onLoading.emit();
        const page = this.newPageType(true);
        if (metadata) {
            page['metadata'] = metadata;
        }

        //console.log("creating page...", metadata, page);

        this.siteApi
            .apiSitesSitePagesPost(page, String(this.store.curSite.id))
            .subscribe(

                page => {
                    this.pageApi
                        .apiPagesSlugVersionsOnlineGet(String(page.id))
                        .subscribe(
                            pageHistory => {
                                this.versionPage = pageHistory;
                                this.moduleService.postDescMeta(
                                    this.versionPage.id,
                                    this.descMeta,
                                    descMeta => {
                                        this.descMeta = descMeta;
                                        // crer un sournois rcpt >Empty dans api php
                                        this.moduleService.createContainer(
                                            pageHistory,
                                            (pageVersion, container) => {
                                                this.page = page;
                                                const curValTitle = this.titleMeta.content;
                                                this.titleMeta = pageVersion.metas.find(
                                                    m => m.name === "title"
                                                );
                                                // console.log("meta title sans id #1", this.titleMeta);
                                                if (curValTitle !== "") {
                                                    this.titleMeta.content = curValTitle;
                                                }

                                                this.moduleService.updateMeta(
                                                    this.titleMeta,
                                                    this.versionPage.id,
                                                    () => {
                                                        // onSuccess();
                                                        this.onEndLoading.emit();
                                                    }
                                                );
                                            }
                                        );
                                    }
                                );
                            },
                            error => {
                                this.errorsService.handleErrorHttp(error);
                            }
                        );
                },
                error => this.errorsService.handleErrorHttp(error)
            );
    }

    updateSlug(value) {
        // console.log("updateSlug", value);
        if (value) {
            const slugPage = slug(value);
            this.slugPage = slugPage;
            // const urlPage = this.pathModule + "/" + slugPage;
            // const page = this.store.pages.find(p => p.url === urlPage && p.module_resource !== this.idResource);
            // this.urlInError = page ? true : false;
            // if (!this.urlInError) {
            // this.onChangeSlug.emit(this.slugPage);
            this.urlPage = this.pathModule + "/" + slugPage;
            // } else {
            // this.urlPage = this.pathModule + "/" + this.originalSlug;
            // }
        }
    }

    getSlug() {
        return this.urlInError ? this.originalSlug : this.slugPage;
    }

    checker() {
        this.init();
    }

    ngOnChanges(changes: SimpleChanges): void {
        const { idResource } = changes;
        if (idResource && idResource.currentValue && !idResource.firstChange) {
            this.checker();
        }
    }
}



